import {Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {RenderStatusComponent} from "../../../ui/render/fields/render-status/render-status.component";

const route: Route = {
  endpoint: "common/callbacks",
  model: "CodeeHutCommon\\ClientRepository\\Models\\Callback",
  texts: {
    key: 'callbacks',
    label: $localize`Callback`,
    title: $localize`Callbacks`,
    backLabel: $localize`Back to Callbacks`,
    description: $localize`Below is the list of the created callbacks`,
  },
  fieldGroups: [{
    title: $localize`Details`,
    key: 'details',
    fields: [{
      key: 'type',
      label: $localize`Type`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'text',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: true,
      },
    }]
  }],
  label: "",
  links: {
    create: null, // '/app/common/callbacks/create',
    update: null, // '/app/common/callbacks/update/',
    manage: '/app/common/callbacks/manage',
    view: '/app/common/callbacks/view/',
  },
  manage: {
    hasStatus: false,
    columns: [{
      title: $localize`Status`,
      className: 'col-md-2',
      fields: [{
        label: $localize`Status`,
        key: 'status',
        component: RenderStatusComponent,
      },],
    }, {
      title: $localize`Data`,
      className: 'col-md-4',
      fields: [{
        label: $localize`Type`,
        key: 'type',
        component: RenderTextComponent,
      }, {
        label: $localize`Client`,
        key: 'email',
        component: RenderRelationshipComponent,
        relationType: 'single',
        relation: 'client',
      }, {
        label: $localize`Remarks`,
        key: 'remarks',
        component: RenderTextComponent,
      },],
    }, {
      title: $localize`Date`,
      className: 'col-md-4',
      fields: [{
        label: $localize`Code`,
        key: 'code',
        component: RenderTextComponent,
      }, {
        label: $localize`Created`,
        key: 'created_at',
        component: RenderTextComponent,
      }]
    }]
  },
  view: {
    columns: [{
      title: $localize`Callback Data`,
      className: 'col-md-8',
      fields: [{
        label: $localize`Id`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Code`,
        key: 'code',
        component: RenderTextComponent,
      }, {
        label: $localize`Type`,
        key: 'type',
        component: RenderTextComponent,
      }, {
        label: $localize`Created`,
        key: 'created_at',
        component: RenderTextComponent,
      }, {
        label: $localize`Remarks`,
        key: 'remarks',
        component: RenderTextComponent,
        maxLength: 1000,
      }, {
        label: $localize`Status`,
        key: 'status',
        component: RenderTextComponent,
      },]
    }, {
      title: $localize`Client`,
      className: 'col-md-4',
      fields: [{
        label: $localize`Id`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`First Name`,
        key: 'client.first_name',
        component: RenderTextComponent,
      }, {
        label: $localize`Last Name`,
        key: 'client.last_name',
        component: RenderTextComponent,
      }, {
        label: $localize`Email`,
        key: 'client.email',
        component: RenderTextComponent,
      }, {
        label: $localize`Address`,
        key: 'client.address',
        component: RenderTextComponent,
      }, {
        label: $localize`City`,
        key: 'client.city',
        component: RenderTextComponent,
      }, {
        label: $localize`Region`,
        key: 'client.region',
        component: RenderTextComponent,
      }, {
        label: $localize`Telephone`,
        key: 'client.telephone',
        component: RenderTextComponent,
      },]
    }],
  }
};

export default route;

