import {Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {CodeeSelectComponent} from "../../../ui/forms/fields/codee-select/codee-select.component";
import {ApiData} from "../../../types/api.types";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {FieldsCommonClass} from "../../common/fields-common.class";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";

const route: Route = {
  endpoint: "hotel/experiences",
  translatable: true,
  model: "CodeeHut\\HotelManagement\\Models\\Experience",
  texts: {
    key: 'experiences',
    label: $localize`Experience`,
    title: $localize`Experiences`,
    backLabel: $localize`Back to Experiences`,
    description: $localize`Below is the list of the created experiences`,
  },
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_main',
        label: 'Main image',
        apiEndpoint: 'files/media'
      }),
    ]
  }, {
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldsCommonClass._id(6),
      FieldsCommonClass._name(6),
      {
        key: 'type',
        label: $localize`Type`,
        hidden: false,
        col: 6,
        default: '',
        validators: [],
        formProps: {
          type: 'select',
          control: FetchControl.value,
          controlKey: 'destinations',
          component: CodeeSelectComponent,
          // TODO I would prefer to load them from api
          data: [{
            id: 1,
            label: $localize`Beach`,
          }, {
            id: 2,
            label: $localize`Swimming Pools`,
          }, {
            id: 3,
            label: $localize`Sports`,
          }, {
            id: 4,
            label: $localize`Kids`,
          }],
          editable: true,
          mapSelectObjectFn: (item: ApiData) => {
            return {
              id: item.id,
              label: item.name,
            };
          }
        },
      },
      FieldsCommonClass._excerpt(12),
      FieldsCommonClass._content(12),
    ]
  }, {
    title: 'Relations',
    key: "relations",
    fields: [
      FieldsCommonClass._propertyRelation(),
    ],
  }, {
    title: $localize`Media`,
    key: 'media',
    fields: [
      FieldGeneratorClass._mediaImage({key: 'media_gallery', label: 'Gallery images', apiEndpoint: 'files/media', col: 12, multiple: true}),
      // FieldsCommonClass._mediaGallery(12, true),
    ]
  }],
  label: "",
  links: {
    create: '/app/hotel/experiences/create',
    update: '/app/hotel/experiences/update/',
    manage: '/app/hotel/experiences/manage',
  },
  layoutBlock: {
    id: 'experience_id',
    relation_key: 'experiences',
  },
  manage: {
    hasStatus: true,
    urlPattern: '/experience/:slug/:id',
    columns: [{
      title: $localize`Logo`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Excerpt`,
        key: 'excerpt',
        component: RenderTextComponent,
      }, {
        label: $localize`Property`,
        component: RenderRelationshipComponent,
        relation: 'properties',
        relationType: 'multiple',
        key: 'name',
      }],
    }]
  },

};


export default route;
