import {Component, ElementRef, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {Field, Route} from "../../../types/route.type";
import {ApiService} from "../../../services/abstract/api.service";
import {AlertService} from "../../../modules/alert/alert.service";
import {ConfirmBoxInitializer, DialogLayoutDisplay} from "@costlydeveloper/ngx-awesome-popup";
import {ActivatedRoute, Router} from "@angular/router";
import {Language, Metadata} from "../../../types/layout.types";
import {environment} from "../../../../environments/environment";
import {HttpParams} from "@angular/common/http";
import {FormBuilder, FormControl, FormGroup} from "@angular/forms";
import {ApiData, ApiError, ApiResponse} from "../../../types/api.types";
import {Subscription} from "rxjs";
import {SessionService} from "../../../services/auth/session.service";
import {LoadableService} from "../../../services/abstract/loadable.service";
import {skip} from "rxjs/operators";
import {RequestService} from "../../../services/abstract/request.service";
import {ModalComponent} from "../../../ui/widgets/modal/modal.component";
import {LanguageService} from "../../../services/abstract/language.service";

@Component({
  selector: 'app-manage',
  templateUrl: './manage.component.html',
  styleUrls: ['./manage.component.scss']
})
export class ManageComponent implements OnInit, OnDestroy {

  @ViewChild("mainCard") mainCard: ElementRef;
  @ViewChild(ModalComponent) modal: ModalComponent;

  public filtersForm: FormGroup = new FormBuilder().group({});

  public $route: Route;
  public items: Array<ApiData> = [];
  public $filterableFields: Field[];
  public page: number = 1;
  public metadata: Metadata;
  public resultsPerPage: Number = environment.resultsPerPage;
  public params: HttpParams = new HttpParams();
  public mediaUrl: string;

  private queryParams: { [key: string]: string } = {};

  private tenantSubscr!: Subscription;

  public lang: Language;

  constructor(
    private apiService: ApiService,
    private request: RequestService,
    private alertService: AlertService,
    private router: Router,
    private route: ActivatedRoute,
    private sessionService: SessionService,
    private loadableService: LoadableService,
    private languageService: LanguageService,
  ) {
  }

  ngOnInit(): void {

    this.defineMediaUrl();

    this.route.data.subscribe(data => {

      // Get the route date
      this.$route = data.handler.route; // console.log('manage columns', this.$route.manage);

      // Get all filtrable fields
      this.getFieldsWithFilters();
    });


    // Subscribe to tenant change.
    // When a tenant is changed then we have to get Entities again.
    this.tenantSubscr = this.sessionService.tenantSelectedSbj
      .pipe(skip(1))
      .subscribe(
        tenant => {
          // console.log(tenant);
          this.getEntities();
        }
      );


    // Subscribe to query params
    this.route.queryParams
      .subscribe(queryParams => {
          this.queryParams = queryParams;
          // Configure filters controls
          this.$filterableFields.forEach(filter => {
            this.filtersForm.addControl(filter.key, new FormControl(this.queryParams[filter.key] || '', []));
          });
          this.getEntities();
        }
      );

    // Get the defined language
    this.lang = this.languageService.selectedLanguage$.getValue();


  }

  get _manage() {
    return this.$route.manage;
  }

  ngOnDestroy(): void {
    this.tenantSubscr.unsubscribe();
  }

  pageChanged(page: number = 1) {
    // Scroll into view
    if (this.mainCard) {
      this.mainCard.nativeElement.scrollIntoView({top: -100, behavior: "smooth", block: "start"});
    }
    this.getEntities(page);
  }

  getEntities(page: number = 1) {

    // console.log('getEntities: page', page);
    this.loadableService.enable();

    if (page)
      this.page = page;

    this.params = new HttpParams();
    this.params = this.params.append('resultsPerPage', this.resultsPerPage.toString());
    this.params = this.params.append('page', this.page.toString());
    this.params = this.params.append('lang', environment.defaultLocale);

    if (this.$route.withRelations) {
      this.params = this.params.append('with', this.$route.withRelations.join(','))
    }

    // Check if extra attributes have been defined in the query
    if (this.$route.withAttributes) {
      this.params = this.params.append('attributes', this.$route.withAttributes.join(','))
    }

    // Add Filter params
    this.setFilterParams();

    this.apiService.getEntities(this.$route.endpoint, this.params, res => {
        this.items = res.data;
        this.metadata = res.meta;
        this.loadableService.disable();

        // Create meta row for each record
        // _meta[row] contain extra info for each record
        // this.items.map(item => {
        //   if (this.$route?.manage?.urlPattern) {
        //     item._metaPreview = this.theUrlPattern(item);
        //   }
        // });
      },
      (err: ApiError) => {
        this.alertService.danger(err.message);
        this.loadableService.disable();
      });
  }

  onDelete(tenant: any) {
    this.confirmBox(tenant); // open confirm box
  }

  onEdit(tenant: any) {
    this.router.navigate([this.$route.links.update + tenant.id]);
  }

  onView(tenant: any) {
    this.router.navigate([this.$route.links.view + tenant.id]);
  }

  confirmBox(item: any) {
    const confirmBox = new ConfirmBoxInitializer();
    confirmBox.setTitle('Confirm Delete');
    confirmBox.setMessage('Are you sure you want to delete ' + item.id);
    confirmBox.setButtonLabels('YES', 'NO');

    // Choose layout color type
    confirmBox.setConfig({
      LayoutType: DialogLayoutDisplay.DANGER // SUCCESS | INFO | NONE | DANGER | WARNING
    });

    // Simply open the popup and listen which button is clicked
    confirmBox.openConfirmBox$().subscribe(resp => {
      // IConfirmBoxPublicResponse
      // console.log('Clicked button response: ',  resp.Success);
      if (resp.Success) {
        this.apiService.deleteEntity(this.$route.endpoint, item.id, res => {
          this.getEntities();
        })
      }
    });
  }

  onFilter() {
    this.router.navigate(
      [this.router.url.split('?')[0]],
      {queryParams: this.filtersForm.value}
    )
    // this.getEntities();
  }

  setFilterParams(): void {
    // console.log('this.filtersForm.value', this.filtersForm.value);
    if (Object.keys(this.filtersForm.controls).length > 0) {
      for (const key of Object.keys(this.filtersForm.controls)) {
        let value = this.filtersForm.controls[key].value;
        if (value) {
          // if (isMoment(value)) {
          //   value = value.format('YYYY-MM-DD');
          // }
          const [props] = this.$filterableFields.filter(filter => filter.key === key)
          // console.log('filterProps', props);
          let data = {
            value: value,
            filterableFn: props?.filterProps?.filterable,
            relation: '',
          };

          if (props.filterProps && props.filterProps.relation) {
            data.relation = props.filterProps.relation;
          }
          this.params = this.params.append(key, JSON.stringify(data));
        }
      }
    }
  }

  getFieldsWithFilters() {
    // Create an array with the fields that contain filterProps
    this.$filterableFields = this.$route.fieldGroups
      .map(fieldGroup => fieldGroup.fields)
      .reduce((appender, state) => appender.concat(state), [])
      .filter((field) => field.filterProps);
    // console.log('this.$filterableFields', this.$filterableFields);
  }


  resetFiltering() {
    this.filtersForm.reset();
    this.page = 1;
    this.router.navigate(
      [this.router.url.split('?')[0]],
      {queryParams: this.filtersForm.value}
    )

    // this.getEntities(1);
  }


  updateStatus(id: number | string, status: number | string) {
    // console.log(status);
    this.apiService.updateEntity(this.$route.endpoint, typeof id === "string" ? parseInt(id) : id, {
        id: id,
        status: status === 0 ? 1 : 0,
      },
      (res: ApiResponse) => this.afterSuccessfulApiAction(res),
      (err: ApiError) => this.afterErrorApiAction(err));
  }


  afterSuccessfulApiAction(res: ApiResponse) {
    this.alertService.success(res.message);
    this.getEntities();
  }

  afterErrorApiAction(err: ApiError) {
    // @ts-ignore
    this.$errors = err.errors;
    this.alertService.danger(err.message);
  }

  qrCodes: any = [];
  get addLabel() {
    return $localize`Add` + ' ' + this.$route.texts.label
  }

  generateQrCode(item: ApiData) {
    this.request.postRequest('hotel/qr-code/generate', {
      urls: [
        item._metaPreview,
      ]
    })
      .subscribe((res: any) => {
        this.modal.openMyself();
        this.qrCodes = res.data;
      });
  }

  private defineMediaUrl() {
    this.sessionService.selectedTenant().subscribe(
      tenant => {
        if ( tenant?.domains) {
          const [domain] = tenant.domains;
          if (domain) {
            this.mediaUrl = environment.protocol + '://' + domain.domain;

            if (environment.port) {
              this.mediaUrl = this.mediaUrl + ':' + environment.port;
            }
          }
        }
      }
    )
  }
}
