import {Component, Input, OnInit} from '@angular/core';
import {FormProps, MapFnData} from "../../../../types/route.type";
import {HttpParams} from "@angular/common/http";
import {ApiService} from "../../../../services/abstract/api.service";
import {CodeeFormControlInterface} from "../codee-form-control.interface";

@Component({
  selector: 'app-codee-select',
  templateUrl: './codee-select.component.html',
  styleUrls: ['./codee-select.component.scss']
})
export class CodeeSelectComponent implements OnInit, CodeeFormControlInterface {

  @Input() label: string;
  @Input() mode: string;
  @Input() value: number[] = [];
  @Input() props: FormProps;

  $data: MapFnData[] = [];
  inputModel: number | Array<number>;

  constructor(
      private apiService: ApiService,
  ) {}

  ngOnInit(): void {
    if (this.props.dataEndpoint)
      this.apiService.getEntities(this.props.dataEndpoint, new HttpParams(),(res) => {
        this.$data = res.data.map(this.props.mapSelectObjectFn);
      })

    if (this.props.data) {
      this.$data = this.props.data;
    }
  }

  onChange = (value: number[]) => { };

  onTouched = () => {};

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  setDisabledState(isDisabled: boolean): void {
  }

  writeValue(value: any): void {
    // console.log('writeValue', value);
    this.inputModel = value;
  }

}


