import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {environment} from "../../../environments/environment";
import {Observable} from "rxjs";
import {SessionService} from "../auth/session.service";

@Injectable({
    providedIn: 'root'
})
export class RequestService {

    protected apiUrl: string;

    constructor(
        private http: HttpClient,
        private sessionService: SessionService,
    ) {}

    getRequest = (suffix: string, options = {}, isCentralDomain = false): Observable<Object> => {
        return this.http.get(this.getUrl(isCentralDomain) + suffix, options);
    };

    postRequest = (suffix: string, data = {}, isCentralDomain = false): Observable<Object> => {
        return this.http.post(this.getUrl(isCentralDomain) + suffix, data);
    };

    putRequest = (suffix: string, data = {}, isCentralDomain = false): Observable<Object> => {
        return this.http.put(this.getUrl(isCentralDomain) + suffix, data);
    };

    deleteRequest = (suffix: string, data = {}, isCentralDomain = false): Observable<Object> => {
        return this.http.delete(this.getUrl(isCentralDomain) + suffix, data);
    };

    private getUrl(isCentralDomain = false): string {

        let domain = environment.centralDomain;

        if ( ! isCentralDomain ) {
            const {domains} = this.sessionService.tenantSelectedSbj.getValue();

            if (domains) {
                const [masterDomain] = domains;

                if (masterDomain)
                    domain = masterDomain.domain;
            }
        }
        this.apiUrl = `${environment.protocol}://${domain}${environment.port !== null ? ':'+ environment.port : ''}/${environment.apiSuffix}`;
        // console.log('RequestService[getUrl]', this.apiUrl)
        return this.apiUrl;
    }
}
