import {FilterFunction, Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeSelectComponent} from "../../../ui/forms/fields/codee-select/codee-select.component";
import {ApiData} from "../../../types/api.types";
import {FieldsCommonClass} from "../../common/fields-common.class";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {CodeeCheckboxComponent} from "../../../ui/forms/fields/codee-checkbox/codee-checkbox.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";

const route: Route = {
  endpoint: "digital-signage/boxes",
  withRelations: ['layout'],
  model: "CodeeHut\\DigitalSignage\\Models\\Box",
  texts: {
    key: 'boxes',
    label: $localize`Box`,
    title: $localize`Boxes`,
    backLabel: $localize`Back to Boxes`,
    description: $localize`Below is the list of the created boxes`,
  },
  fieldGroups: [{
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldGeneratorClass._text({ key: 'id', label: $localize`ID`, hidden: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        } }),
      FieldsCommonClass._name(6),
      {
        key: 'layout',
        label: $localize`Layout`,
        hidden: false,
        col: 6,
        default: '',
        validators: [],
        formProps: {
          type: 'select',
          control: FetchControl.singleRelation,
          controlKey: 'id',
          component: CodeeSelectComponent,
          dataEndpoint: 'digital-signage/layouts?resultsPerPage=all&page=1',
          editable: true,
          mapSelectObjectFn: (item: ApiData) => {
            return {
              id: item.id,
              label: item.name,
            };
          }
        },
      }, {
        key: 'seconds',
        label: $localize`Seconds`,
        hidden: false,
        col: 6,
        default: '30',
        validators: [],
        translatable: true,
        formProps: {
          type: 'text',
          control: FetchControl.value,
          component: CodeeInputComponent,
          editable: true,
        },
      }, {
        key: 'position',
        label: $localize`Position`,
        hidden: false,
        col: 6,
        default: '1',
        validators: [],
        translatable: true,
        formProps: {
          type: 'select',
          control: FetchControl.value,
          controlKey: 'position',
          component: CodeeSelectComponent,
          data:[{
            id: 1,
            label: $localize`Main`,
          }, {
            id: 2,
            label: $localize`Top`,
          }, {
            id: 3,
            label: $localize`Bottom`,
          }, ],
          editable: true,
          mapSelectObjectFn: (item: ApiData) => {
            return {
              id: item.id,
              label: item.name,
            };
          }
        },
      },
      {
        key: 'show_body',
        label: $localize`Text will be visible on top of media`,
        hidden: false,
        col: 6,
        default: '1',
        validators: [],
        formProps: {
          control: FetchControl.valueBoolean,
          component: CodeeCheckboxComponent,
          editable: true,
        },
      },
      FieldsCommonClass._content(12),
    ]
  }, {
    key: 'media',
    title: $localize`Media Files`,
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_main',
        label: 'Main image',
        apiEndpoint: 'files/media'
      }),
      FieldsCommonClass._mediaVideo(6, true, 'digital-signage'),
    ]
  }],
  label: "",
  links: {
    create: '/app/digital-signage/boxes/create',
    update: '/app/digital-signage/boxes/update/',
    manage: '/app/digital-signage/boxes/manage',
  },
  manage: {
    hasStatus: true,
    columns: [{
      title: $localize`Main image`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Layout`,
        component: RenderRelationshipComponent,
        relation: 'layout',
        relationType: 'single',
        key: 'name',
      }],
    }]
  },

};


export default route;
