import {FilterFunction, Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {CodeeSelectComponent} from "../../../ui/forms/fields/codee-select/codee-select.component";
import {ApiData} from "../../../types/api.types";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";

const route: Route = {
  endpoint: "hotel/conferences",
  translatable: true,
  model: "CodeeHut\\HotelManagement\\Models\\Conference",
  texts: {
    key: 'conferences',
    label: $localize`Conference`,
    title: $localize`Conferences`,
    backLabel: $localize`Back to Conferences`,
    description: $localize`Below is the list of the created conferences`,
  },
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_main',
        label: $localize`Main image`,
        description: $localize`The image thumbnail`,
        apiEndpoint: 'files/media'
      }),
      // FieldsCommonClass._mediaMain(6),
    ]
  }, {
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldGeneratorClass._text({
        key: 'id', label: $localize`ID`, hidden: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        }
      }),
      FieldGeneratorClass._text({
        key: 'name', translatable: true, label: $localize`Name`, description: $localize`The name of the conference room`, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        }
      }),
      // FieldsCommonClass._name(6),
      FieldGeneratorClass._richText({key: 'excerpt', translatable: true, label: $localize`Conference excerpt`}),
      FieldGeneratorClass._richText({key: 'content', translatable: true, label: $localize`Conference Content`}),
      FieldGeneratorClass._halls({
        key: 'halls', translatable: false, label: $localize`Conference halls`,
        updatable: true,
        description: 'Add at least one conference hall with it\'s specifications'
      }),

    ]
  }, {
    title: $localize`Relations`,
    key: "relations",
    fields: [
      // FieldsCommonClass._propertyRelation(),
      FieldGeneratorClass._choiceFromApi({
        key: 'properties', col: 12, label: $localize`Properies`, apiEndpoint: 'hotel/properties', filter: {
          control: FetchControl.singleRelation,
          component: CodeeSelectComponent,
          dataEndpoint: 'hotel/properties?resultsPerPage=all&page=1',
          mapSelectObjectFn: (item: ApiData) => {
            return {
              id: item.id,
              label: item.name,
            };
          },
          filterable: FilterFunction.HasRelation,
          relation: 'properties',
        }
      }),
    ],
  }, {
    title: $localize`Media`,
    key: 'media',
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_gallery',
        label: $localize`Conference Gallery`,
        apiEndpoint: 'files/media',
        multiple: true
      }),
    ]
  }],
  label: "",
  links: {
    create: '/app/hotel/conferences/create',
    update: '/app/hotel/conferences/update/',
    manage: '/app/hotel/conferences/manage',
  },
  layoutBlock: {
    id: 'conference_id',
    relation_key: 'conferences',
  },
  manage: {
    hasStatus: true,
    columns: [{
      title: 'Logo',
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Excerpt`,
        key: 'excerpt',
        component: RenderTextComponent,
      }, {
        label: $localize`Property`,
        component: RenderRelationshipComponent,
        relation: 'properties',
        relationType: 'multiple',
        key: 'name',
      }],
    }]
  },

};


export default route;
