import {AbstractControlOptions, ValidatorFn} from "@angular/forms";
import {FetchControlFn} from "../inc/fetch-control.class";
import {RenderControlFn} from "../inc/render-control.class";
import {ApiData} from "./api.types";
import {CodeeFormControlInterface} from "../ui/forms/fields/codee-form-control.interface";
import {ChoiceFormFieldData} from "./layout.types";
import {CallbackFn} from "../inc/callbacks.class";
import {RenderImageComponent} from "../ui/render/fields/render-image/render-image.component";
import {RenderRichTextComponent} from "../ui/render/fields/render-rich-text/render-rich-text.component";
import {RenderRelationshipComponent} from "../ui/render/fields/render-relationship/render-relationship.component";
import {RenderMapObjectComponent} from "../ui/render/fields/render-map-object/render-map-object.component";
import {RenderTextComponent} from "../ui/render/fields/render-text/render-text.component";
import {RenderStatusComponent} from "../ui/render/fields/render-status/render-status.component";

export declare type Route = {
  endpoint: string; // the endpoint of API data feeder
  translatable?: boolean;
  withRelations?: string[];
  withAttributes?: string[];
  model: string;
  label: string; // The main label will be visible as a title in manage.component.html
  texts: RouteTexts;

  // FieldGroups for each section of admin
  fieldGroups: FieldGroup[];
  settingGroups?: FieldGroup[];
  mediaGroups?: FieldGroup[];
  menuGroups?: FieldGroup[];
  emailGroups?: FieldGroup[];

  layoutBlock?: {
    id: string;
    relation_key: string;
  };
  links: {
    update?: string | null;
    create?: string | null;
    manage: string;
    view?: string;
  };
  callbacks?: {
    manage: CallbackFn;
  },
  manage?: {
    hasStatus: boolean, // Add the changing status checkbox (slider material)
    hasQrCodes?: boolean, // Add the QR code button
    urlPattern?: string, // Adds the url that the user can preview the entity
    columns: ManageColumn[]
  },
  view?: {
    columns: ManageColumn[]
  },
};


export declare type RouteTexts = {
  key: string,
  backLabel?: string;
  label: string,
  title: string,
  description: string,
}

export declare type FieldGroup = {
  id?: number;
  key: string | number | null;
  title: string;
  collapsed?: boolean;
  columns?: number;
  fields: Field[];
}

export declare type Field = {
  key: string;
  // formName: string;
  label: string;
  description?: string;
  hidden?: boolean;
  default?: string | Array<any> | null;
  col?: number;
  validators: ValidatorFn | ValidatorFn[] | AbstractControlOptions | null | undefined;
  translatable?: boolean;
  formControl?: FetchControlFn;
  formComponent?: CodeeFormControlInterface,
  formProps: FormProps;
  renderProps?: RenderProps;
  filterProps?: FormProps;
  // fetchCallback: FetchCallbackFn,
  conditions?: FieldCondition[],

  x?: number;
  y?: number;
}

export type FieldCondition = {
  field_key: string,
  compare: ConditionCompare,
  value: any,
}

export enum ConditionCompare {
  equals = '=',
  gt = '>',
  get = '>=',
  lt = '<',
  let = '<=',
}


export type FormProps = {
  type?: string;
  control: FetchControlFn;
  controlKey?: string;
  component: any, // @todo fix type declaration typeof CodeeInputComponent | typeof CodeeCheckboxComponent,
  dataEndpoint?: string;
  data?: ChoiceFormFieldData[];
  apiEndpoint?: string;
  editable?: boolean;
  mapSelectObjectFn?: (item: ApiData) => MapFnData;
  filterable?: FilterFunction,
  relation?: string,
  fileTypes?: string,
  multiple?: boolean;
  canBeNull?: boolean;
  isTextarea?: boolean;
  isUpdatable?: boolean;
}

export type MapFnData = {
  id: string | number,
  label: string | number,
  depth?: number,
}

export type ManageColumn = {
  title: string,
  className: string,
  fields: RenderProps[],
  // columns?: ManageColumn[],
}

export type RenderProps = {
  hidden?: boolean;
  title?: string; // todo Deprecated to remove after transition
  label?: string;
  control?: RenderControlFn;
  component: typeof RenderImageComponent | typeof RenderRichTextComponent | typeof RenderRelationshipComponent | typeof RenderMapObjectComponent | typeof RenderTextComponent | typeof RenderStatusComponent, // @todo fix type declaration typeof CodeeInputComponent | typeof CodeeCheckboxComponent,
  relationType?: 'single' | 'multiple',
  relation?: string;
  key?: string;
  format?: 'image' | 'base64';
  maxLength?: number;
}

export enum FilterFunction {
  Equals = 'where',
  Like = 'whereLike',
  TranslationLike = 'whereTranslationLike',
  WhereIn = 'whereIn',
  HasRelation = 'whereHas',
  HasMorphRelation = 'whereHasMorph',
}

