import {Component, Input, OnInit} from '@angular/core';
import {CodeeRenderControlInterface} from "../codee-render-control.interface";
import {FormProps, RenderProps} from "../../../../types/route.type";

@Component({
  selector: 'app-render-relationship',
  templateUrl: './render-relationship.component.html',
  styleUrls: ['./render-relationship.component.scss']
})
export class RenderRelationshipComponent implements OnInit, CodeeRenderControlInterface {

  @Input() props: RenderProps;
  @Input() label: string;
  @Input() item: any;
  @Input() key: string;

  $outputValue: string | null = null;

  constructor() { }

  ngOnInit(): void {

    if (this.props.relationType === 'single')
      this.fetchSingleOutput();
    else if  (this.props.relationType === 'multiple')
      this.fetchMultipleOutput();
  }

  fetchSingleOutput() {
    let relation;

    if (this.props.relation)  {

      ({ [this.props.relation]: relation } = this.item);

      if (relation)
        this.$outputValue = relation[this.props.key];

    }
  }

  fetchMultipleOutput() {
    let relations;

    if (this.props.relation)  {

      ({ [this.props.relation]: relations } = this.item);

      if (relations)
        this.$outputValue = relations.map((r: any) => {
          if (this.props.key) {
            return r[this.props.key];
          }
        }).join(', ');
    }
  }
}
