import {Component, OnInit} from '@angular/core';
import {Tenant, User} from "../../../types/entities.types";
import {SessionService} from "../../../services/auth/session.service";
import {SidebarCompany, SidebarItem} from "../../../types/layout.types";
import {NavigationEnd, Router, Event} from "@angular/router";

import {sidebarMenus, superAdminSidebarMenus} from '../../../config/layout/sidebar-menu-items';
import {environment} from "../../../../environments/environment";

@Component({
    selector: 'app-sidebar',
    templateUrl: './sidebar.component.html',
    styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {

    public sidebarItems: SidebarItem[] = sidebarMenus;
    public superAdminSidebarItems: SidebarItem[] = superAdminSidebarMenus;
    public items: SidebarItem[];

    public user: User = <User>{};
    public activePath: string;

    public isExpanded: boolean = false;
    envCompany: SidebarCompany = environment.company;

    constructor(
        private sessionService: SessionService,
        private router: Router,
    ) {
    }

    ngOnInit(): void {

        // Get the active path
        this.activePath = this.router.url;
        this.router.events.subscribe((event: Event) => {
            if (event instanceof NavigationEnd) {
                // Hide progress spinner or progress bar
                this.activePath = event.url;
            }
        });

        // Get the logged user
        this.user = this.sessionService.loggeinUser.getValue();

        // Define which menu must be seen
        this.items = this.sessionService.isSuperAdmin() ? this.superAdminSidebarItems : this.sidebarItems;

    }


    isRouteActive(item: SidebarItem, depth = 3) {
        // Get the prefix of the url in order to find if is part of the segment
        // e.x. app.tenancy or app.home
        return this.activePath.split('/').slice(1, depth).join('.')
            === item.route.split('/').slice(1, depth).join('.');
    }

    /**
     *
     * @param item
     */
    isPackageVisible(item: SidebarItem) { // console.log(_package)

        if (item.global === true) {
            return true;
        }

        if (item.namespace === undefined && this.sessionService.isSuperAdmin()){
            return true;
        }

        if (this.user.tenants) {
            const selectedTenant = this.sessionService.tenantSelectedSbj.getValue(); // console.log('tenantSelectedSbj', selectedTenant);

            let [tenant] = this.user.tenants.filter( (tenant: Tenant) => tenant.id === selectedTenant.id);

            if (item.namespace) {
                return tenant.packages.map(p => p.path).indexOf(item.namespace) > -1;
            }
        }
        return false;
    }


}


