import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import AvailableFonts from "../../../../config/layout/available-fonts";
import {ApiService} from "../../../../services/abstract/api.service";
import {AlertService} from "../../../../modules/alert/alert.service";
import {HttpParams} from "@angular/common/http";
import {Field} from "../../../../types/route.type";
import {ApiResponse} from "../../../../types/api.types";
import {FieldGeneratorClass} from "../../../../config/common/field-generator.class";

@Component({
  selector: 'settings-global-settings',
  templateUrl: './global-settings.component.html',
  styleUrls: ['./global-settings.component.scss']
})
export class GlobalSettingsComponent implements OnInit {

  public settingsForm: FormGroup = new FormBuilder().group({});

  public aboutUsFields: Field[] = [
    FieldGeneratorClass._text({ key: 'title' , label: 'Title'}),
    FieldGeneratorClass._text({ key: 'subtitle' , label: 'Subtitle'}),
    FieldGeneratorClass._richText({ key: 'content' , label: 'Content'}),
  ];

  public emailFields: Field[] = [
    FieldGeneratorClass._text({ key: 'sender_name' , label: 'Sender name', description: 'The name that will be displayed in the inbox of the recipients'}),
    FieldGeneratorClass._text({ key: 'sender_email' , label: 'Sender email', description: 'Will be visible as sender of the communication from the application'}),
    FieldGeneratorClass._text({ key: 'support_email' , label: 'Support email', description: 'The email address for any supporting tasks'}),
    FieldGeneratorClass._text({ key: 'dpo_email' , label: 'DPO email address', description: 'The email address of the Data Protection Officer'}),
    FieldGeneratorClass._mediaImage({key: 'media_logo', label: 'Email Logo', apiEndpoint: 'files/media'}),
    FieldGeneratorClass._text({ key: 'email_color' , label: 'Email color', description: 'The primary color of email templates'}),
  ];

  public securityFields: Field[] = [
    FieldGeneratorClass._text({ key: 'recaptcha' , label: 'Recaptcha', description: 'The recaptcha field of the for extra security on webforms'}),
  ];

  public loyaltyFields: Field[] = [
    FieldGeneratorClass._text({ key: 'loyalty_program' , label: 'Loyalty Program', description: ''}),
    FieldGeneratorClass._text({ key: 'loyalty_endpoint' , label: 'Loyalty Endpoint', description: ''}),
  ];

    public availableFonts = AvailableFonts;

  constructor(
      private fb: FormBuilder,
      private apiService: ApiService,
      private alertService: AlertService,
  ) { }

  ngOnInit(): void {

    this.settingsForm = this.fb.group({
      'global_fonts': this.fb.group({
        'headings': [1, []],
        'base': [1, []],
      }),
      'global_about_us': this.fb.group({
        'title': ['', []],
        'subtitle': ['', []],
        'content': ['', []],
        'image_1': ['', []],
      }),
      'global_email': this.fb.group({
        'sender_name': ['', []],
        'sender_email': ['', [Validators.email]],
        'support_email': ['', [Validators.email]],
        'media_logo': ['', []],
        'email_color': ['', []],
      }),
      'global_security': this.fb.group({
        'recaptcha': ['', []],
      }),
      'global_loyalty': this.fb.group({
        'loyalty_program': ['', []],
        'loyalty_endpoint': ['', []],
      }),
      'global_footer': this.fb.group({
        'type': ['', []],
      })
    });

    // Get already submitted values
    let params = new HttpParams();
    params = params.append('keys', 'global_fonts,global_about_us,global_email,global_security,global_loyalty');
    this.apiService.getEntities('settings/settings', params, (settings) => {
      settings.data.forEach( (setting: any) => {
        const fControl = this.settingsForm.get(setting.key);
        if (fControl) {
          fControl.patchValue(setting.value);
        }
      })
    })
  }

  onSubmit() {
    this.apiService.addEntity('settings/settings', this.settingsForm.value, (res: ApiResponse) => {
      this.alertService.success(res.message);
    });
  }

}
