import {Injectable} from '@angular/core';
import {RequestService} from "./request.service";
import {AlertService} from "../../modules/alert/alert.service";
import {HttpParams} from "@angular/common/http";
import {ApiError} from "../../types/api.types";

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(
    private request: RequestService,
    private alertService: AlertService,
  ) {}

  getEntity(endpoint: string, id: number | string, params: HttpParams = new HttpParams(), onSuccess: (res: any) => void, onError = (err: ApiError) => this.alertService.danger(err.message)) {
    return this.request.getRequest(endpoint + '/' + id, {params: params})
      .subscribe(onSuccess, onError);
  }

  getEntities(endpoint: string, params: HttpParams = new HttpParams(), onSuccess: (res: any) => void, onError = (err: ApiError) => this.alertService.danger(err.message)) {
    return this.request.getRequest(endpoint, {params: params})
      .subscribe(onSuccess, onError);
  }

  addEntity(endpoint: string, data: object, onSuccess: (res: any) => void, onError = (err: ApiError) => this.alertService.danger(err.message)) {
    return this.request.postRequest(endpoint, data)
      .subscribe(onSuccess, onError);
  }

  updateEntity(endpoint: string, id: number, data: object, onSuccess: (res: any) => void, onError = (err: ApiError) => this.alertService.danger(err.message)) {
    return this.request.putRequest(endpoint + '/' + id, data)
      .subscribe(onSuccess, onError);
  }

  deleteEntity(endpoint: string, id: number, onSuccess: (res: any) => void, onError = (err: ApiError) => this.alertService.danger(err.message)) {
    return this.request.deleteRequest(endpoint + '/' + id)
      .subscribe(onSuccess, onError);
  }
}
