import { Pipe, PipeTransform } from '@angular/core';
import {Alert} from "../alert.types";

@Pipe({
  pure: false,
  name: 'checkTimestamp'
})
export class CheckTimestampPipe implements PipeTransform {

  transform(value: Alert[], seconds: number): Alert[] {
    return value.filter( alert => {
      return Date.now() - alert.timestamp < seconds * 1000;
    });
  }

}
