import {Component, Input, OnInit} from '@angular/core';
import {CodeeRenderControlInterface} from "../codee-render-control.interface";
import {RenderProps} from "../../../../types/route.type";

@Component({
  selector: 'app-render-rich-text',
  templateUrl: './render-rich-text.component.html',
  styleUrls: ['./render-rich-text.component.scss']
})
export class RenderRichTextComponent implements OnInit, CodeeRenderControlInterface {

  @Input() props: RenderProps;
  @Input() label: string;
  @Input() item: any;
  @Input() key: string;

  constructor() { }

  ngOnInit(): void {
  }

  renderValue() {
    if (this.item[this.key]) {
      return this.item[this.key];
    }
  }

}
