import {Component, Input, OnInit} from '@angular/core';
import {CodeeFormControlInterface} from "../codee-form-control.interface";
import {FormProps} from "../../../../types/route.type";
import {CdkDragDrop, moveItemInArray} from "@angular/cdk/drag-drop";
import {HttpParams} from "@angular/common/http";
import {ApiService} from "../../../../services/abstract/api.service";

@Component({
    selector: 'app-codee-sorting',
    templateUrl: './codee-sorting.component.html',
    styleUrls: ['./codee-sorting.component.scss']
})
export class CodeeSortingComponent implements OnInit, CodeeFormControlInterface {

    sortableItems: Array<any> = [];

    @Input() referenceId: number;
    @Input() description: string;

    @Input() label: string;
    @Input() value: boolean | string | number | number[];
    @Input() mode: string;
    @Input() data: any;
    @Input() props: FormProps;

    constructor(
        private apiService: ApiService,
    ) {
    }

    ngOnInit(): void {
        this.loadSortableItems()
    }


    onChange = (value: any) => {
    };

    onTouched = () => {
    };

    registerOnChange(onChange: any) {
        // console.log('registerOnChange', onChange)
        this.onChange = onChange;
    }

    registerOnTouched(onTouched: any) {
        this.onTouched = onTouched;
    }

    setDisabledState(isDisabled: boolean): void {
    }

    writeValue(obj: any): void {
        // console.log('writeValue', obj)
    }


    drop(event: CdkDragDrop<string[]>) {
        moveItemInArray(this.sortableItems, event.previousIndex, event.currentIndex);
        this.defineControlValue();
    }

    defineControlValue() {
        const orderedValues = {
            relation: this.props.relation,
            values: this.sortableItems.map( (item, index) => {
                return {
                    id: item.id,
                    order: index,
                }
            })
        };
        this.onChange(orderedValues);
    }

    loadSortableItems() {
        if (this.props.apiEndpoint) {
            let params = new HttpParams();
            params = params.append('properties', JSON.stringify({
                value: this.referenceId,
                filterableFn: 'whereHas',
                relation: this.props.controlKey
            }));
            params = params.append('resultsPerPage', 'all');
            params = params.append('page', '1');
            this.apiService.getEntities(this.props.apiEndpoint, params, res => {
                // console.log(res);
                this.sortableItems = res.data.sort((a: any, b: any) => {
                    return a.properties.find( (i: any) => i.id === this.referenceId).pivot.order -
                        b.properties.find( (i: any) => i.id === this.referenceId).pivot.order;
                })
                this.defineControlValue();
            });
        }
    }

}
