import {Component, forwardRef, Input, OnInit} from '@angular/core';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from "@angular/forms";
import {CodeeFormControlInterface} from "../codee-form-control.interface";
import {FormProps} from "../../../../types/route.type";

@Component({
  selector: 'app-codee-quantity',
  templateUrl: './codee-quantity.component.html',
  styleUrls: ['./codee-quantity.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CodeeQuantityComponent),
      multi: true
    }
  ]
})
export class CodeeQuantityComponent implements OnInit, CodeeFormControlInterface {

  @Input() label: string;
  @Input() mode: string;
  @Input() value: number[] = [];
  @Input() props: FormProps;
  @Input() counterValue: number;

  public ngOnInit(): void {}

  onChange = (quantity: any) => {};

  onTouched = () => {};

  touched = false;

  disabled = false;

  increment() {
    this.counterValue++;
    this.onChange(this.counterValue);
  }

  decrement() {
    this.counterValue--;
    this.onChange(this.counterValue);
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  writeValue(value: number) {
    this.counterValue = value;
  }

  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

}
