import { Component, OnInit } from '@angular/core';
import {Route} from "../../../types/route.type";
import {ApiService} from "../../../services/abstract/api.service";
import {AlertService} from "../../../modules/alert/alert.service";
import {ActivatedRoute, Router} from "@angular/router";
import {HttpParams} from "@angular/common/http";

@Component({
  selector: 'app-callback',
  templateUrl: './callback.component.html',
  styleUrls: ['./callback.component.scss']
})
export class CallbackComponent implements OnInit {

  public $route: Route;
  public entity: any;

  constructor(
    private apiService: ApiService,
    private alertService: AlertService,
    private router: Router,
    private route: ActivatedRoute,
  ) { }

  ngOnInit(): void {

    this.route.data.subscribe(data => {
      this.$route = data.handler.route;

      this.getEntityIfNeeded();
    });
  }

  get _view() {
    return this.$route.view;
  }

  get item() {
    return this.entity;
  }


  getEntityIfNeeded() {
    const _id = this.route.snapshot.paramMap.get('id');
    if (_id !== null) {
      let params = new HttpParams();
      if (this.$route.withRelations)
        params = params.append('with', this.$route.withRelations.join(','));

      this.apiService.getEntity(this.$route.endpoint, _id, params, (res) => {
        this.entity = res;
      })
    }
  }

}
