import {Component, forwardRef, Input, OnInit} from '@angular/core';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from "@angular/forms";
import {FormProps} from "../../../../types/route.type";
import {CodeeFormControlInterface} from "../codee-form-control.interface";

@Component({
  selector: 'app-codee-checkbox',
  templateUrl: './codee-checkbox.component.html',
  styleUrls: ['./codee-checkbox.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CodeeCheckboxComponent),
      multi: true
    }
  ]
})
export class CodeeCheckboxComponent implements OnInit, CodeeFormControlInterface {

  @Input() label: string;
  @Input() value: boolean;
  @Input() mode: string;
  @Input() data: any;
  @Input() props: FormProps;

  inputModel: boolean;

  ngOnInit() {}

  onChange = (value: boolean) => {  };

  onTouched = () => {};

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  setDisabledState(isDisabled: boolean): void {
  }

  writeValue(value: boolean): void {
    this.inputModel = value;
  }

}
