import { Component, OnInit } from '@angular/core';
import {ApiService} from "../../../../services/abstract/api.service";
import {HttpParams} from "@angular/common/http";
import {LayoutItem, Metadata} from "../../../../types/layout.types";
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {Field} from "../../../../types/route.type";
import AvailableLanguages from "../../../../config/layout/available-languages";
import {SelectLayoutBlockControl} from "../../../../helpers/select-layout-block-control";
import {LayoutBlock} from "../../../../types/entities.types";
import {RenderTextComponent} from "../../../../ui/render/fields/render-text/render-text.component";
import {environment} from "../../../../../environments/environment";

@Component({
  selector: 'app-translate-layout-blocks',
  templateUrl: './translate-layout-blocks.component.html',
  styleUrls: ['./translate-layout-blocks.component.scss']
})
export class TranslateLayoutBlocksComponent implements OnInit {

  public translateForm: FormGroup = new FormBuilder().group({});
  public translatableForm: FormGroup = new FormBuilder().group({});
  public isTranslateActive: boolean = false;

  public translatableFields: Field[] = [];
  public items: LayoutBlock[] = [];
  public metadata: Metadata;
  public page: number = 1;

  public availableLanguages: Language[];
  public state: TranslateState = {} as TranslateState;
  protected translatableItem: LayoutBlock;

  constructor(
    private apiService: ApiService,
  ) { }

  ngOnInit(): void {

    this.availableLanguages = AvailableLanguages; // console.log(this.availableLanguages);

    // Setup state (selectedLang and more)
    this.loadConfiguration();

    this.getEntities(1);
  }

  get translatableLanguages() {
    return this.availableLanguages.filter( lang => lang.code !== environment.defaultLocale);
  }

  getEntities(page: number = 1) { // console.log(page);
    if (page) {
      this.page = page;
    }

    let params = new HttpParams();
    params = params.append('with', 'related.layoutBlockables');
    params = params.append('translations', 'true');
    params = params.append('resultsPerPage', '12');
    params = params.append('page', this.page.toString());
    this.apiService.getEntities('hotel/layout-blocks', params, (res: any) => {
      this.items = res.data;
      this.metadata = res.meta;
    });
  }

  enableEntityTranslation(item: LayoutBlock) { // console.log(item);
    this.translatableItem = item;
    this.isTranslateActive = true;
    // We have to get translatable fields from layout blogs
    this.translatableFields = [];
    this.translatableItem.layout_block_type.fields.items.forEach( (layoutItem: LayoutItem, index: number) => {
      this.translatableFields.push({
        label: SelectLayoutBlockControl.theLayoutLabel(layoutItem, index), // `${index + 1}.${layoutItem.label}`,
        formProps: SelectLayoutBlockControl.getControl(layoutItem),
        key: SelectLayoutBlockControl.theLayoutKey(layoutItem, index),
        hidden: false,
        validators: Validators.required,
        renderProps: {
          hidden: false,
          // control: RenderControl.string, // Used in: manage app-codee-render-outlet
          component: RenderTextComponent,
        }
      } as Field)
    });

    // Add controls to forms
    this.addControlsToForms();

    // Fill forms
    this.fillTranslateForm()
    this.fillTranslatableForm()
  }

  /**
   * Add controls to both forms
   */
  addControlsToForms() {
    // Configure filters controls
    this.translatableFields.forEach(field => {
      this.translateForm.addControl(field.key, new FormControl('', []));
      this.translatableForm.addControl(field.key, new FormControl('', []));
    });
  }

  saveConfiguration() {
    sessionStorage.setItem('_transState', JSON.stringify(this.state))
  }

  loadConfiguration() {
    const session = sessionStorage.getItem('_transState');
    if (session) {
      this.state = JSON.parse(session) as TranslateState;
    } else {
      const [defaultLang] = AvailableLanguages.filter( lang => lang.code === environment.defaultLocale);
      this.state = {
        selectedLang: defaultLang,
      }
      this.saveConfiguration();
    }
  }

  selecteLanguageToTranslate(lang: Language) {
    // Before changing language we save the translations
    if (!this.translatableForm.pristine) {
      this.onTranslate(false);
    }
    // Now we actually change language
    this.state.selectedLang = lang;
    this.saveConfiguration();
    this.fillTranslatableForm();
  }

  onTranslate(closeModal: boolean = true){
    if (typeof this.translatableItem.id === 'number'){
      this.apiService.updateEntity('hotel/layout-blocks',
        this.translatableItem.id,
        {
          id: this.translatableItem.id,
          lang: this.state.selectedLang.code,
          data: this.translatableForm.value,
        }, (res: any) => {
          if (closeModal) {
            this.isTranslateActive = false;
          }
          this.getEntities();
        });
    }
  }

  copyFromEnglish() {
    this.translatableForm.markAsDirty();
    this.translatableForm.patchValue(this.translateForm.value);
  }

  fillTranslateForm() {
    this.translateForm.patchValue(this.translatableItem.data);
    // this.translatableFields.forEach( field => {
    //   console.log('fillTranslateForm', this.translatableItem.data, field);
    //   this.translateForm.controls[field.key].setValue(this.translatableItem.data[field.key]);
    // })
  }

  fillTranslatableForm() {
    this.translatableForm.reset();
    // check if lang translation already exists
    const [translatedEntity] = this.translatableItem.translations.filter( item => {
      return item.locale === this.state.selectedLang.code;
    });
    // console.log('translatedEntity', translatedEntity, this.translatableFields);
    if (translatedEntity){
      console.log('translatedEntity', translatedEntity)
      this.translatableForm.patchValue(translatedEntity.data);
      // this.translatableFields.forEach( field => {
      //   console.log('field.key', field.key);
      //   this.translatableForm.controls[field.key].setValue(translatedEntity.data[field.key]);
      // })
    }
  }

  pageChanged(page: number = 1) {
    this.getEntities(page);
  }

  closeTranslatableModal(){
    this.isTranslateActive = false;
  }

}

type TranslateState = {
  selectedLang: Language
}

type Language = {
  id: number;
  code: string;
  label: string;
}

