import { Injectable } from '@angular/core';
import {BehaviorSubject} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class SortManagementService {

  public props = new BehaviorSubject<SortProps>({
    active: false,
    reference_id: null,
  });

  constructor() { }

  handle({reference_id}: SortProps) {
    const props = this.props.getValue();
    props.active = true;
    props.reference_id = reference_id;
    this.props.next(props);
  }

  disable() {
    const props = this.props.getValue();
    props.active = false;
    props.reference_id = null;
    this.props.next(props);
  }

  getProps() {
    return this.props.asObservable();
  }
}


type SortProps = {
  active: boolean;
  reference_id: number | null;
}
