import {Injectable} from '@angular/core';
import {Tenant, User} from "../../types/entities.types";
import {BehaviorSubject, Subject} from "rxjs";

@Injectable({
    providedIn: 'root'
})
export class SessionService {

    private localStorareKey = '_storedTenant';
    private tokenKey = 'sessKey';
    private connectResponse: ConnectResponse;

    // Subject for user
    public loggeinUser = new BehaviorSubject<User>({} as User);

    // Subjects for subscriptions
    public tenantsSubject = new BehaviorSubject<Tenant[]>([] as Tenant[]);
    public tenantSelectedSbj = new BehaviorSubject<Tenant>({} as Tenant);

    constructor() {
        const session = this.getSession();
        const {user} = session;
        if (user && user.tenants) {
            this.doSetTenants(user.tenants);
        }
    }

    // get accessLevel(): number {
    //   console.log(this.connectResponse);
    //   if (this.connectResponse && this.connectResponse.user) {
    //     return this.connectResponse.user.level || 0;
    //   }
    //   else {
    //     return 0;
    //   }
    // }


    /**
     * Save access token occured right after the connect successful request
     * @param connectResponse
     */
    saveAccessToken(connectResponse: ConnectResponse) {
        // console.log('saveAccessToken', connectResponse)
        this.connectResponse = connectResponse;
        localStorage.setItem(this.tokenKey, JSON.stringify(connectResponse));
        const {user} = connectResponse;
        // Fill the subject with the returned user
        this.loggeinUser.next(user)
    }

    /**
     * Called from AuthGuard
     */
    isLoggedin() {
        const session = this.getSession();
        // console.log('SessionService:isLoggedin', session, session.hasOwnProperty('user'));
        // Check if session has token property
        if (session.hasOwnProperty('token') && session.hasOwnProperty('user')) {
            this.loggeinUser.next(session.user);
            return session;
        } else {
            return false;
        }
    }


    getSession(): ConnectResponse {
        // console.log('getSession');
        const response = localStorage.getItem(this.tokenKey);
        if (response === null) {
            return {} as ConnectResponse;
        }
        let connectResponse: ConnectResponse;
        connectResponse = JSON.parse(response);
        return connectResponse;
    }

    removeSession() {
        // Remove session data
        localStorage.removeItem(this.tokenKey);
        // Remove localStorage tenants
        localStorage.removeItem(this.localStorareKey);
    }

    /**
     * Check if the user has the super admin role or not
     */
    isSuperAdmin() {
        const [role] = this.loggeinUser.getValue().roles.filter( role => {
            return role.slug === "superadmin";
        });
        return role;
    }



    /**
     * Return tenants observable
     */
    tenants() {
        return this.tenantsSubject.asObservable();
    }

    doSetTenants(tenants: Tenant[]) {
        this.tenantsSubject.next(tenants);
    }

    selectedTenant() {
        return this.tenantSelectedSbj.asObservable();
    }

    doSelectTenant(id: string) {
        const [tenant] = this.tenantsSubject.getValue().filter(t => t.id === id);
        if (tenant) {
            localStorage.setItem(this.localStorareKey, JSON.stringify(tenant));
            this.tenantSelectedSbj.next(tenant);
        }
    }

    initializeTenant() {
        const storedTenant = localStorage.getItem(this.localStorareKey);

        let tenant;
        if (storedTenant && storedTenant !== 'undefined') {
            tenant = JSON.parse(storedTenant);
        } else {
            tenant = this.tenantsSubject.getValue()[0];
        }
        localStorage.setItem(this.localStorareKey, JSON.stringify(tenant));
        this.tenantSelectedSbj.next(tenant as Tenant);
    }
}


type ConnectResponse = {
    token: {
        access_token?: string;
        expires_at?: string;
        token_type?: string;
    },
    user: User
}
