import { Component, OnInit } from '@angular/core';
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeFileUploadComponent} from "../../../ui/forms/fields/codee-file-upload/codee-file-upload.component";
import {Router} from "@angular/router";

@Component({
  selector: 'app-upload-media',
  templateUrl: './upload-media.component.html',
  styleUrls: ['./upload-media.component.scss']
})
export class UploadMediaComponent implements OnInit {

  uploadFileProps = {
    control: FetchControl.value,
    component: CodeeFileUploadComponent,
    // editable: true,
    apiEndpoint: "files/media", // @ todo how to pass FieldsCommonClass.namespace again
    fileTypes: 'image/png, image/jpg, image/jpeg, image/gif, image/svg, image/svg+xml, image/webp, application/pdf'
  }

  constructor(
    private router: Router,
  ) { }

  ngOnInit(): void {
  }

  changed(e: any) {
    console.log('changed', e);
  }

  uploadCompleted($event: boolean) {
    console.log('  uploadCompleted', $event);
    this.router.navigate(['app/files/media/manage']);
  }

}
