import {Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {CodeeHiddenComponent} from "../../../ui/forms/fields/codee-hidden/codee-hidden.component";

const packages: Route = {
  endpoint: "tenancy/packages",
  model: "App\\Models\\Tenancy\\Package",
  texts: {
    key: 'package',
    label: $localize`Package`,
    title: $localize`Packages`,
    backLabel: $localize`Back to Packages`,
    description: $localize`Below is the list of the created packages`,
  },
  fieldGroups: [{
    title: $localize`Details`,
    key: 'details',
    fields: [{
      key: 'name',
      label: $localize`Package Name`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'text',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: true,
      },
      renderProps: {
        // control: RenderControl.string, // Used in: manage app-codee-render-outlet
        component: RenderTextComponent,
      },
    },{
      key: 'path',
      label: $localize`Package Path`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'text',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: true,
      },
      renderProps: {
        // control: RenderControl.string, // Used in: manage app-codee-render-outlet
        component: RenderTextComponent,
      },
    }, {
      key: 'id',
      label: $localize`ID`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'hidden',
        control: FetchControl.value,
        component: CodeeHiddenComponent,
        editable: false,
      },
      renderProps: {
        // control: RenderControl.string, // Used in: manage app-codee-render-outlet
        component: RenderTextComponent,
      },
    },]
  }],
  label: "",
  links: {
    create: '/app/tenancy/packages/create',
    update: '/app/tenancy/packages/update/',
    manage: '/app/tenancy/packages/manage',
  },
  manage: {
    hasStatus: false,
    columns: [{
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      },{
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      },],
    }]
  },
}

export default packages;
