export class FetchControl {

  static value<T>(value: T): T {
    return value;
  }

  static valueBoolean(value: number): boolean {
    return value === 1;
  }

  // static mapObject<T extends Array<FetchObject>>(value: T, key: string = ''): number[] {
  //   // console.log('FetchControl mapObject', value, key)
  //   return value === undefined ? [] : value.map<number>(v => parseInt(v.id));
  // }

  static mapIds<T extends Array<FetchObject>>(value: T, key: string = ''): number[] {
    // console.log('FetchControl mapObject', value, key)
    return value === undefined ? [] : value.map<number>(v => parseInt(v.id));
  }

  static singleRelation<T extends FetchObject>(value: T, key: string = ''): number | null {
    // console.log('FetchControl singleRelation', value, key, typeof value)
    // return value[this.controlKey]
    return value === undefined ? null : parseInt(value.id);
  }

  static multipleRelation<T extends Array<FetchObject>>(value: T, key: string = ''): string[] {
    // console.log('FetchControl multipleRelation', value, key, typeof value)
    return value === undefined ? [] : value.map(i => i.id);
  }


  static returnDomains<T extends Array<FetchObject>>(value: T, key: string = ''): object {
    // console.log('FetchControl returnDomains', value, key);
    return value === undefined ? [] : value.map(v => {
      return {
        id: v.id,
        // title: v.tenant_id,
        content: v.domain,
      }
    });
  }

  static returnValues<T extends Array<FetchObject>>(value: T, key: string = ''): object {
    // console.log('FetchControl returnValues', value, key,);
    return value === undefined ? [] : value.map(v => {
      return {
        id: v.id,
        title: v.title,
        content: v.content,
      }
    });
  }

}

export interface FetchControlFn {
  (value: any, key?: string): void;
}


interface FetchObject {
  [key: string]: string,
}
