import {Component, Input, OnInit} from '@angular/core';
import {RenderProps} from "../../../../types/route.type";
import {CodeeRenderControlInterface} from "../codee-render-control.interface";

@Component({
  selector: 'app-render-status',
  templateUrl: './render-status.component.html',
  styleUrls: ['./render-status.component.scss']
})
export class RenderStatusComponent implements OnInit, CodeeRenderControlInterface {

  @Input() props: RenderProps;
  @Input() label: string;
  @Input() item: any;
  @Input() key: string;

  constructor() { }

  ngOnInit(): void {
  }

  get status() {
    if (this.item[this.key] !== undefined || this.item[this.key] !== null) {
      return this.item[this.key];
    } else {
      return -1;
    }
  }

}
