import {Component, Input, OnChanges, OnInit, Output, EventEmitter, SimpleChanges} from '@angular/core';
import {Metadata} from "../../../types/layout.types";

@Component({
  selector: 'app-pagination',
  templateUrl: './pagination.component.html',
  styleUrls: ['./pagination.component.scss']
})
export class PaginationComponent implements OnChanges {

  @Input() metadata: Metadata;
  @Input() page: number;
  @Input() offset: number = 3;
  @Input() hasEdgeLinks: boolean = true;
  @Output() refreshEntities = new EventEmitter<number>();

  public $visiblePages = [];

  constructor() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.metadata) {
      this.calcVisiblePages();
    }
  }

  setPage(page: number) {
    if (page > 0 && page <= this.metadata.totalPages && page !== this.page) {
      this.page = page;
      this.refreshEntities.emit(this.page);
    }
  }

  calcVisiblePages() {
    this.$visiblePages = [];
    for (let _i = this.page - this.offset; _i <= this.page + this.offset; _i++) {
      if (_i <= 0 || _i > this.metadata.totalPages) {
        continue;
      }
      // @ts-ignore
      this.$visiblePages.push(_i);
    }
  }

}


