import {FilterFunction, Route} from "../../../types/route.type";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {CodeeSelectComponent} from "../../../ui/forms/fields/codee-select/codee-select.component";
import {ApiData} from "../../../types/api.types";

const route: Route = {
  endpoint: "hotel/awards",
  translatable: true,
  model: "CodeeHut\\HotelManagement\\Models\\Award",
  texts: {
    key: 'awards',
    label: $localize`Award`,
    title: $localize`Awards`,
    backLabel: $localize`Back to Awards`,
    description: $localize`Below is the list of the created Awards`,
  },
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_main',
        label: $localize`Main image`,
        description: $localize`The image thumbnail`,
        apiEndpoint: 'files/media'
      }),
      FieldGeneratorClass._mediaDocument({
        key: 'media_file',
        label: $localize`Award file`,
        description: $localize`The file that opens as a new tab when the user clicks on the award`,
        fileTypes: 'image/png, application/pdf, image/jpg, image/jpeg',
        apiEndpoint: 'files/media'
      }),
      // FieldsCommonClass._mediaMain(6),
      // FieldsCommonClass._mediaFile(6, true),
    ]
  }, {
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldGeneratorClass._text({
        key: 'id', label: $localize`ID`, hidden: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        }
      }),
      FieldGeneratorClass._text({
        key: 'name', label: $localize`Name`, description: $localize`The award name (only for administrative reasons))`, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        }
      }),
      FieldGeneratorClass._choiceFromApi({
        key: 'properties', col: 12, label: $localize`Properies`, apiEndpoint: 'hotel/properties', filter: {
          control: FetchControl.singleRelation,
          component: CodeeSelectComponent,
          dataEndpoint: 'hotel/properties?resultsPerPage=all&page=1',
          mapSelectObjectFn: (item: ApiData) => {
            return {
              id: item.id,
              label: item.name,
            };
          },
          filterable: FilterFunction.HasRelation,
          relation: 'properties',
        }
      }),

      // FieldsCommonClass._id(6),
      // FieldsCommonClass._name(6),
      // FieldsCommonClass._propertyRelation()
    ]
  }],
  label: "",
  links: {
    create: '/app/hotel/awards/create',
    update: '/app/hotel/awards/update/',
    manage: '/app/hotel/awards/manage',
  },
  manage: {
    hasStatus: true,
    columns: [{
      title: 'Logo',
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: 'ID',
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }],
    }]
  }
};


export default route;
