import {FilterFunction, Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {FieldGeneratorClass} from "../../common/field-generator.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";

const route: Route = {
  endpoint: "business-listing/businesses",
  model: "CodeeHut\\BusinessListing\\Models\\Business",
  fieldGroups: [{
    title: 'Images',
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({key: 'media_main', label: 'Main image', description: 'The image thumbnail', apiEndpoint: 'files/media'}),
      // FieldsCommonClass._mediaMain(6),
      // FieldsCommonClass._mediaFile(6, true),
    ]
  }, {
    title: 'Details',
    key: 'details',
    fields: [
      FieldGeneratorClass._text({ key: 'id', label: 'ID', hidden: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        } }),
      FieldGeneratorClass._text({ key: 'name', label: 'Name', filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        } }),
      // FieldGeneratorClass._choiceFromApi({key: 'categories', col: 12, label: 'Categories', apiEndpoint: 'business-listing/properties', filter: {
      //     control: FetchControl.singleRelation,
      //     component: CodeeSelectComponent,
      //     dataEndpoint: 'business-listing/categories?resultsPerPage=all&page=1',
      //     mapSelectObjectFn: (item: ApiData) => {
      //       return {
      //         id: item.id,
      //         label: item.name,
      //       };
      //     },
      //     filterable: FilterFunction.HasRelation,
      //     relation: 'properties',
      //   } }),
    ]
  }],
  label: "",
  links: {
    create: '/app/business-listing/businesses/create',
    update: '/app/business-listing/businesses/update/',
    manage: '/app/business-listing/businesses/manage',
  },
  texts: {
    key: 'businesses',
    label: 'Business',
    title: 'Businesses',
    backLabel: 'Back to Businesses',
    description: 'Below is the list of the created Businesses',
  },
  manage: {
    hasStatus: true,
    columns: [{
      title: 'Logo',
      className: 'col-auto',
      fields: [{
        label: 'Main image',
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: 'Data',
      className: 'col',
      fields: [{
        label: 'ID',
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: 'Name',
        key: 'name',
        component: RenderTextComponent,
      }],
    }]
  }
};


export default route;
