import { Route} from "../../../types/route.type";
import {Validators} from "@angular/forms";
import {FetchControl} from "../../../inc/fetch-control.class";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {RenderMapObjectComponent} from "../../../ui/render/fields/render-map-object/render-map-object.component";
import {CodeeChoicesComponent} from "../../../ui/forms/fields/codee-choices/codee-choices.component";
import {CodeeListSingleComponent} from "../../../ui/forms/fields/codee-list-single/codee-list-single.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";

const route: Route = {
  endpoint: 'tenancy/tenants',
  model: "App\\Models\\Tenancy\\Tenant",
  label: $localize`Tenant`,
  texts: {
    key: 'tenant',
    label: $localize`Tenant`,
    title: $localize`Tenants`,
    backLabel: $localize`Back to Tenants`,
    description: $localize`Below is the list of the created tenants`,
  },
  links: {
    create: '/app/tenancy/tenants/create',
    update: '/app/tenancy/tenants/update/',
    manage: '/app/tenancy/tenants/manage',
  },
  fieldGroups: [{
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldGeneratorClass._text({ key: 'id', label: $localize`ID`, editable: false}),
      ]
  }, {
    title: $localize`Domains`,
    key: 'domains',
    fields: [{
      key: 'domains',
      label: $localize`Domain`,
      hidden: false,
      col: 9,
      default: '',
      validators: [Validators.required],
      // validators: [Validators.required, Validators.minLength(3)],
      formProps: {
        type: 'input',
        control: FetchControl.returnDomains,
        component: CodeeListSingleComponent,
        editable: true,
        isTextarea: false,
      },
      renderProps: {
        component: RenderMapObjectComponent,
        key: 'domain',
      },
    }, {
      key: 'packages',
      label: $localize`Packages`,
      hidden: false,
      col: 12,
      default: '',
      validators: [],
      formProps: {
        type: 'choices',
        control: FetchControl.mapIds,
        component: CodeeChoicesComponent,
        dataEndpoint: 'tenancy/packages',
        editable: true,
      },
      renderProps: {
        component: RenderMapObjectComponent,
        key: 'name',
      },
    }],
  }],
  manage: {
    hasStatus: false,
    columns: [{
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Domains`,
        component: RenderRelationshipComponent,
        relation: 'domains',
        relationType: 'multiple',
        key: 'domain',
      }, {
        label: $localize`Packages`,
        component: RenderRelationshipComponent,
        relation: 'packages',
        relationType: 'multiple',
        key: 'name',
      }],
    }]
  },

}

export default route;
