import {FilterFunction, Route} from "../../../types/route.type";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";

const route: Route = {
  endpoint: "files/media",
  withAttributes: [],
  model: "CodeeHutCommon\\FilesRepository\\Models\\Media",
  texts: {
    key: 'media',
    label: $localize`Media`,
    title: $localize`Media`,
    backLabel: $localize`Back to media`,
    description: $localize`Below is the list of the created media`,
  },
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_main',
        label: 'Main image',
        apiEndpoint: 'files/media'
      }),
    ]
  }, {
    title: 'Details',
    key: 'details',
    fields: [
      FieldGeneratorClass._text({ key: 'id', label: $localize`ID`, hidden: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.Like
        } }),
      FieldGeneratorClass._text({ key: 'name', label: $localize`Name`, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.Like
        } }),
      // FieldsCommonClass._id(6),
      // FieldsCommonClass._name(6),
    ]
  }],
  label: "",
  links: {
    create: '/app/upload-media',
    // update: '/app/files/media/update/',
    manage: '/app/files/media/manage',
  },
  manage: {
    hasStatus: false,
    hasQrCodes: true,
    urlPattern: ':mediaUrl:data.path'  ,
    columns: [{
      title: $localize`Main image`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Details`,
      className: 'col',
      fields: [{
        label: $localize`Id`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Filename`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Mime`,
        key: 'mime',
        component: RenderTextComponent,
      }],
    }, /*{
            title: 'SVG',
            className: 'col-auto',
            fields: [{
                label: 'QR code',
                key: 'qr_code',
                component: RenderImageComponent,
                format: 'base64',
            }]
        }*/]
  }
};


export default route;
