import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges
} from '@angular/core';
import {FilterFunction, FormProps, Route} from "../../../../types/route.type";
import {CodeeFormControlInterface} from "../codee-form-control.interface";
import {ApiService} from "../../../../services/abstract/api.service";
import {AlertService} from "../../../../modules/alert/alert.service";
import {ApiResponse} from "../../../../types/api.types";
import {HttpParams} from "@angular/common/http";
import {CdkDragDrop, moveItemInArray, transferArrayItem} from "@angular/cdk/drag-drop";
import {RelationType} from "../../../../types/entities.types";
import RelationTypeItems from "../../../../config/layout/relation-types";
import {ActivatedRoute} from "@angular/router";
import {BehaviorSubject} from "rxjs";

@Component({
  selector: 'app-codee-relation',
  templateUrl: './codee-relation.component.html',
  styleUrls: ['./codee-relation.component.scss']
})
export class CodeeRelationComponent implements OnInit, OnChanges, CodeeFormControlInterface {

  /**
   * todo too much "any" types
   */

  public relationTypeItems: RelationType[] = RelationTypeItems;

  @Input() label: string;
  @Input() value: string;
  @Input() mode: string;
  @Input() data: any;
  @Input() props: FormProps;

  public canBeRelatedEntities: any[] = [];
  public areRelatedEntities: any[] = [];

  public fieldValueObj = {
    relation: '',
    items: [] as any,
  }

  constructor(
      private apiService: ApiService,
      private alertService: AlertService,
      private route: ActivatedRoute,
  ) {}


  ngOnInit() {}


  @Output() onValueChanged = new EventEmitter<string>();

  onChange = (value: any) => {};

  onTouched = () => {};


  registerOnChange(onChange: any) {
    // console.log('registerOnChange', onChange)
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  writeValue(value: any) { // console.log('CodeeRelationComponent@writeValue', value);
    if (value && value.hasOwnProperty('items') && value.hasOwnProperty('relation')) {
      this.fieldValueObj = {...value};
    }
    this.loadCanBeRelatedEntities();
  }

  onSelectChanged(){
    this.canBeRelatedEntities = [];
    this.areRelatedEntities = [];
    this.fieldValueObj.items = [];
    this.loadCanBeRelatedEntities();
  }

  ngOnChanges(changes: SimpleChanges): void {
    console.log('[CodeeRelationComponent]ngOnChanges', changes);
  }


  loadCanBeRelatedEntities() {

    const fieldValueObj = {...this.fieldValueObj};

    const endpoint = fieldValueObj.relation;

    // Get route params
    const route = this.route.data as BehaviorSubject<any>;
    const routeData = route.getValue();

    if (endpoint === undefined || endpoint === null || endpoint === '') {
      return;
    }

    let params = new HttpParams();

    params = params.append('resultsPerPage', 'all');
    params = params.append('page', '1');
      // If the relation key is properties then search only for entities
    // related to the specific property id
    if (routeData.handler.route.layoutBlock.relation_key === 'properties') {
      params = params.append('property_id', JSON.stringify({
        value: this.route.snapshot.paramMap.get('id'),
        filterableFn: FilterFunction.HasRelation,
        relation: this.props.relation,
      }));
    }

    this.apiService.getEntities( endpoint, params,
        (res: ApiResponse) => {
          // Filter the NOT related content
          this.canBeRelatedEntities = res.data.filter( (item: any) => {
            return fieldValueObj.items.map( (v: any) => v.id).indexOf(item.id) === -1;
          });
          // Filter and SORT the related content
          const sortingArr = fieldValueObj.items.map( (i: any) => i.id);
          this.areRelatedEntities = res.data.filter( (item: any) => {
            return fieldValueObj.items.map( (v: any) => v.id).indexOf(item.id) > -1;
          }).sort((a: any, b: any) => sortingArr.indexOf(a.id) - sortingArr.indexOf(b.id));

          this.fetchFieldValue();
        })
  }

  drop(e: CdkDragDrop<any[]>) {
    if (e.previousContainer === e.container) {
      moveItemInArray(e.container.data, e.previousIndex, e.currentIndex);
      this.fetchFieldValue();
    } else {
      transferArrayItem(e.previousContainer.data, e.container.data, e.previousIndex, e.currentIndex,);
      this.fetchFieldValue();
    }
  }

  /**
   * The returned value object
   */
  fetchFieldValue() {
    this.fieldValueObj.items = this.areRelatedEntities.map( i => i.id);
    // console.log('fetchFieldValue', this.fieldValueObj);
    this.onChange(this.fieldValueObj);
  }

}
