import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {ActivatedRoute, Router} from "@angular/router";
import {ApiService} from "../../../../../services/abstract/api.service";
import {Role} from "../../../../../types/entities.types";
import {HttpParams} from "@angular/common/http";

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss'],
  // providers: [
  //   {provide: 'endpoint', useValue: 'roles'},
  // ]
})
export class FormComponent implements OnInit {

  public $roles: Role[] = [];

  public entityForm: FormGroup;

  public passwordVisible = false;

  constructor(
    private fb: FormBuilder,
    private apiService: ApiService,
    private router: Router,
    private route: ActivatedRoute,
  ) {

    this.entityForm = this.fb.group({
      id: ['', []],
      name: ['', [Validators.required]],
      email: ['', [Validators.required]],
      password: ['', []],
      password_confirmation: ['', []],
      roles: ['', [Validators.required]],
    });

  }

  ngOnInit(): void {

    const _id = this.route.snapshot.paramMap.get('id');
    if (_id !== null) {
      this.apiService.getEntity('users', parseInt(_id), new HttpParams(), (res) => {
        this.entityForm.patchValue(res.data);
        this.entityForm.controls['roles'].setValue(res.data.roles.map( (role : Role) => role.id));
      })
    }

    // Get roles
    this.apiService.getEntities('roles', new HttpParams(), res => {
      this.$roles = res.data;
    });
  }

  onSubmit() {
    if (this.entityForm.controls['id'].value) {
      this.apiService.updateEntity('users', this.entityForm.controls['id'].value,
        this.entityForm.value,
        (res) => {
          console.log(res);
        });
    } else {
      this.apiService.addEntity('users', this.entityForm.value, (res) => console.log(res));
    }
  }


  togglePassword(){
    this.passwordVisible = ! this.passwordVisible;
  }
}
