import {Component, Input, OnInit} from '@angular/core';
import {Media} from "../../../../types/entities.types";
import {CodeeRenderControlInterface} from "../codee-render-control.interface";
import {FormProps, RenderProps} from "../../../../types/route.type";

@Component({
  selector: 'app-render-image',
  templateUrl: './render-image.component.html',
  styleUrls: ['./render-image.component.scss']
})
export class RenderImageComponent implements OnInit, CodeeRenderControlInterface {

  @Input() props: RenderProps;
  @Input() label: string;
  @Input() item: any;
  @Input() key: string;

  $media: Media;

  constructor() { }

  ngOnInit(): void {
    if (this.props.format !== 'base64') {
      this.getDisplayableMedia();
    }
  }

  getDisplayableMedia() {
    if (this.key === '') {
      this.$media = this.item;
    }
    if (this.item[this.key]) {
      const [media] = this.item[this.key].splice(0, 1);
      this.$media = media;
    }
  }

}
