import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {AuthService} from "../../../services/api/auth.service";
import {Credentials} from "../../../types/auth.types";
import {AlertService} from "../../../modules/alert/alert.service";

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  public connectForm: FormGroup;

  constructor(
    private fb: FormBuilder,
    private auth: AuthService,
    private alertService: AlertService,
  ) {
    this.connectForm = this.fb.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.minLength(8)]],
      password_confirmation: ['', [Validators.required]],
    });
  }

  ngOnInit(): void {
    console.log('this.connectForm', this.connectForm.controls['email']);
  }


  onRegister() {

    this.connectForm.markAsTouched();

    console.log('this.connectForm', this.connectForm.controls['email']);

    if (this.connectForm.valid) {

      this.auth.register(<Credentials>{
        email: this.connectForm.controls['email'].value,
        password: this.connectForm.controls['password'].value,
        password_confirmation: this.connectForm.controls['password_confirmation'].value,
      }).subscribe(
        res => {
          console.log(res)
        },
        err => {
          this.alertService.danger(err.error.message);
        }
      );
    }
  }
}
