import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {ApiData} from "../../../../types/api.types";
import {Language} from "../../../../types/layout.types";
import {UrlHelperService} from "../../../../helpers/url-helper.service";

@Component({
  selector: 'entity-manage-preview-link',
  templateUrl: './preview-link.component.html',
  styleUrls: ['./preview-link.component.scss']
})
export class PreviewLinkComponent implements OnInit, OnDestroy {

  @Input() mediaUrl: string;
  @Input() item: ApiData;
  @Input() lang: Language;
  @Input() urlPattern: string | undefined;
  previewPath: string;

  constructor() { }

  ngOnInit(): void {
    this.previewPath = this.theUrlPattern(this.item);
  }

  theUrlPattern(item: ApiData) : string {
    UrlHelperService.mediaUrl = this.mediaUrl;
    return UrlHelperService.createUrlPattern(this.urlPattern, item);
  }


  ngOnDestroy(): void {}

}
