import {Field, FieldCondition, FormProps} from "../../types/route.type";
import {FetchControl} from "../../inc/fetch-control.class";
import {CodeeFileUploadComponent} from "../../ui/forms/fields/codee-file-upload/codee-file-upload.component";
import {CodeeInputComponent} from "../../ui/forms/fields/codee-input/codee-input.component";
import {CodeeSelectComponent} from "../../ui/forms/fields/codee-select/codee-select.component";
import {ApiData} from "../../types/api.types";
import {CodeeListItemsComponent} from "../../ui/forms/fields/codee-list-items/codee-list-items.component";
import {CodeeRichTextComponent} from "../../ui/forms/fields/codee-rich-text/codee-rich-text.component";
import {CodeeCheckboxComponent} from "../../ui/forms/fields/codee-checkbox/codee-checkbox.component";
import {CodeeChoicesComponent} from "../../ui/forms/fields/codee-choices/codee-choices.component";
import {CodeeSortingComponent} from "../../ui/forms/fields/codee-sorting/codee-sorting.component";
import {CodeeListHallComponent} from "../../ui/forms/fields/codee-list-hall/codee-list-hall.component";
import {CodeeDatepickerComponent} from "../../ui/forms/fields/codee-datepicker/codee-datepicker.component";
import {
  CodeeSelectMultipleComponent
} from "../../ui/forms/fields/codee-select-multiple/codee-select-multiple.component";

export class FieldGeneratorClass {

  /**
   *
   * @param key
   * @param col
   * @param label
   * @param description
   * @param defaultVal
   * @param type
   * @param apiEndpoint
   * @param hidden
   * @param multiple
   * @param editable
   * @param fileTypes
   */
  static _mediaImage({
                       key,
                       col = 6,
                       label = 'Image',
                       description,
                       defaultVal,
                       type,
                       apiEndpoint = "files/media",
                       hidden = false,
                       multiple = false,
                       editable = true,
                      fileTypes = 'image/png, image/svg, image/svg+xml, image/jpg, image/jpeg'}: Params): Field {
    return {
      key: `${key}`,
      label: label,
      description: description || '',
      hidden: hidden,
      col: col || 6,
      default: defaultVal || null,
      validators: [],
      formProps: {
        type: type || 'input',
        control: FetchControl.value,
        component: CodeeFileUploadComponent,
        editable: editable,
        apiEndpoint: apiEndpoint,
        multiple: multiple,
        fileTypes: fileTypes,
      },
      filterProps: undefined,
    }
  }


  /**
   *
   * @param key
   * @param col
   * @param label
   * @param description
   * @param defaultVal
   * @param type
   * @param apiEndpoint
   * @param hidden
   * @param multiple
   * @param editable
   * @param fileTypes
   * @private
   */
  static _mediaDocument({
                       key,
                       col = 6,
                       label = 'Image',
                       description,
                       defaultVal,
                       type,
                       apiEndpoint = "files/media",
                       hidden = false,
                       multiple = false,
                       editable = true,
                          fileTypes = 'application/pdf'}: Params): Field {
    return {
      key: `${key}`,
      label: label,
      description: description || '',
      hidden: hidden,
      col: col || 6,
      default: defaultVal || null,
      validators: [],
      formProps: {
        type: type || 'input',
        control: FetchControl.value,
        component: CodeeFileUploadComponent,
        editable: editable,
        apiEndpoint: apiEndpoint,
        multiple: multiple,
        fileTypes: fileTypes,
      },
      filterProps: undefined,
    }
  }


  /**
   *
   * @param key
   * @param col
   * @param label
   * @param description
   * @param defaultVal
   * @param type
   * @param translatable
   * @param hidden
   * @param filter
   * @param editable
   * @param conditions
   */
  static _text({key, col = 6, label = 'Text', description, defaultVal, type, translatable = false, hidden = false, filter = undefined, editable = true, conditions = []}: Params): Field {
    return {
      key: key,
      label: label,
      description: description || '',
      hidden: hidden,
      col: col,
      default: defaultVal,
      validators: [],
      translatable: translatable,
      formProps: {
        type: type || 'text',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: editable,
      },
      filterProps: filter,
      conditions: conditions,
    }
  }

  /**
   *
   * @param key
   * @param col
   * @param label
   * @param description
   * @param defaultVal
   * @param type
   * @param translatable
   * @param hidden
   * @param filter
   * @param editable
   */
  static _number({key, col = 6, label = 'Number', description, defaultVal, type, translatable, hidden = false, filter = undefined, editable = true}: Params): Field {
    return {
      key: key,
      label: label,
      description: description || '',
      hidden: hidden,
      col: col,
      default: defaultVal || null,
      validators: [],
      translatable: translatable || false,
      formProps: {
        type: type || 'text',
        control: FetchControl.value,
        component: CodeeInputComponent, // CodeeQuantityComponent,
        editable: editable,
      },
      filterProps: filter,
    }
  }

  /**
   *
   * @param key
   * @param col
   * @param label
   * @param description
   * @param defaultVal
   * @param type
   * @param translatable
   * @param hidden
   * @param filter
   * @param editable
   */
  static _datepicker({key, col = 6, label = 'Date', description, defaultVal, type, translatable, hidden = false, filter = undefined, editable = true}: Params): Field {
    return {
      key: key,
      label: label,
      description: description || '',
      hidden: hidden,
      col: col,
      default: defaultVal || null,
      validators: [],
      translatable: false,
      formProps: {
        type: type || 'date',
        control: FetchControl.value,
        component: CodeeDatepickerComponent, // CodeeQuantityComponent,
        editable: editable,
      },
      filterProps: filter,
    }
  }

  /**
   *
   * @param key
   * @param col
   * @param label
   * @param description
   * @param defaultVal
   * @param type
   * @param translatable
   * @param hidden
   * @param filter
   * @param editable
   */
  static _richText({key, col = 12, label = 'Medium Text', description, defaultVal, type, translatable, hidden = false, filter = undefined, editable = true}: Params): Field {
    return {
      key: key,
      label: label,
      description: description || '',
      hidden: hidden,
      col: col,
      default: defaultVal,
      validators: [],
      translatable: translatable,
      formProps: {
        type: type,
        control: FetchControl.value,
        component: CodeeRichTextComponent,
        editable: editable,
      },
      filterProps: filter,
    }
  }

  /**
   *
   * @param key
   * @param col
   * @param label
   * @param description
   * @param defaultVal
   * @param type
   * @param options
   * @param translatable
   * @param hidden
   * @param filter
   * @param editable
   */
  static _select({key, col = 6, label = 'Text', description, defaultVal, type = 'select', options = [], translatable, hidden = false, filter = undefined, editable = true}: Params): Field {
    return {
      key: key,
      label: label,
      hidden: false,
      col: col,
      default: defaultVal,
      validators: [],
      formProps: {
        type: type,
        canBeNull: true,
        control: FetchControl.value,
        controlKey: key,
        component: CodeeSelectComponent,
        data: options,
        editable: editable,
        mapSelectObjectFn: (item: ApiData) => {
          return {
            id: item.id,
            label: item.name,
          };
        }
      },
    }
  }

  /**
   *
   * @param key
   * @param col
   * @param label
   * @param defaultVal
   * @param description
   * @param type
   * @param apiEndpoint
   * @param editable
   * @param single
   */
  static _selectFromApi({key, col = 6, label = 'Text', defaultVal, description, type = 'select', apiEndpoint, editable = true, formPropsControlFn = FetchControl.multipleRelation}: Params): Field {

    const formPropsComponent = formPropsControlFn === FetchControl.multipleRelation ?
      CodeeSelectMultipleComponent : CodeeSelectComponent;

    return {
      key: key,
      label: label,
      description: description,
      hidden: false,
      col: col,
      default: defaultVal,
      validators: [],
      formProps: {
        type: type,
        control: formPropsControlFn,
        controlKey: 'id',
        component: formPropsComponent,
        dataEndpoint: apiEndpoint,
        editable: editable,
        mapSelectObjectFn: (item: ApiData) => {
          return {
            id: item.id,
            label: item.name,
            depth: item.depth ? item.depth as number : 0,
          };
        }
      },
    }
  }


  /**
   *
   * @param key
   * @param col
   * @param label
   * @param defaultVal
   * @param description
   * @param apiEndpoint
   * @param editable
   * @param filter
   * @private
   */
  static _choiceFromApi({key, col = 6, label = 'Text', defaultVal, description, apiEndpoint, editable = true, filter = undefined}: Params): Field {
    return {
      key: key,
      label: label,
      description: description,
      hidden: false,
      col: col,
      default: defaultVal,
      validators: [],
      formProps: {
        type: 'choices',
        control: FetchControl.mapIds,
        component: CodeeChoicesComponent,
        dataEndpoint: apiEndpoint,
        editable: editable,
      },
      filterProps: filter,
    }
  }

  /**
   *
   * @param key
   * @param col
   * @param label
   * @param translatable
   * @param description
   * @param defaultVal
   * @param editable
   */
  static _links({
                  key, col = 12,
                  label = 'Links',
                  translatable = false,
                  description,
                  defaultVal,
                  editable = true}: Params): Field {
    return {
      key: key,
      label: label,
      description: description,
      hidden: false,
      col: col,
      translatable: translatable,
      default: defaultVal,
      validators: [],
      formProps: {
        control: FetchControl.value,
        component: CodeeListItemsComponent,
        editable: editable,
      },
    }
  }


  static _halls({
                  key, col = 12,
                  label = 'Halls',
                  translatable = false,
                  description,
                  defaultVal,
                  updatable = false,
                  editable = true}: Params): Field {
    return {
      key: key,
      label: label,
      description: description,
      hidden: false,
      col: col,
      translatable: translatable,
      default: defaultVal,
      validators: [],
      formProps: {
        control: FetchControl.value,
        component: CodeeListHallComponent,
        editable: editable,
        isUpdatable: updatable,
      },
    }
  }


  /**
   *
   * @param key
   * @param col
   * @param label
   * @param description
   * @param defaultVal
   * @param hidden
   * @param filter
   * @param editable
   * @private
   */
  static _checkbox({key, col = 6, label = 'Text', description, defaultVal, hidden = false, filter = undefined, editable = true}: Params): Field {
    return {
      key: key,
      label: label,
      description: description,
      hidden: false,
      col: col,
      default: defaultVal,
      validators: [],
      formProps: {
        control: FetchControl.valueBoolean,
        component: CodeeCheckboxComponent,
        editable: editable,
      },
      filterProps: filter,
    }
  }


  /**
   *
   * @param key
   * @param col
   * @param label
   * @param description
   * @param defaultVal
   * @param hidden
   * @param filter
   * @param editable
   * @param apiEndpoint
   * @param relation
   * @param controlKey
   */
  static _sorting({key, col = 6, label = 'Text', description, defaultVal, hidden = false, filter = undefined, editable = true, apiEndpoint, relation, controlKey}: Params): Field {
    return {
      key: key,
      label: label,
      description: description,
      hidden: false,
      col: col,
      default: defaultVal,
      validators: [],
      formProps: {
        control: FetchControl.value,
        component: CodeeSortingComponent,
        editable: editable,
        apiEndpoint: apiEndpoint,
        relation: relation,
        controlKey: controlKey
      },
      filterProps: filter,
    }
  }


}



type Params = {
  key: string,
  col?: number,
  label?: string,
  description?: string,
  translatable?: boolean,
  hidden?: boolean,
  editable?: boolean,
  type?: string,
  apiEndpoint?: string,
  controlKey?: string,
  relation?: string,
  multiple?: boolean,
  updatable?: boolean,
  options?: {
    id: number | string,
    label: string
  }[],
  defaultVal?: string | null,
  filter?: FormProps | undefined,
  fileTypes?: string,
  formPropsControlFn?: typeof FetchControl.multipleRelation | typeof FetchControl.singleRelation | typeof FetchControl.value,
  conditions?: FieldCondition[],
}
