import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {ConnectComponent} from "./user/connect/connect.component";
import {UiModule} from "../ui/ui.module";
import {Page404Component} from './common/page404/page404.component';
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {RegisterComponent} from './user/register/register.component';
import {RouterModule} from "@angular/router";
import {HomeComponent} from './app/home/home.component';


import {ManageComponent as UserManageComponent} from './app/users/users/manage/manage.component';
import {ViewComponent as UserViewComponent} from './app/users/users/view/view.component';
import {FormComponent as UserFormComponent} from './app/users/users/form/form.component';
import {ManageComponent as RoleManageComponent} from './app/users/roles/manage/manage.component';
import {ViewComponent as RoleViewComponent} from './app/users/roles/view/view.component';
import {FormComponent as RoleFormComponent} from './app/users/roles/form/form.component';

import {MatSelectModule} from "@angular/material/select";

// Import from library
import {
  NgxAwesomePopupModule,
  ConfirmBoxConfigModule
} from '@costlydeveloper/ngx-awesome-popup';
import {HomeSettingsComponent} from './settings/index/home-settings/home-settings.component';
import {GlobalSettingsComponent} from './settings/index/global-settings/global-settings.component';
import {IndexComponent as QrCodesIndexComponent} from './qr-codes/index/index.component';
import {IndexComponent as SettingsIndexComponent} from './settings/index/index.component';
import {HeadquartersSettingsComponent} from "./settings/index/headquarters-settings/headquarters-settings.component";
import {EntityModule} from "./entities/entity.module";
import { CallbackComponent } from './app/callback/callback.component';
import {MatSlideToggleModule} from "@angular/material/slide-toggle";


@NgModule({
  declarations: [
    ConnectComponent,
    Page404Component,
    RegisterComponent,
    HomeComponent,

    UserManageComponent,
    UserViewComponent,
    UserFormComponent,
    RoleManageComponent,
    RoleViewComponent,
    RoleFormComponent,

    // Settings
    HomeSettingsComponent,
    GlobalSettingsComponent,
    HeadquartersSettingsComponent,
    QrCodesIndexComponent,
    SettingsIndexComponent,

    CallbackComponent,
  ],
  imports: [
    CommonModule,
    UiModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule,
    MatSelectModule,

    EntityModule,

    NgxAwesomePopupModule.forRoot(), // Essential, mandatory main module.
    ConfirmBoxConfigModule.forRoot(),
    MatSlideToggleModule,
    // Essential, mandatory confirm box module.

  ],
  exports: [
    ConnectComponent,
    Page404Component,
  ]
})
export class PagesModule {
}
