import {Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";

const route: Route = {
  endpoint: "common/clients",
  model: "CodeeHutCommon\\ClientRepository\\Models\\Client",
  texts: {
    key: 'clients',
    label: $localize`Client`,
    title: $localize`Clients`,
    backLabel: $localize`Back to Clients`,
    description: $localize`Below is the list of the created clients`,
  },
  fieldGroups: [{
    title: $localize`Details`,
    key: 'details',
    fields: [{
      key: 'first_name',
      label: $localize`First Name`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'text',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: true,
      },
    }]
  }],
  label: "",
  links: {
    create: null, // '/app/common/clients/create',
    update: null, // '/app/common/clients/update/',
    manage: '/app/common/clients/manage',
  },
  manage: {
    hasStatus: false,
    columns: [{
      title: 'Data',
      className: 'col-md-4',
      fields: [{
        label: 'ID',
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: 'First name',
        key: 'first_name',
        component: RenderTextComponent,
      }, {
        label: 'Last name',
        key: 'last_name',
        component: RenderTextComponent,
      },],
    }, {
      title: 'Data',
      className: 'col-md-4',
      fields: [{
        label: 'Email',
        key: 'email',
        component: RenderTextComponent,
      }, {
        label: 'Telephone',
        key: 'telephone',
        component: RenderTextComponent,
      }, {
        label: 'Address',
        key: 'address',
        component: RenderTextComponent,
      },],
    }, {
      title: '',
      className: 'col-md-4',
      fields: [{
        label: 'Region',
        key: 'region',
        component: RenderTextComponent,
      }, {
        label: 'City',
        key: 'city',
        component: RenderTextComponent,
      }, {
        label: 'Country',
        key: 'country',
        component: RenderTextComponent,
      }]
    }]
  },
};


export default route;
