import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'renderObjectValue'
})
export class RenderObjectValuePipe implements PipeTransform {

  transform(obj: {[key: string]: any}, startingWith: string): string {
    for (let k in obj) {
      if (k.startsWith(startingWith)) {
        return obj[k];
      }
    }
    return '-';
  }

}
