import { Injectable } from '@angular/core';
import {BehaviorSubject} from "rxjs";
import {Media} from "../../types/entities.types";
import {CodeeFormControlInterface} from "../../ui/forms/fields/codee-form-control.interface";
import {CodeeFileUploadComponent} from "../../ui/forms/fields/codee-file-upload/codee-file-upload.component";

@Injectable({
  providedIn: 'root'
})
export class MediaLibraryService {

  public active = new BehaviorSubject<boolean>(false);
  public props = new BehaviorSubject<MediaLibraryProps>({} as MediaLibraryProps);

  constructor() { }

  enable(props: MediaLibraryProps) {
    this.props.next(props);
    this.active.next(true);
  }

  disable() {
    this.props.next({} as MediaLibraryProps);
    this.active.next(false);
  }

  isActive() {
    return this.active.asObservable();
  }

}


type MediaLibraryProps = {
  endpoint: string,
  component: CodeeFileUploadComponent,
}
