import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor
} from '@angular/common/http';
import {Observable, throwError} from 'rxjs';
import {SessionService} from "../services/auth/session.service";
import {catchError} from "rxjs/operators";
import {environment} from "../../environments/environment";

@Injectable()
export class PassportInterceptor implements HttpInterceptor {

  constructor(
    private sessionService: SessionService,
  ) {}

  intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {

    const session = this.sessionService.getSession();

    // console.log('[PassportInterceptor]token', session)

    if (session.token) {

      request = request.clone({ headers: request.headers.set('Authorization', 'Bearer ' + session.token.access_token)})
    }

    if ( !request.headers.has('Content-Type')) {

      request = request.clone({ headers: request.headers.set('Content-Type', 'application/json')});
    }

    request = request.clone({ headers: request.headers.set('Accept', 'application/json')});
    request = request.clone({ headers: request.headers.set('X-Application', 'core')});
    request = request.clone({ headers: request.headers.set('X-Application-Env', environment.production ? 'prod' : 'stag')});
    request = request.clone({ headers: request.headers.set('X-Application-Domain', window.location.hostname)});


    return next.handle(request).pipe(
      catchError( err => {
        if (err.status === 401) {
          this.sessionService.removeSession();
        }
        const error = err.error;
        return throwError(error);
      })
    );
  }
}
