import { Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {FieldsCommonClass} from "../../common/fields-common.class";
import {CodeeDatepickerComponent} from "../../../ui/forms/fields/codee-datepicker/codee-datepicker.component";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";

const route: Route = {
  endpoint: "hotel/offers",
  translatable: true,
  model: "CodeeHut\\HotelManagement\\Models\\Offer",
  texts: {
    key: 'offers',
    label: $localize`Offer`,
    title: $localize`Offers`,
    backLabel: $localize`Back to Offers`,
    description: $localize`Below is the list of the created offers`,
  },
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_main',
        label: 'Main image',
        apiEndpoint: 'files/media'
      }),
    ]
  }, {
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldsCommonClass._id(6),
      FieldsCommonClass._name(6),
      FieldGeneratorClass._text({
        key: 'code',
        label: $localize`Code id`,
        description: $localize`The offer code of the booking provider. This helps the system show prices related to this offer.`,
        type: 'text',
        defaultVal: ''
      }),
      FieldGeneratorClass._richText({key: 'excerpt', translatable: true, label: 'Offer details'}),
    ]
  }, {
    title: $localize`Relations`,
    key: "relations",
    fields: [
      FieldsCommonClass._propertyRelation(),
    ],
  }, {
    title: $localize`Properties`,
    key: "date",
    fields: [{
      key: 'from',
      label: $localize`Valid From`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'input',
        control: FetchControl.value,
        component: CodeeDatepickerComponent,
        editable: true,
      },
    }, {
      key: 'to',
      label: $localize`Valid To`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'input',
        control: FetchControl.value,
        component: CodeeDatepickerComponent,
        editable: true,
      },
    },
      FieldGeneratorClass._text({key: 'min_nights', label: $localize`Minimum nights`, type: 'number', defaultVal: '1'}),
      FieldGeneratorClass._text({
        key: 'book_target', label: $localize`Target`, description: $localize`Customer target for Call to action`, /*
        conditions: [{
          field_key: 'code',
          compare: ConditionCompare.equals,
          value: '1',
        }]*/
      }),
    ]
  },],
  label: "",
  links: {
    create: '/app/hotel/offers/create',
    update: '/app/hotel/offers/update/',
    manage: '/app/hotel/offers/manage',
  },
  manage: {
    hasStatus: true,
    columns: [{
      title: $localize`Logo`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Excerpt`,
        key: 'excerpt',
        component: RenderTextComponent,
      }, {
        label: $localize`Property`,
        component: RenderRelationshipComponent,
        relation: 'properties',
        relationType: 'multiple',
        key: 'name',
      }],
    }, {
      title: $localize`Dates`,
      className: 'col',
      fields: [{
        label: $localize`Valid From`,
        key: 'from',
        component: RenderTextComponent,
      }, {
        label: $localize`Valid To`,
        key: 'to',
        component: RenderTextComponent,
      }]
    }]
  },

};


export default route;
