import {FilterFunction, Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";

const route: Route = {
  endpoint: "collection/portfolios",
  withRelations: ['media_gallery'],
  model: "CodeeHut\\Collections\\Models\\Portfolio",
  texts: {
    key: 'portfolio',
    label: $localize`Portfolio`,
    title: $localize`Portfolios`,
    backLabel: $localize`Back to Portfolios`,
    description: $localize`Below is the list of the created portfolios`,
  },
  fieldGroups: [{
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldGeneratorClass._text({ key: 'id', label: $localize`ID`, hidden: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        } }),
      FieldGeneratorClass._text({ key: 'name', label: $localize`Name`, translatable: true, description: $localize`The name of the portfolio project`, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        } }),
      FieldGeneratorClass._datepicker({ key: 'released_at', description: $localize`Select the release date (if exists)`, label: $localize`Released`}),
      FieldGeneratorClass._richText({ key: 'excerpt', translatable: true, description: $localize`A small description of the portfolio`, label: $localize`Portfolio excerpt`}),
      FieldGeneratorClass._richText({ key: 'content', translatable: true, description: $localize`More details about the item`, label: $localize`Portfolio content`}),
    ]
  }, {
    key: 'media',
    title: 'Media Files',
    fields: [
      FieldGeneratorClass._mediaImage({key: 'media_main', label: $localize`Main image`, description: $localize`The image thumbnail`, apiEndpoint: 'files/media'}),
      FieldGeneratorClass._mediaImage({key: 'media_gallery', label: $localize`Portfolio Gallery`, apiEndpoint: 'files/media', multiple: true}),
    ]
  }],
  label: "",
  links: {
    create: '/app/collection/portfolios/create',
    update: '/app/collection/portfolios/update/',
    manage: '/app/collection/portfolios/manage',
  },
  manage: {
    hasStatus: true,
    columns: [{
      title: $localize`Main image`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      },],
    }]
  },

};


export default route;
