import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {ApiService} from "../../../../../services/abstract/api.service";
import {Role} from "../../../../../types/entities.types";
import {ActivatedRoute} from "@angular/router";
import {HttpParams} from "@angular/common/http";

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss']
})
export class FormComponent implements OnInit {

  public entityForm: FormGroup;

  constructor(
    private fb: FormBuilder,
    private apiService: ApiService,
    private route: ActivatedRoute,
  ) {

    this.entityForm = this.fb.group({
      id: ['', []],
      name: ['', [Validators.required]],
      description: ['', []],
    });

  }

  ngOnInit(): void {

    const _id = this.route.snapshot.paramMap.get('id');
    if (_id !== null) {
      this.apiService.getEntity('roles', parseInt(_id), new HttpParams(), (res) => {
        this.entityForm.patchValue(res.data);
      })
    }

  }


  onSubmit() {
    if (this.entityForm.controls['id'].value) {
      this.apiService.updateEntity('roles', this.entityForm.controls['id'].value,
        this.entityForm.value,
        (res) => console.log(res));
    } else {
      this.apiService.addEntity('roles', this.entityForm.value, (res) => console.log(res));
    }
  }

}
