import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-translate-views-item',
  templateUrl: './translate-item.component.html',
  styleUrls: ['./translate-item.component.scss']
})
export class TranslateItemComponent implements OnInit {

  @Input() item: any;
  @Output() onClicked = new EventEmitter<any>();

  constructor() { }

  ngOnInit(): void {
  }

  translatableItemClicked(item: any) {
    this.onClicked.emit(item);
  }
}
