import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Language} from "../../../types/layout.types";
import {environment} from "../../../../environments/environment";
import {LanguageService} from "../../../services/abstract/language.service";
import AvailableLanguages from "../../../config/layout/available-languages";

@Component({
  selector: 'app-language-switcher',
  templateUrl: './language-switcher.component.html',
  styleUrls: ['./language-switcher.component.scss']
})
export class LanguageSwitcherComponent implements OnInit {

  public availableLanguages: Language[];
  public selectedLanguage: Language;

  @Input() showCopyFromEnglish: boolean = true;

  @Output() copyFromDefaultLang = new EventEmitter();
  @Output() onSelectLanguage = new EventEmitter<Language>();

  constructor(
    private languageService: LanguageService,
  ) {  }

  get defaultLocale(){ return environment.defaultLocale; }

  get copyFromDefaultLabel() {
    const defaultLang = this.availableLanguages.find(lang => lang.code === this.defaultLocale);
    if (defaultLang)
      return `Copy from ${defaultLang.label}`;
    else
      return '';
  }

  ngOnInit(): void {
    this.availableLanguages = AvailableLanguages;
    this.selectedLanguage = this.languageService.selectedLanguage$.getValue();
  }

  doCopyFromDefaultLag() {
    this.copyFromDefaultLang.emit();
  }

  /**
   *
   * @param $event
   */
  doSelectLanguage($event: any) {
    this.languageService.doSelectLanguage($event.target.value);
    // this.onSelectLanguage.emit(this.languageService.selectedLanguage$);
  }



}
