import {Component, forwardRef, Input, OnInit} from '@angular/core';
import {FormProps} from "../../../../types/route.type";
import {CodeeFormControlInterface} from "../codee-form-control.interface";
import {NG_VALUE_ACCESSOR} from "@angular/forms";
import * as moment from 'moment';
import {MomentDateAdapter, MAT_MOMENT_DATE_ADAPTER_OPTIONS} from '@angular/material-moment-adapter';
import {DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE} from "@angular/material/core";
export const MY_FORMATS = {
  parse: {
    dateInput: 'D MMM YYYY', //'YYYY-MM-DD',
  },
  display: {
    dateInput: 'YYYY-MM-DD', // 'D MMM YYYY',
    monthYearLabel: 'MMM YYYY',
    dateA11yLabel: 'YYYY-MM-DD',
    monthYearA11yLabel: 'MMM YYYY',
  },
};


@Component({
  selector: 'app-codee-datepicker',
  templateUrl: './codee-datepicker.component.html',
  styleUrls: ['./codee-datepicker.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CodeeDatepickerComponent),
      multi: true
    },
    {
      provide: DateAdapter,
      useClass: MomentDateAdapter,
      deps: [MAT_DATE_LOCALE, MAT_MOMENT_DATE_ADAPTER_OPTIONS],
    },

    {provide: MAT_DATE_FORMATS, useValue: MY_FORMATS},

  ]
})
export class CodeeDatepickerComponent implements OnInit, CodeeFormControlInterface {

  @Input() label: string;
  @Input() mode: string;
  @Input() value: number[] = [];
  @Input() props: FormProps;

  inputModel: string;

  constructor() { }

  onChange = (value: string) => {};

  onTouched = () => {};

  ngOnInit() {
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  writeValue(value: string) { // console.log('CodeeDatepickerComponent@writeValue', value);
    this.inputModel = moment(value).add(1,'days').format('YYYY-MM-DD');
  }

  dateChanged(date: HTMLInputElement) {
    this.onChange(date.value);
  }

}
