import {Route} from "../../../types/route.type";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {FieldsCommonClass} from "../../common/fields-common.class";
import {FieldGeneratorClass} from "../../common/field-generator.class";

const route: Route = {
  endpoint: "hotel/destinations",
  translatable: true,
  model: "CodeeHut\\HotelManagement\\Models\\Destination",
  texts: {
    key: 'destinations',
    label: $localize`Destination`,
    title: $localize`Destinations`,
    backLabel: $localize`Back to Destinations`,
    description: $localize`Below is the list of the created destinations`,
  },
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_main',
        label: 'Main image',
        description: 'The image thumbnail',
        apiEndpoint: 'files/media'
      }),
    ]
  }, {
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldsCommonClass._id(6),
      FieldsCommonClass._name(6),
    ]
  }, {
    title: $localize`Content`,
    key: 'content',
    fields: [
      FieldsCommonClass._excerpt(),
      FieldsCommonClass._content(),
    ]
  }],
  label: "",
  links: {
    create: '/app/hotel/destinations/create',
    update: '/app/hotel/destinations/update/',
    manage: '/app/hotel/destinations/manage',
  },
  layoutBlock: {
    id: 'destination_id',
    relation_key: 'destinations',
  },
  manage: {
    hasStatus: true,
    columns: [{
      title: $localize`Main image`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: 'ID',
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Excerpt`,
        key: 'excerpt',
        component: RenderTextComponent,
      }],
    }]
  }

};


export default route;
