import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {Field, Route} from "../../../../types/route.type";
import {Language, Metadata, TranslateState} from "../../../../types/layout.types";
import {ApiService} from "../../../../services/abstract/api.service";
import destination from "../../../../config/routes/hotel/destination";
import property from "../../../../config/routes/hotel/property";
import room from "../../../../config/routes/hotel/room";
import restaurant from "../../../../config/routes/hotel/restaurant";
import experience from "../../../../config/routes/hotel/experience";
import event from "../../../../config/routes/hotel/event";
import wellness from "../../../../config/routes/hotel/wellness";
import page from "../../../../config/routes/main/page";
import offer from "../../../../config/routes/hotel/offer";
import notification from "../../../../config/routes/hotel/notification";
import awards from "../../../../config/routes/hotel/awards";
import conferences from "../../../../config/routes/hotel/conferences";
import AvailableLanguages from "../../../../config/layout/available-languages";
import {HttpParams} from "@angular/common/http";
import {ResponseData} from "../../../../types/api.types";
import {environment} from "../../../../../environments/environment";

@Component({
  selector: 'app-translate-data',
  templateUrl: './translate-data.component.html',
  styleUrls: ['./translate-data.component.scss']
})
export class TranslateDataComponent implements OnInit {

  public entityForm: FormGroup = new FormBuilder().group({});

  public translateForm: FormGroup = new FormBuilder().group({});
  public translatableForm: FormGroup = new FormBuilder().group({});
  public isTranslateActive: boolean = false;

  public translatableFields: Field[] = [];
  public items: ResponseData[] = [];
  public metadata: Metadata;
  public page: number = 1;

  public availableLanguages: Language[];
  public state: TranslateState = {} as TranslateState;
  protected translatableItem: ResponseData;

  protected endpoint: string;

  protected config: {
    [key: string ]: Route
  };

  constructor(
    private fb: FormBuilder,
    private apiService: ApiService,
  ) {

    this.config = {
      destinations: destination,
      properties: property,
      rooms: room,
      restaurants: restaurant,
      experiences: experience,
      events: event,
      wellnesses: wellness,
      pages: page,
      offers: offer,
      notifications: notification,
      awards: awards,
      conferences: conferences,
      // 'layout-blocks': layout_blocks,
    };
  }

  ngOnInit(): void {

    this.availableLanguages = AvailableLanguages; // console.log(this.availableLanguages);

    // Setup state (selectedLang and more)
    this.loadConfiguration();

    this.entityForm = this.fb.group({
      entity: new FormControl('', Validators.required),
    })
  }

  get translatableLanguages() {
    return this.availableLanguages.filter( lang => lang.code !== environment.defaultLocale);
  }

  onEntitySelected(event: any) {
    this.endpoint = event.target.value;
    this.getTranslatableFields();
    this.getEntities(1);
  }

  getEntities(page: number = 1) { // console.log(page);
    if (page)
      this.page = page;

    let params = new HttpParams();
    params = params.append('translations', 'true');
    params = params.append('resultsPerPage', '12');
    params = params.append('page', this.page.toString());
    this.apiService.getEntities('hotel/' + this.endpoint, params, (res: any) => {
      this.items = res.data;
      this.metadata = res.meta;
    });
  }

  /**
   * Get the fields where translatable property is true
   */
  getTranslatableFields() {
    // Create an array with the fields that contain filterProps
    this.translatableFields = this.config[this.endpoint].fieldGroups
      .map(fieldGroup => fieldGroup.fields)
      .reduce((appender, state) => appender.concat(state), [])
      .filter((field) => field.translatable === true);

    this.addControlsToForms();
  }

  /**
   * Add controls to both forms
   */
  addControlsToForms() {
    // Configure filters controls
    this.translatableFields.forEach(field => {
      this.translateForm.addControl(field.key, new FormControl('', []));
      this.translatableForm.addControl(field.key, new FormControl('', []));
    });
  }

  onTranslate(closeModal: boolean = true){
    if (typeof this.translatableItem.id === 'number'){
      this.apiService.updateEntity('hotel/' + this.endpoint,
        this.translatableItem.id,
        {
          id: this.translatableItem.id,
          lang: this.state.selectedLang.code,
          ...this.translatableForm.value
        }, (res: any) => {
          if (closeModal) {
            this.isTranslateActive = false;
          }
          this.getEntities(this.page);
        });
    }
  }

  copyFromEnglish() {
    this.translatableForm.markAsDirty();
    this.translatableForm.patchValue(this.translateForm.value);
  }

  enableEntityTranslation(item: ResponseData) {
    this.translatableItem = item;
    this.isTranslateActive = true;
    this.fillTranslateForm()
    this.fillTranslatableForm()
  }


  pageChanged(page: number = 1) {
    this.getEntities(page);
  }

  closeTranslatableModal(){
    this.isTranslateActive = false;
  }

  loadConfiguration() {
    const session = sessionStorage.getItem('_transState');
    if (session) {
      this.state = JSON.parse(session) as TranslateState;
    } else {
      const [defaultLang] = AvailableLanguages.filter( lang => lang.code === environment.defaultLocale);
      this.state = {
        selectedLang: defaultLang,
      }
      this.saveConfiguration();
    }
  }

  saveConfiguration() {
    sessionStorage.setItem('_transState', JSON.stringify(this.state))
  }

  selectLanguageToTranslate(lang: Language) {
    // Before changing language we save the translations
    if (!this.translatableForm.pristine) {
      this.onTranslate(false);
    }
    // Now we actually change language
    this.state.selectedLang = lang;
    this.saveConfiguration();
    this.fillTranslatableForm();
  }

  fillTranslateForm() {
    this.translatableFields.forEach( field => {
      this.translateForm.controls[field.key].setValue(this.translatableItem[field.key]);
    })
  }

  fillTranslatableForm() {
    this.translatableForm.reset();
    // check if lang translation already exists
    const [translatedEntity] = this.translatableItem.translations.filter( item => {
      return item.locale === this.state.selectedLang.code;
    });
    if (translatedEntity){
      this.translatableFields.forEach( field => {
        this.translatableForm.controls[field.key].setValue(translatedEntity[field.key]);
      })
    }
  }

  getTranslatableLabel(item: any) {

    let label = item.name

    if (item.properties) {
      const propertiesLabel = item.properties.map( (property: any) => property.name).join(',');

      label += ` (${propertiesLabel})`;
    }

    return label;
  }
}


