import {Component, Input, OnInit} from '@angular/core';
import {CALLBACK_STATUS_ENUM} from "../../../enums/callbacks.enum";

@Component({
  selector: 'app-status-switcher',
  templateUrl: './status-switcher.component.html',
  styleUrls: ['./status-switcher.component.scss']
})
export class StatusSwitcherComponent implements OnInit {

  @Input() status: number;

  constructor() { }

  ngOnInit(): void {
  }

  get statusText() { console.log('statusText', this.status);
    switch (this.status) {
      case CALLBACK_STATUS_ENUM.New:
        return 'New';
      case CALLBACK_STATUS_ENUM.Pending:
        return 'Pending';
      case CALLBACK_STATUS_ENUM.Responded:
        return 'Responded';
      case CALLBACK_STATUS_ENUM.Completed:
        return 'Completed';
      case CALLBACK_STATUS_ENUM.Scheduled:
        return 'Scheduled';
      case CALLBACK_STATUS_ENUM.Rejected:
        return 'Rejected';
      case CALLBACK_STATUS_ENUM.Canceled:
        return 'Canceled';
      default:
        return 'Unknown';
    }
  }

}
