import {Component, forwardRef, Input, OnChanges, OnInit, SimpleChanges, ViewChild} from '@angular/core';
import {CodeeFormControlInterface} from "../codee-form-control.interface";
import {FormProps} from "../../../../types/route.type";
import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import {CKEditorComponent} from "@ckeditor/ckeditor5-angular";
import {NG_VALUE_ACCESSOR} from "@angular/forms";

@Component({
  selector: 'app-codee-rich-text',
  templateUrl: './codee-rich-text.component.html',
  styleUrls: ['./codee-rich-text.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CodeeRichTextComponent),
      multi: true
    }
  ]

})
export class CodeeRichTextComponent implements OnInit, OnChanges, CodeeFormControlInterface {

  public Editor = ClassicEditor;
  public editorConfig = {
    toolbar: [
      'heading', '|', 'bold', 'italic', '|', 'link', 'bulletedList', 'numberedList', '|', 'insertTable'
    ]
  }
  @ViewChild('editor') editorComponent: CKEditorComponent;


  @Input() label: string;
  @Input() value: string;
  @Input() mode: string;
  @Input() data: any;
  @Input() props: FormProps;

  inputModel: string;

  onChange = (value: string) => {
  };

  onTouched = () => {
  };

  ngOnInit() {
  }

  public getEditor() {
    // Warning: This may return "undefined" if the editor is hidden behind the `*ngIf` directive or
    // if the editor is not fully initialised yet.
    return this.editorComponent.editorInstance;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  writeValue(value: string) {
    this.inputModel = value;
  }

  ngOnChanges(changes: SimpleChanges): void {
    console.log('changes', changes);
  }
}
