import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {ConnectComponent} from "./pages/user/connect/connect.component";
import {Page404Component} from "./pages/common/page404/page404.component";
import {RegisterComponent} from "./pages/user/register/register.component";
import {AuthGuard} from "./guards/auth.guard";
import {HomeComponent} from "./pages/app/home/home.component";

import {ManageComponent as EntityManageComponent} from './pages/entities/manage/manage.component';
import {ViewComponent as EntityViewComponent} from './pages/entities/view/view.component';
import {FormComponent as EntityFormComponent} from './pages/entities/form/form.component';
import {TranslateComponent} from "./pages/entities/translate/translate.component";
import {UploadMediaComponent} from './pages/entities/upload-media/upload-media.component';

import {AppLayoutComponent} from "./layout/app-layout/app-layout.component";
import {LevelGuard} from "./guards/level.guard";
import {RouteHandler} from "./inc/route-handler.class";

import Tenant from "./config/routes/tenancy/tenant";
import Package from "./config/routes/tenancy/package";
import User from "./config/routes/user/users";
import Role from "./config/routes/user/roles";

import Application from "./config/routes/main/application";
import Page from "./config/routes/main/page";
import LayoutBlockType from "./config/routes/main/layout-block-type";

import Property from "./config/routes/hotel/property";
import Destination from "./config/routes/hotel/destination";
import Room from "./config/routes/hotel/room";
import Restaurant from "./config/routes/hotel/restaurant";
import Experience from "./config/routes/hotel/experience";
import Event from "./config/routes/hotel/event";
import Conference from "./config/routes/hotel/conferences";
import Award from "./config/routes/hotel/awards";
import Wellness from "./config/routes/hotel/wellness";
import Offer from "./config/routes/hotel/offer";
import Notification from "./config/routes/hotel/notification";

import Story from "./config/routes/knowledge-base/story";
import Category from "./config/routes/knowledge-base/category";

import Layout from "./config/routes/digital-signage/layout";
import Box from "./config/routes/digital-signage/box";

import Business from "./config/routes/business-listing/business";
import Portfolio from "./config/routes/collection/portfolio";

import Client from "./config/routes/support/client";
import Callback from "./config/routes/support/callback";

import Media from "./config/routes/files/media";


import {CallbackComponent} from "./pages/app/callback/callback.component";

import {IndexComponent as SettingsIndexComponent} from "./pages/settings/index/index.component";
import {IndexComponent as QrCodesIndexComponent} from "./pages/qr-codes/index/index.component";

const routes: Routes = [
  {path: '', redirectTo: '/connect', pathMatch: 'full'},

  // Unauthorized
  {path: 'connect', component: ConnectComponent},
  {path: 'register', component: RegisterComponent},

  // Authorized
  {
    path: 'app', component: AppLayoutComponent, canActivate: [AuthGuard], children: [
      {path: 'upload-media', component: UploadMediaComponent},
      {
        path: 'tenancy', canActivateChild: [LevelGuard], data: {level: 10}, children: [
          {
            path: 'tenants', data: {handler: RouteHandler.create<typeof Tenant>(Tenant)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'packages', data: {handler: RouteHandler.create<typeof Package>(Package)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
        ]
      },
      // Users management
      {
        path: 'user', canActivateChild: [LevelGuard], data: {level: 10}, children: [
          {
            path: 'users', data: {handler: RouteHandler.create<typeof User>(User)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'roles', data: {handler: RouteHandler.create<typeof Role>(Role)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
        ]
      },
      /**
       * Main Repository
       *
       */
      {path: 'main', component: HomeComponent},
      {
        path: 'main', canActivateChild: [LevelGuard], data: {level: 5}, children: [
          {
            path: 'applications', data: {handler: RouteHandler.create<typeof Application>(Application)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'pages', data: {handler: RouteHandler.create<typeof Page>(Page)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'layout-block-types',
            data: {handler: RouteHandler.create<typeof LayoutBlockType>(LayoutBlockType)},
            children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
        ]
      },
      /**
       * Hotel management
       *
       */
      {
        path: 'hotel', canActivateChild: [LevelGuard], data: {level: 5}, children: [
          {
            path: 'properties', data: {handler: RouteHandler.create<typeof Property>(Property)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'destinations', data: {handler: RouteHandler.create<typeof Destination>(Destination)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'rooms', data: {handler: RouteHandler.create<typeof Room>(Room)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'restaurants', data: {handler: RouteHandler.create<typeof Restaurant>(Restaurant)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'experiences', data: {handler: RouteHandler.create<typeof Experience>(Experience)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'events', data: {handler: RouteHandler.create<typeof Event>(Event)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'conferences', data: {handler: RouteHandler.create<typeof Conference>(Conference)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'wellness', data: {handler: RouteHandler.create<typeof Wellness>(Wellness)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'offers', data: {handler: RouteHandler.create<typeof Offer>(Offer)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'notifications', data: {handler: RouteHandler.create<typeof Notification>(Notification)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'awards', data: {handler: RouteHandler.create<typeof Award>(Award)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {path: 'translate/index', component: TranslateComponent},
        ]
      },
      /**
       * Knowledge base
       *
       */
      {
        path: 'knowledge-base', canActivateChild: [LevelGuard], data: {level: 5}, children: [
          {
            path: 'stories', data: {handler: RouteHandler.create<typeof Story>(Story)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'categories', data: {handler: RouteHandler.create<typeof Category>(Category)}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
        ]
      },
      /**
       * Digital Signage
       *
       */
      {
        path: 'digital-signage', canActivateChild: [LevelGuard], data: {level: 5}, children: [
          {
            path: 'layouts', data: {handler: RouteHandler.create<typeof Layout>(Layout, 'digital-signage')}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'boxes', data: {handler: RouteHandler.create<typeof Box>(Box, 'digital-signage')}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
        ]
      },
      /**
       * Digital Signage
       *
       */
      {
        path: 'collection', canActivateChild: [LevelGuard], data: {level: 5}, children: [
          {
            path: 'portfolios',
            data: {handler: RouteHandler.create<typeof Portfolio>(Portfolio, 'collection')},
            children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
        ]
      },
      /**
       * Business Listing
       *
       */
      { path: 'business-listing', canActivateChild: [LevelGuard], data: { level: 5 }, children: [
          { path: 'businesses', data: { handler: RouteHandler.create<typeof Business>(Business, 'business-listing') }, children: [
              { path: 'manage', component: EntityManageComponent },
              { path: 'create', component: EntityFormComponent, data: { mode: 'create'}},
              { path: 'update/:id', component: EntityFormComponent, data: { mode: 'update'} },
              { path: 'view/:id', component: EntityViewComponent },
            ]},
        ]},
      /**
       * Common Support
       *
       */
      {
        path: 'common', canActivateChild: [LevelGuard], data: {level: 5}, children: [
          {
            path: 'clients', data: {handler: RouteHandler.create<typeof Client>(Client, 'common')}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
          {
            path: 'callbacks', data: {handler: RouteHandler.create<typeof Callback>(Callback, 'common')}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: CallbackComponent} //EntityViewComponent },
            ]
          },
        ]
      },
      {
        path: 'files', canActivateChild: [LevelGuard], data: {level: 5}, children: [
          {
            path: 'media', data: {handler: RouteHandler.create<typeof Media>(Media, 'files')}, children: [
              {path: 'manage', component: EntityManageComponent},
              {path: 'create', component: EntityFormComponent, data: {mode: 'create'}},
              {path: 'update/:id', component: EntityFormComponent, data: {mode: 'update'}},
              {path: 'view/:id', component: EntityViewComponent},
            ]
          },
        ]
      },
      {
        path: 'settings', canActivateChild: [LevelGuard], data: {level: 5}, children: [
          {path: 'settings/index', component: SettingsIndexComponent},
          {path: 'qr-code/index', component: QrCodesIndexComponent},
        ]
      },
      // { path: 'user', children: [
      //     { path: 'users', children: [
      //         { path: 'manage', component: UserManageComponent },
      //         { path: 'create', component: UserFormComponent },
      //         { path: 'update/:id', component: UserFormComponent },
      //         { path: 'view/:id', component: UserViewComponent },
      //     ]},
      //     { path: 'roles', children: [
      //         { path: 'manage', component: RoleManageComponent },
      //         { path: 'create', component: RoleFormComponent },
      //         { path: 'update/:id', component: RoleFormComponent },
      //         { path: 'view/:id', component: RoleViewComponent },
      //     ]},
      //   ]},
      {path: '**', component: Page404Component},  // Wildcard route for a 404 page
    ]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
