import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {AuthService} from "../../../services/api/auth.service";
import {AuthResponse, Credentials} from "../../../types/auth.types";
import {AlertService} from "../../../modules/alert/alert.service";
import {SessionService} from "../../../services/auth/session.service";
import {Router} from "@angular/router";
import {User} from "../../../types/entities.types";


@Component({
  selector: 'app-connect',
  templateUrl: './connect.component.html',
  styleUrls: ['./connect.component.scss']
})
export class ConnectComponent implements OnInit {

  public connectForm: FormGroup;

  public isConnecting = false;

  constructor(
    private fb: FormBuilder,
    private auth: AuthService,
    private alertService: AlertService,
    private sessionService: SessionService,
    private router: Router,
  ) {

    this.connectForm = this.fb.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required]],
    });

  }

  ngOnInit(): void {

  }

  onConnect() {
    this.isConnecting = true;
    this.auth.connect(<Credentials>{
      email: this.connectForm.controls['email'].value,
      password: this.connectForm.controls['password'].value,
    }).subscribe(
      // @ts-ignore
      (response: AuthResponse) => {
        // this.alertService.appendSuccessAlert(response.message);
        this.alertService.success(response.message);
        this.sessionService.saveAccessToken({
          token: response.data,
          user: response.user as User,
        });
        // Push tenants to sessionService
        if (response.user.tenants)
          this.sessionService.doSetTenants(response.user.tenants);

        // Everything is OK.
        // todo add a subscription to wait until tenants are set
        this.router.navigate(['/app/main']);
        this.isConnecting = false;
      },
      err => { // console.log('err', err);
        if (typeof err === 'string') {
          this.alertService.danger(err)
        } else {
          // console.log('err', err);
          this.alertService.danger(err.message || 'An error occurred. Please contact administrator.') // @todo Improve error display
        }
        this.isConnecting = false;
      },
    );
  }

}
