import { Pipe, PipeTransform } from '@angular/core';
import {ApiData} from "../types/api.types";
import {Route} from "../types/route.type";
import {UrlHelperService} from "../helpers/url-helper.service";

@Pipe({
  name: 'urlPattern'
})
export class UrlPatternPipe implements PipeTransform {

  transform(item: ApiData, route: Route): string {

    return UrlHelperService.createUrlPattern(route?.manage?.urlPattern, item);
  }

}
