import {FilterFunction, Route} from "../../../types/route.type";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {CodeeSelectComponent} from "../../../ui/forms/fields/codee-select/codee-select.component";
import {ApiData} from "../../../types/api.types";

const route: Route = {
  endpoint: "main/pages",
  translatable: true,
  model: "CodeeHutCommon\\MainRepository\\Models\\Page",
  texts: {
    key: 'pages',
    label: $localize`Page`,
    title: $localize`Pages`,
    backLabel: $localize`Back to Pages`,
    description: $localize`Below is the list of the created pages`,
  },
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({key: 'media_main', label: $localize`Main image`, apiEndpoint: 'files/media'}),
      // FieldsCommonClass._mediaMain()
      ]
  }, {
    title: $localize`Title`,
    key: 'title',
    fields: [
      FieldGeneratorClass._text({ key: 'id', label: $localize`ID`, hidden: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        } }),
      FieldGeneratorClass._text({ key: 'name', label: $localize`Name`, translatable: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        } }),
      // FieldsCommonClass._id(12),
      // FieldsCommonClass._name(12),
    ]
  }, {
    title: $localize`Content`,
    key: 'content',
    fields: [
      FieldGeneratorClass._richText({ key: 'excerpt', label: $localize`Excerpt`, translatable: true,}),
      FieldGeneratorClass._richText({ key: 'content', label: $localize`Content`, translatable: true,}),
    ]
  }, {
    title: $localize`Relations`,
    key: "relations",
    fields: [
      FieldGeneratorClass._choiceFromApi({key: 'applications', col: 12, label: $localize`Application`, apiEndpoint: 'main/applications', filter: {
          control: FetchControl.singleRelation,
          component: CodeeSelectComponent,
          dataEndpoint: 'main/applications?resultsPerPage=all&page=1',
          mapSelectObjectFn: (item: ApiData) => {
            return {
              id: item.id,
              label: item.name,
            };
          },
          filterable: FilterFunction.HasRelation,
          relation: 'applications',
        } }),

      // FieldsCommonClass._propertyRelation(),
    ],
  }],
  label: "",
  links: {
    create: '/app/main/pages/create',
    update: '/app/main/pages/update/',
    manage: '/app/main/pages/manage',
  },
  layoutBlock: {
    id: 'page_id',
    relation_key: 'pages',
  },
  manage: {
    hasStatus: true,
    urlPattern: '/p/:slug',
    columns: [{
      title: $localize`Logo`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Excerpt`,
        key: 'excerpt',
        component: RenderTextComponent,
      }, {
        label: $localize`Property`,
        component: RenderRelationshipComponent,
        relation: 'properties',
        relationType: 'multiple',
        key: 'name',
      }],
    }]
  },

};


export default route;
