import {FilterFunction, Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {RenderMapObjectComponent} from "../../../ui/render/fields/render-map-object/render-map-object.component";
import {FieldsCommonClass} from "../../common/fields-common.class";
import {CodeeFileUploadComponent} from "../../../ui/forms/fields/codee-file-upload/codee-file-upload.component";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {CodeeListItemsComponent} from "../../../ui/forms/fields/codee-list-items/codee-list-items.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";

const route: Route = {
  endpoint: "digital-signage/layouts",
  withRelations: ['media_gallery', 'social_profiles'],
  model: "CodeeHut\\DigitalSignage\\Models\\Layout",
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({key: 'media_logo', label: 'Logo', apiEndpoint: 'files/media'}),
      // FieldsCommonClass._mediaLogo(6, false, 'digital-signage'),
      {
        key: 'media_background',
        label: $localize`Background`,
        hidden: false,
        col: 6,
        default: null,
        validators: [], // [Validators.required],
        formProps: {
          type: 'input',
          control: FetchControl.value,
          component: CodeeFileUploadComponent,
          editable: true,
          apiEndpoint: "files/media",
          fileTypes: 'image/png, image/jpeg, image/jpg',
        },
        renderProps: {
          // control: RenderControl.string, // Used in: manage app-codee-render-outlet
          component: RenderImageComponent,
          hidden: true,
        },
      }
      // FieldsCommonClass._excerpt(12),
    ]
  }, {
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldsCommonClass._id(6),
      FieldGeneratorClass._text({ key: 'name', label: 'Name', filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.Equals // todo Why FilterFunction.Like does not work
        } }),
    ]
  }, {
    title: $localize`Social Links`,
    key: 'social-links',
    fields: [{
      key: 'social_profiles',
      label: $localize`Social Profiles`,
      hidden: false,
      col: 12,
      default: '',
      validators: [],
      formProps: {
        control: FetchControl.value,
        component: CodeeListItemsComponent,
        editable: true,
      },
      renderProps: {
        component: RenderMapObjectComponent,
        key: 'type',
      },
    }]
  }, {
    title: $localize`Gallery`,
    key: 'gallery',
    fields: [
      FieldGeneratorClass._mediaImage({key: 'media_gallery', label: 'Gallery images', apiEndpoint: 'files/media', col: 12, multiple: true}),
      // FieldsCommonClass._mediaGallery(12, false, 'digital-signage'),
    ]
  }],
  label: "",
  links: {
    create: '/app/digital-signage/layouts/create',
    update: '/app/digital-signage/layouts/update/',
    manage: '/app/digital-signage/layouts/manage',
  },
  texts: {
    key: 'layouts',
    label: $localize`Layout`,
    title: $localize`Layouts`,
    backLabel: $localize`Back to Layouts`,
    description: $localize`Below is the list of the created layouts`,
  },
  manage: {
    hasStatus: true,
    columns: [{
      title: 'Logo',
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    },{
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Social`,
        component: RenderRelationshipComponent,
        relation: 'social_profiles',
        relationType: 'multiple',
        key: 'label',
      }],
    }]
  },

};


export default route;
