import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {FormBuilder, FormControl, FormGroup} from "@angular/forms";
import {Route} from "../../../../types/route.type";
import {ApiService} from "../../../../services/abstract/api.service";
import {AlertService} from "../../../../modules/alert/alert.service";
import {ActivatedRoute, Router} from "@angular/router";
import {ApiError, ApiResponse} from "../../../../types/api.types";
import {Language} from "../../../../types/layout.types";
import {environment} from "../../../../../environments/environment";
import {Subscription} from "rxjs";
import {LanguageService} from "../../../../services/abstract/language.service";

@Component({
  selector: 'entity-form-settings',
  templateUrl: './form-settings.component.html',
  styleUrls: ['./form-settings.component.scss']
})
export class FormSettingsComponent implements OnInit, OnDestroy {

  // @Input() $route: Route;
  @Input() mode: string;
  @Input() entityToUpdate: any;
  @Input() groupName: 'settingGroups' | 'mediaGroups' = 'settingGroups';

  public entityForm: FormGroup = new FormBuilder().group({});

  public $route: Route;
  public $errors: Error;

  public selectedLanguage: Language;
  protected langSubscription: Subscription;

  constructor(
    private fb: FormBuilder,
    private apiService: ApiService,
    private alertService: AlertService,
    private router: Router,
    private route: ActivatedRoute,
    private languageService: LanguageService,
  ) {
  }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.$route = data.handler.route;

      this.langSubscription = this.languageService.selectedLanguage$.subscribe( lang => { console.log('subscribe lang', lang.code);
        this.selectedLanguage = lang;
        this.onSelectLanguage()
      })
      this.addFormControls();
    });
  }

  ngOnDestroy() {
    this.langSubscription.unsubscribe();
  }

  addFormControls() {
    /**
     * Add Form controls
     */
    const {settings} = this.entityToUpdate;
    // console.log('settings', this.$route[this.groupName])
    const $routeElement = this.$route[this.groupName];
    if ($routeElement) {
      for (let fieldGroup of $routeElement) {
        for (let field of fieldGroup.fields) {
          // Check if there is submitted value for the formcontrol
          const [setting] = settings.filter((s: any) => s.key === field.key);
          let value = null;
          if (setting) {
            value = field.translatable ?
              setting.localized_value || setting.value :
              setting.value;
          }
          this.entityForm.addControl(field.key, new FormControl(value, field.validators));

          // const fieldControl = this.entityForm.get(field.key);
          // console.log('setting',fieldControl, value);
          // if (fieldControl)
          //     fieldControl.patchValue(value);
        }
      }
    }
  }

  onSubmit() {
    this.entityForm.markAsTouched();
    if (this.entityForm.valid) {
      this.submitForm();
    } else {
      this.alertService.warning('Please fix validation errors');
    }
  }

  updateAndStay() {
    this.entityForm.markAsTouched();
    // console.log(this.entityForm.value);
    if (this.entityForm.valid) {
      this.apiService.updateEntity(this.$route.endpoint, this.entityToUpdate.id, {  // todo Check if id exists
          ...this.entityForm.value,
          lang: this.selectedLanguage.code,
          id: this.entityToUpdate.id,
          type: 'settings',
        },
        (res: ApiResponse) => this.alertService.success(res.message),
        (err: ApiError) => this.afterErrorApiAction(err));
    } else {
      this.alertService.warning('Please fix validation errors');
    }
  }

  submitForm() {
    // console.log('this.entityForm', this.entityForm.value);
    this.apiService.updateEntity(this.$route.endpoint, this.entityToUpdate.id, {  // todo Check if id exists
        ...this.entityForm.value,
        lang: this.selectedLanguage.code,
        id: this.entityToUpdate.id,
        handler: 'settings',
      },
      (res: ApiResponse) => this.afterSuccessfulApiAction(res),
      (err: ApiError) => this.afterErrorApiAction(err));
  }

  afterSuccessfulApiAction(res: ApiResponse) {
    this.alertService.success(res.message);
    this.router.navigate([this.$route.links.manage]);
  }

  afterErrorApiAction(err: ApiError) {
    // @ts-ignore
    this.$errors = err.errors;
    this.alertService.danger(err.message);
  }


  getValidationErrorByKey(key: string): string | null {
    if (this.$errors)
      if (this.$errors[key])
        return this.$errors[key].join();
    return null;
  }


  /**
   *
   */
  onSelectLanguage() {
    const {settings} = this.entityToUpdate;
    // console.log('settings', this.$route[this.groupName])
    const $routeElement = this.$route[this.groupName];
    if ($routeElement) {
      for (let fieldGroup of $routeElement) {
        for (let field of fieldGroup.fields) {
          // Check if there is submitted value for the formcontrol
          const [setting] = settings.filter((s: any) => s.key === field.key);
          // If translatable is true then look first for submitted translation
          if (field.translatable) {

            const lang = this.selectedLanguage.code;
            const value = setting.translations.find((item: any) => item.locale === lang)
            const fieldS = this.entityForm.get(field.key);
            if (fieldS !== null) {
              if (value && value.localized_value) {
                fieldS.reset();
                fieldS.setValue(value.localized_value);
              } else {
                fieldS.reset();
              }
            }
          }
        }
      }
    }
  }

  copyValuesFromDefault() {

    const {settings} = this.entityToUpdate;
    // console.log('settings', this.$route[this.groupName])
    const $routeElement = this.$route[this.groupName];
    if ($routeElement) {
      for (let fieldGroup of $routeElement) {
        for (let field of fieldGroup.fields) {
          // Check if there is submitted value for the formcontrol
          const [setting] = settings.filter((s: any) => s.key === field.key);
          // If translatable is true then look first for submitted translation
          if (field.translatable) {
            const value = setting.translations.find((item: any) => item.locale === environment.defaultLocale)
            const fieldS = this.entityForm.get(field.key);
            if (fieldS !== null) {
              fieldS.reset();
              if (value && value.localized_value) {
                fieldS.setValue(value.localized_value);
              }
            }
          }
        }
      }
    }

    // const {settings} = this.entityToUpdate;
    //
    // const $routeElement = this.$route.menuGroups;
    // if ($routeElement) {
    //     for (let fieldGroup of $routeElement) {
    //         for (let field of fieldGroup.fields) {
    //             // Check if there is submitted value for the formcontrol
    //             const [setting] = settings.filter((s: any) => s.key === field.key);
    //             // If translatable is true then look first for submitted translation
    //             if (field.translatable) {
    //                 const value = setting.translations.find((item: any) => item.locale === 'en')
    //                 const fieldS = this.entityForm.get(field.key);
    //                 if (fieldS !== null) {
    //                     fieldS.reset();
    //                     fieldS.setValue(value.localized_value);
    //                 }
    //             }
    //         }
    //     }
    // }
  }

}

type Error = {
  [key: string]: string[];
}
