import {Component, forwardRef, Input, OnInit} from '@angular/core';
import {NG_VALUE_ACCESSOR} from "@angular/forms";
import {ApiService} from "../../../../services/abstract/api.service";
import {FormProps} from "../../../../types/route.type";
import {HttpParams} from "@angular/common/http";
import {CodeeFormControlInterface} from "../codee-form-control.interface";
import {ApiData} from "../../../../types/api.types";
import {ChoiceFormFieldData} from "../../../../types/layout.types";
import {SortManagementService} from "../../../../modules/sort-management/sort-management.service";

@Component({
  selector: 'app-codee-choices',
  templateUrl: './codee-choices.component.html',
  styleUrls: ['./codee-choices.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CodeeChoicesComponent),
      multi: true
    }
  ]

})
export class CodeeChoicesComponent implements OnInit, CodeeFormControlInterface {

  @Input() label: string;
  @Input() mode: string;
  @Input() value: number[] = [];
  @Input() props: FormProps;

  $data: ChoiceFormFieldData[] = [];

  constructor(
      private apiService: ApiService,
      // private sortManagementService: SortManagementService,
  ) {}

  ngOnInit() {
    if (this.props.dataEndpoint)
      this.apiService.getEntities(this.props.dataEndpoint, new HttpParams(),(res) => {
        this.$data = res.data.map((item: ApiData) => {
          return {
            id: item.id,
            label: item.name,
          }
        });
      })
    else if (this.props.data)
      this.$data = this.props.data;

  }

  onChange = (value: number[]) => {};

  onTouched = () => {};

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  setDisabledState(isDisabled: boolean): void {
  }

  writeValue(value: number[]): void {
    // console.log('writeValue', value)
    this.value = value || [];
  }

  toggleCheckbox(datum: ChoiceFormFieldData) {
    // console.log('datum', this.value, datum, this.value.indexOf(datum.id));
    // First check if the value must be included or excluded
    const searchIndex = this.value.indexOf(<number>datum.id);
    // console.log('this.value', this.value);
    if (searchIndex === -1)
      this.value.push(<number>datum.id);
    else
      this.value.splice(searchIndex, 1);
    // Change the value
    // console.log('this.value', this.value);
    this.onChange(this.value);
  }

  // todo Create a pipe for the functionality below
  isCheckboxActive(datum: ChoiceFormFieldData) {
    return this.value.indexOf(<number>datum.id) !== -1;
  }

  //
  // openSortManagement(id: number) {
  //   console.log('openSortManagement', id);
  //   this.sortManagementService.handle({
  //     active: true,
  //     reference_id: id,
  //   });
  // }


}


