import {
  Component,
  ComponentFactoryResolver,
  forwardRef,
  Injector, Input, OnInit,
  ViewContainerRef
} from "@angular/core";
import {NgControl, NG_VALUE_ACCESSOR} from "@angular/forms";

import {FormProps} from "../../../types/route.type";
import {CodeeFormControlInterface} from "./codee-form-control.interface";

@Component({
  selector: "app-codee-form-outlet",
  template: `
    <div class="text-danger">{{ validation }}</div>`,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CodeeFormOutletComponent),
      multi: true
    }
  ]
})
export class CodeeFormOutletComponent implements OnInit {

  @Input() referenceId: number;

  @Input() props: FormProps;
  @Input() label: string;
  @Input() description: string | undefined;
  @Input() mode: string;
  @Input() validation: string | null;

  constructor(
    public injector: Injector,
    private componentFactoryResolver: ComponentFactoryResolver,
    private viewContainerRef: ViewContainerRef
  ) {
  }

  public ngOnInit(): void {

    const ngControl = this.injector.get(NgControl);

    const componentRef = this.viewContainerRef.createComponent<CodeeFormControlInterface>(
      this.props.component
    );

    componentRef.instance.referenceId = this.referenceId;
    componentRef.instance.label = this.label;
    componentRef.instance.description = this.description;
    componentRef.instance.props = this.props;
    componentRef.instance.mode = this.mode;

    ngControl.valueAccessor = componentRef.instance;

  }

}
