import {Component, Input, OnInit} from '@angular/core';
import {FormArray, FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {LayoutBlocksService} from "../../../../services/ui/layout-blocks.service";
import {LayoutBlock, LayoutBlockType} from "../../../../types/entities.types";
import {Field, FieldGroup, FilterFunction} from "../../../../types/route.type";
import {RenderTextComponent} from "../../../../ui/render/fields/render-text/render-text.component";
import {Language, LayoutItem} from "../../../../types/layout.types";
import {ApiService} from "../../../../services/abstract/api.service";
import {ApiError, ApiResponse} from "../../../../types/api.types";
import { HttpParams} from "@angular/common/http";
import {AlertService} from "../../../../modules/alert/alert.service";
import {ActivatedRoute, Router} from "@angular/router";
import {Route} from "../../../../types/route.type";
import {ConfirmBoxInitializer, DialogLayoutDisplay} from "@costlydeveloper/ngx-awesome-popup";
import {LoadableService} from "../../../../services/abstract/loadable.service";
import {CdkDragDrop, moveItemInArray} from "@angular/cdk/drag-drop";
import {RequestService} from "../../../../services/abstract/request.service";
import {SelectLayoutBlockControl} from "../../../../helpers/select-layout-block-control";
import {environment} from "../../../../../environments/environment";
import {LanguageService} from "../../../../services/abstract/language.service";
import {Subscription} from "rxjs";

@Component({
  selector: 'entity-form-layout-blocks',
  templateUrl: './form-layout-blocks.component.html',
  styleUrls: ['./form-layout-blocks.component.scss']
})
export class FormLayoutBlocksComponent implements OnInit {

  @Input() $route: Route;
  @Input() mode: string;
  @Input() entityToAssign: any;

  selectedLayoutBlock: LayoutBlockType | null = null;

  public selectedLanguage: Language;
  protected langSubscription: Subscription;

  public layoutBlockForm: FormGroup; // = new FormBuilder().group({});
  public layoutBlockTypes: LayoutBlockType[] = [];
  public layoutBlocks: LayoutBlock[] = [];
  public selectedLayoutFieldGroups: FieldGroup[] = [];
  public loadingLayoutBlock: number = -1;

  constructor(
    private fb: FormBuilder,
    private layoutBlockService: LayoutBlocksService,
    private apiService: ApiService,
    private request: RequestService,
    private loadableService: LoadableService,
    private alertService: AlertService,
    private router: Router,
    private route: ActivatedRoute,
    private languageService: LanguageService,
  ) {
  }

  get layout_blocks_control(): FormArray {
    return <FormArray>this.layoutBlockForm.get('layout_blocks_control');
  }

  get defaultLocale(){ return environment.defaultLocale; }

  ngOnInit(): void {

    this.langSubscription = this.languageService.selectedLanguage$.subscribe( lang => {
      this.selectedLanguage = lang;
      this.onSelectLanguage(lang)
    })

    this.layoutBlockForm = this.fb.group({
      id: ['', []],
      morph_id: this.route.snapshot.paramMap.get('id'),
      model: this.$route.model,
      layout_blocks_control: this.fb.array([]),
    });

    this.layoutBlockService.getLayoutBlockTypes().then((layoutBlockTypes: LayoutBlockType[]) => {
      this.layoutBlockTypes = layoutBlockTypes;
      // this.fillValuesIfIsUpdateMode();
    });

    this.getLayoutBlocksFromApi(true);

  }

  ngOnDestroy() {
    this.langSubscription.unsubscribe();
  }

  /**
   *
   * @param lang
   */
  onSelectLanguage(lang: Language) {
    if (this.layoutBlockForm) {
      while (this.layout_blocks_control.length !== 0) {
        this.layout_blocks_control.removeAt(0)
      }
      this.fetchFormControls();
    }
  }

  /**
   *
   * @param $event
   */
  onSelectLayoutBlock($event: any) { console.log('onSelectLayoutBlock', $event.target.value);
    const [layoutBlockType] = this.layoutBlockTypes.filter(l => l.id === parseInt($event.target.value));
    if (layoutBlockType) {
      this.createLayoutBlock(layoutBlockType);
      this.selectedLayoutBlock = null;
    }
  }

  /**
   * The entities' layout blocks from DB
   */
  getLayoutBlocksFromApi(clear = false) {
    let params = new HttpParams();
    params = params.append('with', 'layout_block_type');
    if (this.$route.layoutBlock) {
      params = params.append(this.$route.layoutBlock.id, JSON.stringify({
        value: this.route.snapshot.paramMap.get('id'),
        filterableFn: FilterFunction.HasRelation,
        relation: this.$route.layoutBlock?.relation_key,
      }));
    }
    params = params.append('order', 'order');
    params = params.append('orderBy', 'asc');
    params = params.append('resultsPerPage', 'all');
    params = params.append('translations', 'true');
    this.apiService.getEntities('main/layout-blocks', params,
      res => {
        if (clear) {
          this.layoutBlocks = [];
          this.layout_blocks_control.clear();
        }
        this.layoutBlocks = res.data; // console.log("this.layoutBlocks", this.layoutBlocks);
        this.fetchFormControls();
      });
  }

  /**
   *
   * @param layoutBlockType
   */
  createLayoutBlock(layoutBlockType: LayoutBlockType) {

    // console.log(this.layoutBlocks);
    const data = {};
    layoutBlockType.elements.items.forEach((layoutItem: LayoutItem, index: number) => {
      // @ts-ignore
      data[SelectLayoutBlockControl.theLayoutKey(layoutItem, index)] = '';
    });

    this.loadableService.enable();
    this.apiService.addEntity('main/layout-blocks', {
      data: data,
      morph_id: this.route.snapshot.paramMap.get('id'),
      model: this.$route.model,
      layout_block_type_id: layoutBlockType.id,
      order: this.layout_blocks_control.length,
      lang: this.selectedLanguage.code,
    }, (res: ApiResponse) => {
      if (res.success) {
        // @ts-ignore
        this.layoutBlocks.push(res.data);
        // @ts-ignore
        this.addFieldGroup(res.data);
      }
      window.scrollTo(0, document.body.scrollHeight);
      this.loadableService.disable();
      this.alertService.success(res.message);
    }, (err: ApiError) => {
      this.loadableService.disable();
      this.alertService.danger(err.message);
    });
  }

  /**
   * Add layout block to form
   * @param i
   */
  saveLayoutBlock(i: number) {
    // console.log('this.selectedLanguage', this.selectedLanguage);
    const layoutBlock = this.layoutBlocks[i];
    this.loadingLayoutBlock = i;
    if (layoutBlock) {
      this.apiService.updateEntity('main/layout-blocks', layoutBlock.id, {
        id: layoutBlock.id,
        data: this.layout_blocks_control.at(i).value,
        order: i,
        elements: [],
        lang: this.selectedLanguage.code,
      }, (res: any) => {
        if (res.success) {
          this.getLayoutBlocksFromApi(true);
          this.alertService.success(res.message);
        } else {
          this.alertService.danger(res.message);
        }
        this.resetLayoutBlockLoader();
      }, err => {
        this.resetLayoutBlockLoader();
      });
    } else {
      this.alertService.warning('An error occurred');
      this.resetLayoutBlockLoader();
    }
  }

  copyLayoutBlockFromDefault(i: number) {
    const layoutBlock = this.layoutBlocks[i];
    this.layout_blocks_control.at(i).patchValue(layoutBlock.data);
  }


  fetchFormControls() {
    // console.log(this.layoutBlocks);
    this.layoutBlocks.forEach((layoutBlock: LayoutBlock) => { // console.log('layoutBlock', layoutBlock);
      this.addFieldGroup(layoutBlock);
    })
  }

  addFieldGroup(layoutBlock: LayoutBlock) {
    const layoutBlockType = layoutBlock.layout_block_type; // console.log('layoutBlockType add', layoutBlock)

    const layoutBlockFormGroup: FormGroup = this.fb.group({});

    const fieldGroup: FieldGroup = {
      title: layoutBlockType.name,
      key: layoutBlockType.slug,
      fields: layoutBlockType.elements.items.map((layoutItem: LayoutItem, index: number) => {
        const field: Field = {
          label: SelectLayoutBlockControl.theLayoutLabel(layoutItem, index), // `${index + 1}.${layoutItem.label}`,
          formProps: SelectLayoutBlockControl.getControl(layoutItem, this.$route.layoutBlock?.relation_key),
          key: SelectLayoutBlockControl.theLayoutKey(layoutItem, index),
          hidden: false,
          validators: Validators.required,
          translatable: layoutItem.translatable,
          renderProps: {
            hidden: false,
            // control: RenderControl.string, // Used in: manage app-codee-render-outlet
            component: RenderTextComponent,
          },
          x: layoutItem?.options?.x ?? 1,
          y: layoutItem?.options?.y ?? index + 1,
        };
        /**
         * Get the already submitted value
         */
        let langLayoutBlock;
        if (layoutBlock.translations && field.translatable) { //!field.key.startsWith('relation')) {
          langLayoutBlock = layoutBlock.translations.find(item => item.locale === this.selectedLanguage.code);
        } else {
          langLayoutBlock = layoutBlock;
        }

        const fieldValue = langLayoutBlock ? langLayoutBlock.data[field.key] : null; // console.log('fieldValue', fieldValue)
        layoutBlockFormGroup.addControl(field.key, new FormControl(fieldValue, field.validators));
        return field;
      })
    };

    // Form control to handle collapsable
    fieldGroup.collapsed = true;
    fieldGroup.columns = layoutBlockType.elements.meta.columns;

    this.selectedLayoutFieldGroups.push(fieldGroup);
    this.layout_blocks_control.push(layoutBlockFormGroup);

  }


  toggleLayoutBlock(i: number) { // todo Check the type of the param
    this.selectedLayoutFieldGroups[i].collapsed = !this.selectedLayoutFieldGroups[i].collapsed;
  }

  drop(e: CdkDragDrop<any[]>) {
    // console.log(e.container.data, e.previousIndex, e.currentIndex)
    moveItemInArray(this.layout_blocks_control.controls, e.previousIndex, e.currentIndex);
    moveItemInArray(this.selectedLayoutFieldGroups, e.previousIndex, e.currentIndex);
    moveItemInArray(this.layoutBlocks, e.previousIndex, e.currentIndex);
    this.request.putRequest('main/layout-blocks-bulk', {
      orderedIds: this.layoutBlocks.map(lb => lb.id),
    }).subscribe(
      res => console.log(res)
    );
  }

  /**
   * Remove the
   * @param layoutBlockForm
   * @param i
   */
  removeLayoutBlock(layoutBlockForm: any, i: number) {
    const layoutBlock = this.layoutBlocks[i];
    // console.log('removeLayoutBlock', layoutBlockForm, this.layoutBlocks)
    if (layoutBlock) {
      this.apiService.deleteEntity('main/layout-blocks', layoutBlock.id,
        (res: ApiResponse) => {
          if (res.success) {
            this.alertService.success(res.message);
            this.layout_blocks_control.removeAt(i);
            this.selectedLayoutFieldGroups.splice(i, 1);
            this.layoutBlocks.splice(i, 1);
            // this.getLayoutBlocks();
          } else {
            this.alertService.danger(res.message);
          }
        });
    } else {
      this.alertService.warning('An error occurred');
    }


    // this.layout_blocks_control.removeAt(i);
  }


  confirmAddition(layoutBlockType: LayoutBlockType) {
    const confirmBox = new ConfirmBoxInitializer();
    confirmBox.setTitle('Confirmation');
    confirmBox.setMessage(`Are you sure you want to proceed with creating a new layout block of type ${layoutBlockType.name}`);
    confirmBox.setButtonLabels('YES', 'NO');

    // Choose layout color type
    confirmBox.setConfig({
      LayoutType: DialogLayoutDisplay.INFO // SUCCESS | INFO | NONE | DANGER | WARNING
    });

    // Simply open the popup and listen which button is clicked
    // todo Check if unsubscription is needed
    confirmBox.openConfirmBox$().subscribe(resp => {
      // IConfirmBoxPublicResponse
      // console.log('Clicked button response: ',  resp.Success);
      if (resp.Success) {
        this.createLayoutBlock(layoutBlockType)
      }
    });
  }

  confirmDeletion(layoutBlockForm: any, i: number) {
    const confirmBox = new ConfirmBoxInitializer();
    confirmBox.setTitle('Confirm Delete');
    confirmBox.setMessage('Are you sure you want to proceed');
    confirmBox.setButtonLabels('YES', 'NO');

    // Choose layout color type
    confirmBox.setConfig({
      LayoutType: DialogLayoutDisplay.DANGER // SUCCESS | INFO | NONE | DANGER | WARNING
    });

    // Simply open the popup and listen which button is clicked
    // todo Check if unsubscription is needed
    confirmBox.openConfirmBox$().subscribe(resp => {
      // IConfirmBoxPublicResponse
      // console.log('Clicked button response: ',  resp.Success);
      if (resp.Success) {
        this.removeLayoutBlock(layoutBlockForm, i)
      }
    });
  }

  onSubmit() {
    console.log('onSubmit');
  }

  copyValuesFromDefault() {

  }

  calcColumnClass(columns: number = 1) {
    switch (columns) {
      case 1:
        return 'col-12';
      case 2:
        return 'col-6';
      case 3:
        return 'col-4';
      case 4:
        return 'col-3';
      default:
        return 'col-12';
    }
  }

  toggleLayoutBlockStatus(layoutBlock: LayoutBlock) {
    this.apiService.updateEntity('main/layout-blocks', layoutBlock.id, {
      id: layoutBlock.id,
      status: ! layoutBlock.status,
    }, (res: any) => {
      if (res.success) {
        this.getLayoutBlocksFromApi(true);
        this.alertService.success(res.message);
      } else {
        this.alertService.danger(res.message);
      }
    });
  }

  resetLayoutBlockLoader() {
    this.loadingLayoutBlock = -1;
  }
}


