import {FilterFunction, Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";
import {CodeeSelectComponent} from "../../../ui/forms/fields/codee-select/codee-select.component";
import {ApiData} from "../../../types/api.types";

const route: Route = {
  endpoint: "hotel/restaurants",
  translatable: true,
  withRelations: ['media_file', 'media_file_qr', 'media_gallery'],
  model: "CodeeHut\\HotelManagement\\Models\\Restaurant",
  texts: {
    key: 'restaurants',
    label: $localize`Restaurant`,
    title: $localize`Restaurants`,
    backLabel: $localize`Back to Restaurants`,
    description: $localize`Below is the list of the created restaurants`,
  },
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({key: 'media_main', label: $localize`Main image`, apiEndpoint: 'files/media'}),
      FieldGeneratorClass._mediaImage({key: 'media_logo', label: $localize`Restaurant Logo`, apiEndpoint: 'files/media'}),
      FieldGeneratorClass._mediaDocument({
        key: 'media_file', label: $localize`Restaurant Menu (Pdf)`,
        description: $localize`This file is visible on website (or any digital platform)`, apiEndpoint: 'files/media'
      }),
      FieldGeneratorClass._mediaDocument({
        key: 'media_file_qr',
        label: $localize`Restaurant Menu (Pdf) - QR code`,
        description: $localize`This document will be used for landing page for the users coming from QR code redirect`,
        apiEndpoint: 'files/media'
      }),
    ]
  }, {
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldGeneratorClass._text({
        key: 'id', label: $localize`ID`, hidden: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        }
      }),
      FieldGeneratorClass._text({
        key: 'name', label: $localize`Name`, translatable: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        }
      }),
      FieldGeneratorClass._text({key: 'link', label: $localize`External Link (Optional)`}),
      FieldGeneratorClass._checkbox({key: 'primary', label: $localize`Show restaurant in group home page`}),
      FieldGeneratorClass._richText({key: 'excerpt', translatable: true, label: $localize`Restaurant excerpt`}),
      FieldGeneratorClass._richText({key: 'content', translatable: true, label: $localize`Restaurant Amenities`}),
    ]
  }, {
    title: $localize`Relations`,
    key: "relations",
    fields: [
      FieldGeneratorClass._choiceFromApi({
        key: 'properties', col: 12, label: $localize`Properies`, apiEndpoint: 'hotel/properties', filter: {
          control: FetchControl.singleRelation,
          component: CodeeSelectComponent,
          dataEndpoint: 'hotel/properties?resultsPerPage=all&page=1',
          mapSelectObjectFn: (item: ApiData) => {
            return {
              id: item.id,
              label: item.name,
            };
          },
          filterable: FilterFunction.HasRelation,
          relation: 'properties',
        }
      }),
    ],
  }, {
    title: $localize`Media`,
    key: 'media',
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_gallery',
        label: $localize`Room Gallery`,
        apiEndpoint: 'files/media',
        multiple: true
      }),
    ]
  }],
  label: "",
  links: {
    create: '/app/hotel/restaurants/create',
    update: '/app/hotel/restaurants/update/',
    manage: '/app/hotel/restaurants/manage',
  },
  layoutBlock: {
    id: 'restaurant_id',
    relation_key: 'restaurants',
  },
  manage: {
    hasStatus: true,
    urlPattern: '/dine-n-drink/:slug/:id',
    columns: [{
      title: $localize`Logo`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Excerpt`,
        key: 'excerpt',
        component: RenderTextComponent,
      }, {
        label: $localize`Property`,
        component: RenderRelationshipComponent,
        relation: 'properties',
        relationType: 'multiple',
        key: 'name',
      }],
    }]
  },

};


export default route;
