import {FilterFunction, Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {CodeeSelectComponent} from "../../../ui/forms/fields/codee-select/codee-select.component";
import {ApiData} from "../../../types/api.types";
import AvailableLanguages from "../../../config/layout/available-languages";
import AvailableFonts from "../../../config/layout/available-fonts";
import {CodeeMultiSelectComponent} from "../../../ui/forms/fields/codee-multi-select/codee-multi-select.component";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";
import {APPLICATION_TYPE} from "../../../enums/applications.enum";
import currenciesList from "../../layout/currencies-list";

const route: Route = {
  endpoint: "main/applications",
  translatable: true,
  withRelations: ['social_profiles', 'settings'],
  model: "CodeeHutCommon\\MainRepository\\Models\\Application",
  texts: {
    key: 'applications',
    label: $localize`Application`,
    title: $localize`Applications`,
    backLabel: $localize`Back to Applications`,
    description: $localize`Below is the list of the created applications`,
  },
  fieldGroups: [{
    title: $localize`Application Basic Details`,
    key: 'details',
    fields: [
      FieldGeneratorClass._text({ key: 'id', label: $localize`ID`, hidden: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        } }),
      FieldGeneratorClass._text({ key: 'name', label: $localize`Name`, translatable: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        } }),
      FieldGeneratorClass._select({ key: 'type', label: $localize`Application Type`,
        options: Object.keys(APPLICATION_TYPE).map( (k:string) => {
          return {
            id: k,
            label: APPLICATION_TYPE[k as keyof typeof APPLICATION_TYPE], // super https://stackoverflow.com/questions/50417254/dynamically-access-enum-in-typescript-by-key
          }
        })}),
    ]
  },  {
    title: $localize`Domain`,
    key: 'domain',
    fields: [
      FieldGeneratorClass._text({ key: 'domain', label: $localize`Application Domain` }),
    ],
  },{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({key: 'media_logo', label: $localize`Logo`,
          description: $localize`Selected logo image will be used in the header and footer of the site`,
        apiEndpoint: 'files/media'}),
      FieldGeneratorClass._mediaImage({key: 'media_main',
        description: $localize`The most important picture on the site. will be seen in many cases, as well as when people share it on social media.`,
        label: $localize`Main image`, apiEndpoint: 'files/media'}),
    ]
  },],
  label: "",
  links: {
    create: '/app/main/applications/create',
    update: '/app/main/applications/update/',
    manage: '/app/main/applications/manage',
  },
  manage: {
    hasStatus: true,
    hasQrCodes: true,
    columns: [{
      title: $localize`Logo`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Application Domain`,
        key: 'domain',
        component: RenderTextComponent,
      }, {
        label: $localize`Destination`,
        component: RenderRelationshipComponent,
        relation: 'destination',
        relationType: 'single',
        key: 'name',
      }],
    }]
  },
  settingGroups: [{
    title: $localize`Fonts`,
    key: 'fonts',
    fields: [{
      key: 'settings-font-headings',
      label: $localize`Heading Font`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'select',
        control: FetchControl.value,
        component: CodeeSelectComponent,
        data: AvailableFonts,
        editable: true,
        mapSelectObjectFn: (item: ApiData) => {
          return {
            id: item.id,
            label: item.name,
          };
        }
      },
    }, {
      key: 'settings-font-body',
      label: $localize`Body Font`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'select',
        control: FetchControl.value,
        component: CodeeSelectComponent,
        data: AvailableFonts,
        editable: true,
        mapSelectObjectFn: (item: ApiData) => {
          return {
            id: item.id,
            label: item.name,
          };
        }
      },
    }]
  }, {
    title: $localize`Booking Parameters`,
    key: 'booking-parameters',
    fields: [
      FieldGeneratorClass._checkbox({
        col: 12,
        key: 'settings-booking-api-enable',
        label: $localize`Enable Booking Provider API`,
        description: `Enable booking search to be handled with Booking Provider API` }),
      FieldGeneratorClass._text({
        key: 'settings-booking-url',
        label: `Booking Url`,
        description: $localize`The target url where the user will complete the reservation`}),
      FieldGeneratorClass._select({
        key: 'settings-booking-currency',
        label: $localize`Currency`,
        description: $localize`The default currency the user will see prices`,
        options: currenciesList.map( currency => {
          return {
            id: currency.code,
            label: currency.name,
          }
        })}),
    ]
  }, {
    title: $localize`Extra Booking Parameters`,
    key: 'extra-booking-params',
    fields: [
      FieldGeneratorClass._text({
        key: 'settings-bookingflight-url',
        label: $localize`Search Booking n Flight Url`,
        description: $localize`The target url where the user will be redirected for search reservation with a flight`}),
      FieldGeneratorClass._text({ key: 'settings-booking-transfer-url',
        label: $localize`Transfer Url`,
        description: $localize`Submit the URL to direct users to book a transfer.` }),
      FieldGeneratorClass._text({ key: 'settings-booking-map-url',
        label: $localize`Map Url`,
        description: $localize`Show how the users will drive to your location` }),
    ]
  }, {
    title: $localize`Loyalty`,
    key: 'loyalty',
    fields:[
      FieldGeneratorClass._checkbox({
        col: 12,
        key: 'settings-loyalty-enable',
        label: $localize`Enable loyalty`,
      }),
      FieldGeneratorClass._text({
        key: 'settings-loyalty-appid',
        label: 'App Id',
      }),
      FieldGeneratorClass._text({
        key: 'settings-loyalty-clientId',
        label: 'Client Id',
      }),
    ]
  }, {
    title: $localize`Languages`,
    key: 'languages',
    fields: [{
      key: 'settings-languages',
      label: $localize`Languages`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'select',
        control: FetchControl.value,
        controlKey: 'theme',
        component: CodeeMultiSelectComponent,
        data: AvailableLanguages,
        editable: true,
        mapSelectObjectFn: (item: ApiData) => {
          return {
            id: item.id,
            label: item.name,
          };
        }
      },
    }]
  },],
  layoutBlock: {
    id: 'application_id',
    relation_key: 'applications',
  },
  /*menuGroups: [{
    title: 'Application navigation links',
    key: 'navigation-links',
    fields: [
      FieldGeneratorClass._links({ key: 'menu_main_links', translatable: true, label: 'Main navigation links',
        description: 'The navigation links visible on top of the site. Usually holds up to 6 items' }),
      FieldGeneratorClass._links({ key: 'menu_full_navigation_links', translatable: true, label: 'Full navigation links',
        description: 'The links inside the extended navigation element when the user clicks the dots button'}),
      FieldGeneratorClass._links({ key: 'menu_footer_links', translatable: true, label: 'Footer navigation links',
        description: 'Navigation links on the footer (very bottom of the site)'}),
    ]
  }],*/
  emailGroups: [{
    title: $localize`Sender settings`,
    key: 'sender-settings',
    fields: [
      FieldGeneratorClass._text({ key: 'settings-email-sender',
        label: $localize`Email sender`,
        description: $localize`The email address "from" will be visible on email messages` }),
      FieldGeneratorClass._text({ key: 'settings-email-recipient',
        label: $localize`Email recipient`,
        description: $localize`All of the emails from the application forms will be sent to this address.` }),
      FieldGeneratorClass._mediaImage({key: 'settings-email-logo',
        label: $localize`Email logo`,
        description: $localize`This image will be shown at the top of the email template.`,
        apiEndpoint: 'files/media'}),
    ]
  }, {
    title: $localize`Subscription`,
    key: 'subscription',
    fields: [
      FieldGeneratorClass._text({ key: 'settings-email-moosend-apikey',
        label: $localize`Moosend API Key`, description: '' }),
      FieldGeneratorClass._text({ key: 'settings-email-moosend-mailing-list-id',
        label: $localize`Moosend Mailing List`, description: '' }),
    ]
  }]
};


export default route;

