import { Component, OnInit } from '@angular/core';
import {LoadableService} from "../../../services/abstract/loadable.service";

@Component({
  selector: 'app-loadable',
  templateUrl: './loadable.component.html',
  styleUrls: ['./loadable.component.scss']
})
export class LoadableComponent implements OnInit {

  public isLoading: boolean = false;

  constructor(
      private loadableService: LoadableService,
  ) { }

  ngOnInit(): void {

    // Subscribe to loadable service
    this.loadableService.active().subscribe(
        loading => this.isLoading = loading
    )
  }

}
