import {
  Component,
  OnInit,
} from '@angular/core';
import { Location } from '@angular/common';
import {ActivatedRoute, Router} from "@angular/router";
import {Route} from "../../../types/route.type";
import {ApiService} from "../../../services/abstract/api.service";
import {HttpParams} from "@angular/common/http";
import {Subscription} from "rxjs";
import {SessionService} from "../../../services/auth/session.service";
import {FORM_MODE} from "../../../enums/entities.enum";

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss'],
})
export class FormComponent implements OnInit {

  public $route: Route;
  public mode: FORM_MODE;
  public entityToUpdate: any;

  public activeTab: AvailableTab =  'data'; // 'layout-block'; //

  private tenantSubscr!: Subscription;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private apiService: ApiService,
    private location: Location,
    private sessionService: SessionService,
  ) { }

  ngOnInit(): void {

    this.route.data.subscribe(data => {
      // console.log('data', data);
      this.$route = data.handler.route;
      this.mode = data.mode;
      // console.log('this.$route', this.$route.settingGroups);

      this.getEntityIfNeeded();
    });


    // Subscribe to tenant change.
    // When a tenant is changed then we have to return to manage component.
    this.tenantSubscr = this.sessionService.tenantSelectedSbj.subscribe(
        tenant => {
          // console.log(tenant, this.$route.links.manage);
          // Track tenant change in order to redirect user to manage component
          // this.router.navigate([this.$route.links.manage]);
        }
    )


  }


  getEntityIfNeeded() {
    const _id = this.route.snapshot.paramMap.get('id');
    if (_id !== null) {
      let params = new HttpParams();
      params = params.append('translations', 'true');
      if (this.$route.withRelations)
        params = params.append('with', this.$route.withRelations.join(','));

      this.apiService.getEntity(this.$route.endpoint, _id, params, (res) => {
        this.entityToUpdate = res.data;
      })
    }
  }

  refreshEntity(entity: any) {
    // this.entityToUpdate = entity;
    // this.getEntityIfNeeded();
  }

  setTab(tab: AvailableTab) {
    this.activeTab = tab;
  }

  isActiveTab(tab: AvailableTab) {
    return this.activeTab === tab;
  }

  onBack() {
    this.location.back();
  }

}


type AvailableTab = 'data' | 'layout-block' | 'settings' | 'media' | 'menu' | 'email';
