import {FilterFunction, Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {CodeeListSingleComponent} from "../../../ui/forms/fields/codee-list-single/codee-list-single.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";

const route: Route = {
  endpoint: "knowledge-base/stories",
  translatable: true,
  model: "CodeeHut\\KnowledgeBase\\Models\\Story",
  texts: {
    key: 'stories',
    label: $localize`Story`,
    title: $localize`Stories`,
    backLabel: $localize`Back to Stories`,
    description: $localize`Below is the list of the created stories`,
  },
  fieldGroups: [{
    title: `Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_main',
        label: $localize`Main image`,
        description: $localize`The image thumbnail`,
        apiEndpoint: 'files/media'
      }),
    ]
  }, {
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldGeneratorClass._text({
        key: 'id', label: $localize`ID`, hidden: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        }
      }),
      FieldGeneratorClass._text({
        key: 'name', label: $localize`Story Title`, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        }
      }),
      FieldGeneratorClass._selectFromApi({
        key: 'categories',
        col: 6,
        label: $localize`Categories`,
        apiEndpoint: 'knowledge-base/categories?flat-tree=1&resultsPerPage=all&page=1',
        // filter: {
        //   control: FetchControl.multipleRelation,
        //   component: CodeeSelectComponent,
        //   dataEndpoint: 'knowledge-base/categories?flat-tree=1&resultsPerPage=all&page=1',
        //   mapSelectObjectFn: (item: ApiData) => {
        //     return {
        //       id: item.id,
        //       label: item.name,
        //       depth: item.depth as number,
        //     };
        //   },
        //   filterable: FilterFunction.HasRelation,
        //   relation: 'categories',
        // }
      }),
    ]
  }, {
    title: $localize`Content`,
    key: 'content',
    fields: [
      FieldGeneratorClass._richText({key: 'excerpt', translatable: true, label: $localize`Excerpt`}),
      FieldGeneratorClass._richText({key: 'content', translatable: true, label: $localize`Content`}),
      {
        key: 'snippets',
        label: $localize`Snippets`,
        hidden: false,
        col: 12,
        default: '',
        validators: [],
        formProps: {
          type: 'input',
          control: FetchControl.returnValues,
          component: CodeeListSingleComponent,
          editable: true,
          isTextarea: true,
        },
        renderProps: {
          hidden: false,
          // control: RenderControl.string, // Used in: manage app-codee-render-outlet
          component: RenderTextComponent,
        },
      },]
  }],
  label: "",
  links: {
    create: '/app/knowledge-base/stories/create',
    update: '/app/knowledge-base/stories/update/',
    manage: '/app/knowledge-base/stories/manage',
  },
  manage: {
    hasStatus: true,
    columns: [/*{
            title: 'Logo',
            className: 'col-auto',
            fields: [{
                label: 'Main image',
                key: 'media_main',
                component: RenderImageComponent,
            }],
        },*/{
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Categories`,
        key: 'name',
        component: RenderRelationshipComponent,
        relationType: 'multiple',
        relation: 'categories',
      }],
    }]
  },
};


export default route;
