import {Injectable} from '@angular/core';
import {ApiService} from "../abstract/api.service";
import {HttpParams} from "@angular/common/http";
import {LayoutBlockType} from "../../types/entities.types";

@Injectable({
  providedIn: 'root'
})
export class LayoutBlocksService {

  $layoutBlocks: any = [];

  constructor(
      private apiService: ApiService
  ) {

      // if (this.areEmpty())
      //     this.$layoutBlocks = this.getLayoutBlockTypes();
  }

  async getLayoutBlockTypes(params = new HttpParams()) : Promise<LayoutBlockType[]> {
      return await new Promise(resolve => {
        params = params.append('resultsPerPage', 'all');
        this.apiService.getEntities('main/layout-block-types', params,
              async (res) => {
                  this.$layoutBlocks = res.data;
                  resolve(this.$layoutBlocks);
              })
      })
  }

  areEmpty() {
      return this.$layoutBlocks.length === 0;
  }
}
