import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup} from "@angular/forms";
import {ApiService} from "../../../../services/abstract/api.service";
import {AlertService} from "../../../../modules/alert/alert.service";
import {HttpParams} from "@angular/common/http";
import {Field} from "../../../../types/route.type";
import {CodeeInputComponent} from "../../../../ui/forms/fields/codee-input/codee-input.component";
import {FetchControl} from "../../../../inc/fetch-control.class";
import {ApiResponse} from "../../../../types/api.types";

@Component({
  selector: 'settings-headquarters-settings',
  templateUrl: './headquarters-settings.component.html',
  styleUrls: ['./headquarters-settings.component.scss']
})
export class HeadquartersSettingsComponent implements OnInit {

  public settingsForm: FormGroup = new FormBuilder().group({});

  public locationFields: Field[] = [{
    key: 'address',
    label: 'Address',
    col: 12,
    formProps: {
      control: FetchControl.value,
      component: CodeeInputComponent,
    },
    validators: [],
  }, {
    key: 'city',
    label: 'City',
    col: 6,
    formProps: {
      control: FetchControl.value,
      component: CodeeInputComponent,
    },
    validators: [],
  }, {
    key: 'postal_code',
    label: 'Postal Code',
    col: 6,
    formProps: {
      control: FetchControl.value,
      component: CodeeInputComponent,
    },
    validators: [],
  }, {
    key: 'country',
    label: 'Country',
    col: 6,
    formProps: {
      control: FetchControl.value,
      component: CodeeInputComponent,
    },
    validators: [],
  }, ];


  constructor(
      private fb: FormBuilder,
      private apiService: ApiService,
      private alertService: AlertService,
  ) { }

  ngOnInit(): void {

    this.settingsForm = this.fb.group({
      'headquarters_location': this.fb.group({
        'translatable': [true],
        'address': ['', []],
        'city': ['', []],
        'country': ['', []],
        'postal_code': ['', []],
      }),
    });

    // Get already submitted values
    let params = new HttpParams();
    params = params.append('keys', 'headquarters_location');
    this.apiService.getEntities('settings/settings', params, (settings) => {
      settings.data.forEach( (setting: any) => {
        const fControl = this.settingsForm.get(setting.key); console.log('setting', setting.translatable, setting.localized_value, setting.value)
        if (fControl) {
          fControl.patchValue(this.settingsForm.get('headquarters_location.translatable')?.value ?
            setting.localized_value : setting.value);
        }
      })
    })
  }

  onSubmit() {
    console.log('onSubmit()', this.settingsForm.value);

    this.apiService.addEntity('settings/settings', this.settingsForm.value, (res: ApiResponse) => {
      this.alertService.success(res.message);
    });
  }

}
