import {Component, Input, OnInit} from '@angular/core';
import {CodeeFormControlInterface} from "../codee-form-control.interface";
import {FormProps} from "../../../../types/route.type";
import {FormArray, FormBuilder, FormGroup} from "@angular/forms";
import {CdkDragDrop} from "@angular/cdk/drag-drop";

@Component({
  selector: 'app-codee-list-hall',
  templateUrl: './codee-list-hall.component.html',
  styleUrls: ['./codee-list-hall.component.scss']
})
export class CodeeListHallComponent implements OnInit, CodeeFormControlInterface {

  @Input() label: string;
  @Input() description: string | undefined;
  @Input() value: number[];
  @Input() mode: string;
  @Input() props: FormProps;

  public listForm: FormGroup;

  constructor(private fb: FormBuilder) {
    this.listForm = this.fb.group({
      items: this.fb.array([]) ,
    });
  }

  ngOnInit(): void {
  }

  onChange = (items: any) => {};

  onTouched = () => {};

  get items() : FormArray {
    return this.listForm.get("items") as FormArray;
  }

  newItemGroup(): FormGroup {
    return this.fb.group({
      id: null,
      name: null,
      sq_meters: 0,
      dimensions: '',
      ceiling_height: null,
      floor_type: null,
      floor_location: null,
      entrance_dimensions: null,
      theatre: null,
      school_room: null,
      u_shape: null,
      banquet: null,
      buffet: null,
      cocktail: null,
    })
  }

  addItemGroup() {
    this.items.push(this.newItemGroup());
  }

  removeItemGroup(i:number) {
    this.items.removeAt(i);
    this.onChange(this.listForm.controls['items'].value);
  }

  toggleItemMoreOptions(i: number) {
    this.items.at(i).get('more_options')?.setValue(!this.items.at(i).get('more_options')?.value);
  }

  onSubmit() {
    // console.log('this.listForm.value', this.listForm.value);
    this.onChange(this.items.value);
  }

  registerOnChange(onChange: any): void {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any): void {
    this.onTouched = onTouched;
  }

  clearItems() {
    while (this.items.length !== 0) {
      this.items.removeAt(0)
    }
  }

  writeValue(obj: any): void {
    console.log('[CodeeListItemsComponent] writeValue', obj);
    if (obj) {
      this.clearItems();
      obj.forEach( (o: any) => {
        this.items.push(this.fb.group({
          id: o.id,
          name: o.name,
          sq_meters: o.sq_meters,
          dimensions: o.dimensions,
          ceiling_height: o.ceiling_height,
          floor_type: o.floor_type,
          floor_location: o.floor_location,
          entrance_dimensions: o.entrance_dimensions,
          theatre: o.theatre,
          school_room: o.school_room,
          u_shape: o.u_shape,
          banquet: o.banquet,
          buffet: o.buffet,
          cocktail: o.cocktail,
        }));
      })
    } else {
      // When reset() is triggered from outside
      this.listForm.reset();
      while (this.items.length !== 0) {
        this.items.removeAt(0)
      }
      // console.log('this.items', this.items);
    }
  }

  drop(event: CdkDragDrop<FormGroup[]>) {
    const dir = event.currentIndex > event.previousIndex ? 1 : -1;

    const from = event.previousIndex;
    const to = event.currentIndex;

    const temp = this.items.at(from);
    for (let i = from; i * dir < to * dir; i = i + dir) {
      const current = this.items.at(i + dir);
      this.items.setControl(i, current);
    }
    this.items.setControl(to, temp);
    this.onChange(this.items.value);
  }

}
