import { Injectable } from '@angular/core';
import {Language} from "../../types/layout.types";
import AvailableLanguages from "../../config/layout/available-languages";
import {environment} from "../../../environments/environment";
import {BehaviorSubject} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class LanguageService {

  public selectedLanguage$ = new BehaviorSubject<Language>(<Language>{
    id: 1,
    code: 'en',
    label: 'English',
  });

  constructor() {
    const lang = AvailableLanguages.find(l => l.code === this.getFromLocal()); // console.log('LanguageService lang', lang)
    if (lang) {
      this.setLanguage(lang);
    }
  }



  doSelectLanguage(locale: string) {
    const lang = AvailableLanguages.find(l => l.code === locale);
    if (lang) {
      this.setLanguage(lang);
    }
  }

  /**
   *
   * @param lang
   */
  setLanguage(lang: Language) {
    this.storeToLocal(lang);
    this.selectedLanguage$.next(lang);
  }

  /**
   *
   * @param lang
   */
  storeToLocal(lang: Language) {
    localStorage.setItem('_selectedLang', lang.code);
  }

  getFromLocal() {
    const langStored = localStorage.getItem('_selectedLang');
    if (langStored) {
      return langStored;
    } else {
      return environment.defaultLocale;
    }
  }

}
