import {
  Component,
  ComponentFactoryResolver,
  forwardRef,
  Injector,
  Input,
  OnInit,
  ViewContainerRef
} from "@angular/core";
import {NG_VALUE_ACCESSOR, NgControl} from "@angular/forms";
import {RenderTextComponent} from "./render-text/render-text.component";
import {RenderMapObjectComponent} from "./render-map-object/render-map-object.component";
import {ApiData} from "../../../types/api.types";
import {CodeeRenderControlInterface} from "./codee-render-control.interface";

@Component({
  selector: "app-codee-render-outlet",
  template: ``,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CodeeRenderOutletComponent),
      multi: true
    }
  ]
})
export class CodeeRenderOutletComponent implements OnInit {

  @Input() props: any;
  @Input() label: string;
  @Input() item: ApiData;
  @Input() key: string;

  constructor(
    // public injector: Injector,
    private viewContainerRef: ViewContainerRef
  ) {
  }

  public ngOnInit(): void {

    // const ngControl = this.injector.get(NgControl);

    let isVisible = true;
    if (this.props.hidden) {
      isVisible = !this.props.hidden;
    }
    // console.log('CodeeRenderOutletComponent', isVisible);

    if (isVisible) {

      const componentRef = this.viewContainerRef.createComponent<CodeeRenderControlInterface>(
          this.props.component
      );

      componentRef.instance.label = this.label;
      componentRef.instance.item = this.item;
      componentRef.instance.key = this.key;
      componentRef.instance.props = this.props;

    }

    // // @ts-ignore
    // ngControl.valueAccessor = componentRef.instance;
  }
}

