import {Component, Input, OnInit} from '@angular/core';
import {CodeeFormControlInterface} from "../codee-form-control.interface";
import {FormProps} from "../../../../types/route.type";
import {FormArray, FormBuilder, FormGroup} from "@angular/forms";
import {FetchControl} from "../../../../inc/fetch-control.class";
import {CodeeFileUploadComponent} from "../codee-file-upload/codee-file-upload.component";

@Component({
  selector: 'app-codee-media-link',
  templateUrl: './codee-media-link.component.html',
  styleUrls: ['./codee-media-link.component.scss']
})
export class CodeeMediaLinkComponent implements OnInit, CodeeFormControlInterface {

  @Input() label: string;
  @Input() value: number[];
  @Input() mode: string;
  @Input() props: FormProps;

  public listForm: FormGroup;

  uploadFileProps = {
    control: FetchControl.value,
    component: CodeeFileUploadComponent,
    // editable: true,
    apiEndpoint: "files/media", // @ todo how to pass FieldsCommonClass.namespace again
    fileTypes: 'image/png, image/svg, image/svg+xml, application/pdf'
  }

  constructor(private fb: FormBuilder) {
    this.listForm = this.fb.group({
      items: this.fb.array([]) ,
    });
  }

  ngOnInit(): void {
  }

  onChange = (items: any) => {};

  onTouched = () => {};

  get items() : FormArray {
    return this.listForm.get("items") as FormArray;
  }

  newItemGroup(): FormGroup {
    return this.fb.group({
      id: '',
      label: '',
      type: 'regular',
      media: '',
    })
  }

  addItemGroup() {
    this.items.push(this.newItemGroup());
  }

  removeItemGroup(i:number) {
    this.items.removeAt(i);
    this.onChange(this.listForm.controls['items'].value);
  }

  onSubmit() { console.log('this.listForm.controls[\'items\'].value', this.listForm.controls['items'].value);
    this.onChange(this.listForm.controls['items'].value);
  }

  registerOnChange(onChange: any): void {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any): void {
    this.onTouched = onTouched;
  }

  writeValue(obj: any): void {
    console.log('[CodeeMediaLinkComponent] writeValue', obj);
    if (obj) {
      obj.forEach( (o: any) => {
        this.items.push(this.fb.group({
          id: o.id,
          label: o.label,
          type: o.type,
          media: o.media,
        }));
      })
    }
  }

  changed(e: any) {
    console.log('changed', e);
  }
}
