import {Component, forwardRef, Input, OnChanges, OnInit, Output, SimpleChanges, EventEmitter} from '@angular/core';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from "@angular/forms";
import {FormProps} from "../../../../types/route.type";
import {CodeeFormControlInterface} from "../codee-form-control.interface";

@Component({
  selector: 'app-codee-input',
  templateUrl: './codee-input.component.html',
  styleUrls: ['./codee-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CodeeInputComponent),
      multi: true
    }
  ]
})
export class CodeeInputComponent implements OnInit, OnChanges, CodeeFormControlInterface {

  @Input() label: string;
  @Input() description: string | undefined;
  @Input() placeholder: string = '';
  @Input() value: string;
  @Input() mode: string;
  @Input() data: any;
  @Input() props: FormProps = {} as FormProps;

  @Output() onValueChanged = new EventEmitter<string>();

  inputModel: string;

  type: string = 'text';

  onChange = (value: string) => {};

  onTouched = () => {};

  ngOnInit() {
    this.type = this.props && this.props.type ? this.props.type : 'text';
  }

  registerOnChange(onChange: any) {
    // console.log('registerOnChange', onChange)
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  writeValue(value: string) {
    this.inputModel = value;
  }

  ngOnChanges(changes: SimpleChanges): void {
    console.log('changes', changes);
  }

}
