import {Component, Input, OnInit} from '@angular/core';
import {FormProps} from "../../../../types/route.type";
import {CodeeFormControlInterface} from "../codee-form-control.interface";
import {FormArray, FormBuilder, FormGroup} from "@angular/forms";

@Component({
  selector: 'app-codee-list-single',
  templateUrl: './codee-list-single.component.html',
  styleUrls: ['./codee-list-single.component.scss']
})
export class CodeeListSingleComponent implements OnInit, CodeeFormControlInterface {

  @Input() label: string;
  @Input() value: number[];
  @Input() mode: string;
  @Input() props: FormProps;

  public listForm: FormGroup;
  public titleDisabled: boolean = false;

  constructor(private fb: FormBuilder) {
    this.listForm = this.fb.group({
      items: this.fb.array([]) ,
    });
  }

  ngOnInit(): void {
    // console.log('CodeeListSingleComponent', this.props);
  }

  onChange = (items: any) => {};

  onTouched = () => {};

  get items() : FormArray {
    return this.listForm.get("items") as FormArray;
  }

  newItemGroup(): FormGroup {
    return this.fb.group({
      id: '',
      title: '',
      content: '',
    })
  }

  addItemGroup() {
    this.items.push(this.newItemGroup());
  }

  removeItemGroup(i:number) {
    this.items.removeAt(i);
    this.onChange(this.items.value);
  }

  onFormChange() {
    // console.log('[CodeeListItemsComponent] onFormChange', this.items.value);
    this.onChange(this.items.value);
  }

  registerOnChange(onChange: any): void {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any): void {
    this.onTouched = onTouched;
  }

  writeValue(obj: any): void {
    console.log('[CodeeListItemsComponent] writeValue', obj);
    if (Array.isArray(obj)) { console.log(obj.some( item => item.title === undefined))
      // Check if titles must be visible
      this.titleDisabled = obj.some( item => item.title === undefined);
      obj.forEach( (o: any) => {
        // const {title, content} = o.value;
        this.items.push(this.fb.group({
          id: o.id,
          title: o.title, //title || null,
          content: o.content,
        }));
      });
      // console.log('[CodeeListItemsComponent] writeValue', this.items.value);
      this.onTouched();
    }
  }

}
