import { Injectable } from '@angular/core';
import {RequestService} from "../abstract/request.service";
import {Credentials} from "../../types/auth.types";

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(
    private request: RequestService,
  ) { }

  public connect(credentials: Credentials) {
    return this.request.postRequest('connect', credentials, true);
  }

  public register(credentials: Credentials) {
    return this.request.postRequest('register', credentials, true);
  }
}
