import { Component, OnInit } from '@angular/core';
import {Role, Tenant, User} from "../../../types/entities.types";
import {SessionService} from "../../../services/auth/session.service";
import tenant from "../../../config/routes/tenancy/tenant";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  public user: User;
  public isSuperAdmin: boolean | Role;

  public selectedTenant!: Tenant;

  constructor(
      private sessionService: SessionService,
  ) { }

  ngOnInit(): void {

    // Get the logged user
    this.user = this.sessionService.loggeinUser.getValue();
    // console.log('user', this.user);

    this.isSuperAdmin = this.sessionService.isSuperAdmin();

    this.sessionService.selectedTenant().subscribe(
      tenant => this.selectedTenant = tenant
    )

  }

  /**
   *
   * @param _package
   */
  isPackageVisible(_package: string) {

    if (this.isSuperAdmin) {
      return false;
    }

    const selectedTenant = this.sessionService.tenantSelectedSbj.getValue(); // console.log('tenantSelectedSbj', selectedTenant);

    if (this.user.tenants) {
      let [tenant] = this.user.tenants.filter( (tenant: Tenant) => tenant.id === selectedTenant.id);
      // console.log(tenant)
      return tenant.packages.map( p => p.path).indexOf(_package) > -1;
    }

    return false;
  }

}
