import {Component, Input, OnInit} from '@angular/core';
import {CodeeFormControlInterface} from "../codee-form-control.interface";
import {FormProps} from "../../../../types/route.type";
import {ElementLayoutBlockTypeData, LayoutItem} from "../../../../types/layout.types";
import {AbstractControl, FormArray, FormBuilder, FormGroup} from "@angular/forms";
import layoutBlockTypeItems from "../../../../config/layout/layout-block-type-items";
import {CdkDragDrop, moveItemInArray} from "@angular/cdk/drag-drop";
import {AlertService} from "../../../../modules/alert/alert.service";
import {CdkDropList} from "@angular/cdk/drag-drop/directives/drop-list";

@Component({
  selector: 'app-codee-layout-block',
  templateUrl: './codee-layout-block.component.html',
  styleUrls: ['./codee-layout-block.component.scss']
})
export class CodeeLayoutBlockComponent implements OnInit, CodeeFormControlInterface {

  @Input() label: string;
  @Input() mode: string;
  @Input() props: FormProps;

  layoutBlockTypeForm: FormGroup;
  listIdPrefix: string = 'cdkDropList-';
  maxNumberOfColumnsAllowed: number = 4;

  availableParts: LayoutItem[] = layoutBlockTypeItems;

  selectedLayoutItems: LayoutItem[] = [];

  blockTypeData: ElementLayoutBlockTypeData = {
    meta: {
      columns: 1,
    },
    items: [],
    version: 2,
  }

  constructor(
    private alertService: AlertService,
    private fb: FormBuilder,
  ) {
    this.layoutBlockTypeForm = this.fb.group({
      items: this.fb.array([]),
    })
  }

  get items() : FormArray {
    return this.layoutBlockTypeForm.get("items") as FormArray;
  }

  // newItemGroup(): FormGroup { debugger
  //   return this.fb.group({
  //     key: '',
  //     description: '',
  //     label: '',
  //     translatable: [false],
  //     options: {},
  //   })
  // }

  // update() {
  //   this.items.push(this.newItemGroup());
  //   this.onChange(this.items.value);
  // }

  removeItemGroup(i:number) {
    this.items.removeAt(i);
    this.updateItems();
  }

  onLayoutBlockTypeChanged() {
    this.updateItems();
  }

  onChange = (value: ElementLayoutBlockTypeData) => {};

  updateItems() {
    this.blockTypeData.items = this.items.value;
    this.blockTypeData.items.sort((a: LayoutItem, b: LayoutItem) => {
      if (a?.options && b?.options) {
        return a.options.x - b.options.x || a.options.y - b.options.y;
      } else {
        return 1;
      }
    });
    this.onChange(this.blockTypeData);
    console.log('items', this.blockTypeData)
  }

  onTouched = () => {};

  ngOnInit(): void {}

  registerOnChange(onChange: any) {
    // console.log('registerOnChange');
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  setDisabledState(isDisabled: boolean): void {
  }

  writeValue(layoutBlockTypes: any): void {  // console.log('CodeeLayoutBlockComponent@writeValue', layoutBlockTypes);
    this.clearItems();

    this.blockTypeData.meta.columns = layoutBlockTypes?.meta?.columns ?? 1;

    if (! layoutBlockTypes?.items) return;

    layoutBlockTypes.items.forEach( (layoutBlockType: any) => {
      // Start looping over the items
      this.items.push(this.fb.group({
        key: layoutBlockType.key,
        type: layoutBlockType.type,
        description: layoutBlockType.description || '',
        label: layoutBlockType.label || '',
        translatable: layoutBlockType.translatable || false,
        options: this.fb.group({
          x: [layoutBlockType?.options?.x || 1],
          y: [layoutBlockType?.options?.y || 1],
          selected: [layoutBlockType?.options?.selected],
          choices: [layoutBlockType?.options?.choices],
        }),
      }));
    })
    this.items.patchValue(layoutBlockTypes.items);
    this.updateItems();
  }

  clearItems() {
    while (this.items.length !== 0) {
      this.items.removeAt(0)
    }
  }

  /**
   * Creates a new empty layout block
   * @param part
   */
  addPart(part: LayoutItem) {
    // this.selectedLayoutItems.push(part);
    this.items.push(this.fb.group({
      key: part.key,
      type: part.type,
      description: part.description ?? '',
      label: part.label ?? '',
      translatable: part.translatable ?? false,
      options: this.fb.group({
        x: [part.options?.x ?? 1],
        y: [part.options?.y ?? 1],
        choices: [part.options?.choices],
        selected: [part.options?.selected],
      }),
    }));
    this.reorderItemsPerColumn(1);
    this.updateItems(); console.log(this.items.value);
  }

  // removeFromSelection(part: LayoutItem) {
  //   // console.log('part', part, this.selected.indexOf(part));
  //   this.selectedLayoutItems.splice(this.selectedLayoutItems.indexOf(part), 1);
  //   this.onChange(this.selectedLayoutItems);
  // }


  /**
   * With this method we rearrange items in the columns.
   * We override the default function of the material draggable cdk because in our case the position of the item
   * is defined from the parameres "y" and "x". So, for example, the first item of the second columns will have
   * the following values:
   * x: 2
   * y: 1
   * @param event
   */
  drop(event: CdkDragDrop<string[]>) {
    // Get the column it's been dropped
    const columnIndex = this.getIndexFromContainer(event.container); //parseInt(event.container.id.replace(this.listIdPrefix, '')) + 1; console.log('columnIndex', columnIndex)
    const previouColumnIndex = this.getIndexFromContainer(event.previousContainer); //parseInt(event.container.id.replace(this.listIdPrefix, '')) + 1; console.log('columnIndex', columnIndex)

    /**
     * We have to separate the cases
     * First we examine the case the item to be reordered inside the same column
     */
    if (event.previousContainer === event.container) {
      // Reorder items only on current column
      moveItemInArray(this.items.controls, event.previousIndex, event.currentIndex);
      this.reorderItemsPerColumn(columnIndex);
    }
    /**
     * Next the case the user change column to that item
     */
    else {
      // moveItemInArray(this.items.controls, event.previousIndex, event.currentIndex);
      this.items.at(event.previousIndex).get('options.x')?.setValue(columnIndex)
      // We have to reorder the items on the previous and current column
      this.reorderItemsPerColumn(columnIndex);
      this.reorderItemsPerColumn(previouColumnIndex);
    }

    /**
     * Finally sort the array based first on the x and then on the y
     */
    // this.items.controls.sort((a: AbstractControl, b: AbstractControl) : number => {
    //   if (a.get('options.x')?.value > b.get('options.x')?.value) {
    //     return 1;
    //   } else {
    //     if ( a.get('options.y')?.value > b.get('options.y')?.value) {
    //       return 1;
    //     } else {
    //       return -1;
    //     }
    //   }
    // })
    this.updateItems();
  }

  /**
   * Returns which is the index of the column
   * The first one is 1 not 0
   * @param container
   */
  getIndexFromContainer(container: CdkDropList) {
    const columnIndex = parseInt(container.id.replace(this.listIdPrefix, '')) + 1; console.log('columnIndex', columnIndex)
    return columnIndex;
  }

  /**
   * Specify the y attribute in options
   * @param columnIndex
   */
  reorderItemsPerColumn(columnIndex: number) {
    let y = 1;
    this.items.controls
      .filter(item => item.get('options.x')?.value === columnIndex)
      .forEach( item => { item.get('options.y')?.setValue(y++); })
  }

  addColumn() {
    if (this.blockTypeData.meta.columns < this.maxNumberOfColumnsAllowed) {
      this.blockTypeData.meta.columns++;
      this.updateItems()
    }
    else
      this.alertService.warning('Too much columns')
  }

  changeColumn(item: AbstractControl, prevColumn: number, nextColumn: number) {
    item.get('options.x')?.setValue(nextColumn)
    this.reorderItemsPerColumn(prevColumn);
    this.reorderItemsPerColumn(nextColumn);
    this.updateItems(); console.log(this.items.value);
  }
}



