import {Component, ElementRef, forwardRef, Input, OnInit, ViewChild, Output, EventEmitter} from '@angular/core';
import {CodeeFormControlInterface} from "../codee-form-control.interface";
import {FormProps} from "../../../../types/route.type";
import {environment} from "../../../../../environments/environment";
import {SessionService} from "../../../../services/auth/session.service";
import {Media} from "../../../../types/entities.types";
import {MediaLibraryService} from "../../../../modules/media-library/media-library.service";
import {CdkDragDrop, moveItemInArray} from "@angular/cdk/drag-drop";
import {NG_VALUE_ACCESSOR} from "@angular/forms";
import {AlertService} from "../../../../modules/alert/alert.service";

@Component({
  selector: 'app-codee-file-upload',
  templateUrl: './codee-file-upload.component.html',
  styleUrls: ['./codee-file-upload.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CodeeFileUploadComponent),
      multi: true
    }
  ]
})
export class CodeeFileUploadComponent implements OnInit, CodeeFormControlInterface {

  @Input() label: string;
  @Input() description: string | undefined;
  @Input() value: number[];
  @Input() mode: string;
  @Input() props: FormProps;
  @Input() hasMediaLibrary: boolean = true;

  @Output() change = new EventEmitter<any>();
  @Output() onComplete = new EventEmitter<boolean>();

  @ViewChild('fileUploadInput') fileUploaderInput: ElementRef;


  public acceptedFileTypes: string;
  public isLoading: boolean;
  public uploadedMedia: Media[] = [];

  // fileName = '';
  uploadProgress: number | null;
  protected apiUrl: string;

  constructor(
      private sessionService: SessionService,
      private mediaLibraryService: MediaLibraryService,
      private alertService: AlertService,
  ) {}

  ngOnInit(): void { // console.log('[CodeeFileUploadComponent]ngOnInit', this.props);
    if (this.props.fileTypes)
      this.acceptedFileTypes = this.props.fileTypes;
  }

  /**
   * Called once the user submit new files in the upload input
   * @param event
   */
  onFileSelected(event: any) {

    if (event.target.files.length === 0) {
      // No files selected
      return;
    }

    const files: File[] = [...event.target.files];

    this.uploadFiles(files);
  }

  /**
   * Get all the submitted files and recalls itself
   * if the files array is not empty yet
   * @param files
   */
  uploadFiles(files: File[]) { // console.log('uploadFiles', files)

    const currentFile = files.pop();  // console.log('pop', currentFile)

    this.isLoading = true;

    if (currentFile) {

      this.sendToApi(currentFile).then( (media: Media) => { // console.log('media', media);
        this.uploadedMedia.unshift(media);
        this.onChange(this.uploadedMedia);
        this.isLoading = false;

        if (files.length > 0) {
          this.uploadFiles(files); // console.log('recall uploadfiles');
        } else {
          // Uploading of all files completed
          this.uploadingFilesQueueCompleted();
        }
      })
        .catch( () => {
          console.log('catch');
          this.isLoading = false;
          this.alertService.danger('An error occured uploading the file');
        });
    }
  }

  uploadingFilesQueueCompleted(){
    this.isLoading = false;
    console.log('input cleared');
    this.fileUploaderInput.nativeElement.value = "";
    this.onComplete.emit(true);
  }

  /**
   * Create and execute the request.
   * @param file
   */
  async sendToApi(file: File): Promise<Media> {

    const formData = new FormData();
    formData.append("media", file);

    const session = this.sessionService.getSession();

    return new Promise( (resolve, reject) => {
      fetch( this.getUrl() + this.props.apiEndpoint, {
          method: 'POST',
          headers: {
            'Authorization': 'Bearer ' + session.token.access_token,
          },
          body: formData // body data type must match "Content-Type" header
        })
          .then(response => response.json())
          .then(
              (media: Media) => {
                resolve(media);
              }
          )
          .catch(
              error => {
                console.log('error', error);
                reject(error);
                // resolve({} as Media); // todo consider to improve error handling
              } // Handle the error response object
          );
    });
  }

  openMediaSearch() {
    this.mediaLibraryService.enable({
      endpoint: this.props.apiEndpoint || '',
      component: this,
    });
  }

  drop(event: CdkDragDrop<any[]>) {
    moveItemInArray(this.uploadedMedia, event.previousIndex, event.currentIndex);
    this.uploadedMedia.forEach((media, index) => {
      media.pivot.order = index;
    })
    // console.log(this.uploadedMedia);
  }

  reset() {
    this.uploadProgress = null;
    // this.uploadSub = null;
  }

  onChange = (media: Media[]) => {
    console.log('test', media)
    this.change.emit(media)
  };

  onTouched = () => {};

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  setDisabledState(isDisabled: boolean): void {
  }

  writeValue(media: Media[]): void {
    // console.log('[CodeeFileUploadComponent] writeValue', media, typeof media)
    this.uploadedMedia = media || [];
  }

  isUploaderActive(): boolean {
    if ( this.props.multiple === true) {
      return true;
    } else {
      return this.uploadedMedia.length === 0;
    }
  }

  removeMedia(index: number) {
    // console.log('removeMedia', index);
    this.uploadedMedia.splice(index, 1);
  }


  extendOptions(index: number) {
    this.uploadedMedia[index].extended = ! this.uploadedMedia[index].extended;
    // console.log('extendOptions', index, this.uploadedMedia);
  }

  private getUrl(isCentralDomain = false): string {

    let domain = environment.centralDomain;

    if ( ! isCentralDomain ) {
      const {domains} = this.sessionService.tenantSelectedSbj.getValue();

      if (domains) {
        const [masterDomain] = domains;

        if (masterDomain)
          domain = masterDomain.domain;
      }
    }
    this.apiUrl = `${environment.protocol}://${domain}${environment.port !== null ? ':'+ environment.port : ''}/${environment.apiSuffix}`;
    // console.log('CodeeFileUploadComponent[getUrl]', this.apiUrl);
    return this.apiUrl;
  }

}

