import {LayoutItem} from "../types/layout.types";
import {CodeeInputComponent} from "../ui/forms/fields/codee-input/codee-input.component";
import {CodeeRichTextComponent} from "../ui/forms/fields/codee-rich-text/codee-rich-text.component";
import {CodeeFileUploadComponent} from "../ui/forms/fields/codee-file-upload/codee-file-upload.component";
import {CodeeRelationComponent} from "../ui/forms/fields/codee-relation/codee-relation.component";
import {FetchControl} from "../inc/fetch-control.class";
import {FormProps} from "../types/route.type";
import {CodeeListItemsComponent} from "../ui/forms/fields/codee-list-items/codee-list-items.component";
import {CodeeCheckboxComponent} from "../ui/forms/fields/codee-checkbox/codee-checkbox.component";
import {CodeeSelectComponent} from "../ui/forms/fields/codee-select/codee-select.component";
import {LayoutBlockOrientationEnum} from "../enums/layout-blocks.enum";

const defaultSelectionData = [{
  id: LayoutBlockOrientationEnum.Right,
  label: 'Right',
}, {
  id: LayoutBlockOrientationEnum.Center,
  label: 'Center',
}, {
  id: LayoutBlockOrientationEnum.Left,
  label: 'Left',
}, ];

export class SelectLayoutBlockControl {

  static getControl(layoutItem: LayoutItem, relation_key: string = '') : FormProps {
    // console.log('layoutItem.key', layoutItem.key)
    switch (layoutItem.type) {
      case 'title':
      case 'heading':
      case 'heading-2':
        return {
          component: CodeeInputComponent,
          control: FetchControl.value,
          // relation: this.$route.layoutBlock?.relation_key,
        }
      case 'small-text':
      case 'text':
      case 'medium-text':
      case 'long-text':
      case 'excerpt':
        return {
          component: CodeeRichTextComponent,
          control: FetchControl.value,
          // relation: this.$route.layoutBlock?.relation_key,
        }
      case 'medium-paragraph':
        return {
          component: CodeeRichTextComponent,
          control: FetchControl.value,
          // relation: this.$route.layoutBlock?.relation_key,
        }
      case 'long-paragraph':
        return {
          component: CodeeRichTextComponent,
          control: FetchControl.value,
          // relation: this.$route.layoutBlock?.relation_key,
        }
      case 'image-single':
        return {
          component: CodeeFileUploadComponent,
          multiple: false,
          control: FetchControl.value,
          apiEndpoint: 'files/media', // @todo Consider to pass this prop only for media fields
        }
      case 'links':
        return {
          component: CodeeListItemsComponent, // CodeeMediaLinkComponent,
          control: FetchControl.value,
        }
      case 'image-multiple':
        return {
          component: CodeeFileUploadComponent,
          multiple: true,
          control: FetchControl.value,
          apiEndpoint: 'files/media', // @todo Consider to pass this prop only for media fields
        }
      case 'relation':
        return {
          component: CodeeRelationComponent,
          control: FetchControl.value,
          relation: relation_key,
        }
      case 'checkbox':
        return {
          control: FetchControl.valueBoolean,
          component: CodeeCheckboxComponent,
        }
      case 'select':
        const data = layoutItem.options?.choices?.split('|').map( (split: string) => {
          const splitSelection = split.split(':');
          return {
            id: parseInt(splitSelection[0]),
            label: splitSelection[1],
          }
        });
        return {
          control: FetchControl.value,
          component: CodeeSelectComponent,
          data: data || defaultSelectionData,
        }
      // case 'ui-builder':
      //   return {
      //     control: FetchControl.value,
      //     component: CodeeUiBuilderComponent,
      //   }
      default:
        return {
          component: CodeeInputComponent,
          control: FetchControl.value,
          // relation: this.$route.layoutBlock?.relation_key,
        }
    }
  }


  static theLayoutKey(layoutItem: LayoutItem, index: number): string {
    return `${layoutItem.key}`;
    // return `${layoutItem.key}.${index}`;
  }

  static theLayoutLabel(layoutItem: LayoutItem, index: number): string {
    return `${index + 1}.${layoutItem.label}`
  }

}


