import {Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {CodeeHiddenComponent} from "../../../ui/forms/fields/codee-hidden/codee-hidden.component";
import {ApiData} from "../../../types/api.types";
import {CodeeMultiSelectComponent} from "../../../ui/forms/fields/codee-multi-select/codee-multi-select.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";

const users: Route = {
  endpoint: 'user/users',
  model: "App\\Models\\User",
  label: $localize`Users`,
  texts: {
    key: 'user',
    label: $localize`User`,
    title: $localize`Users`,
    backLabel: $localize`Back to Users`,
    description: $localize`Below is the list of the created users`,
  },
  links: {
    create: '/app/user/users/create',
    update: '/app/user/users/update/',
    manage: '/app/user/users/manage',
  },
  fieldGroups: [{
    title: $localize`User Details`,
    key: 'user-details',
    fields: [{
      key: 'id',
      label: $localize`ID`,
      hidden: true,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'input',
        control: FetchControl.value,
        component: CodeeHiddenComponent,
        editable: false,
      },
      renderProps: {
        component: RenderTextComponent,
      },
    }, {
      key: 'name',
      label: $localize`Name`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'text',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: true,
      },
    }, {
      key: 'email',
      label: $localize`Email`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'text',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: false,
      },
    }, {
      key: 'password',
      label: $localize`Password`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'password',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: true,
      },
    }, {
      key: 'password_confirmation',
      label: $localize`Confirm Password`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'password',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: true,
      },
    }]
  }, {
    title: $localize`Tenants`,
    key: 'tenants',
    fields: [{
      key: 'tenants',
      label: `Tenants`,
      hidden: false,
      col: 12,
      default: '',
      validators: [],
      formProps: {
        type: 'select',
        control: FetchControl.multipleRelation,
        controlKey: 'tenants',
        component: CodeeMultiSelectComponent,
        dataEndpoint: 'tenancy/tenants',
        editable: true,
        mapSelectObjectFn: (item: ApiData) => {
          return {
            id: item.id,
            label: item.id,
          };
        }
      },
    }]
  }, {
    title: $localize`Roles`,
    key: 'roles',
    fields: [{
      key: 'roles',
      label: $localize`Roles`,
      hidden: false,
      col: 12,
      default: '',
      validators: [],
      formProps: {
        type: 'select',
        control: FetchControl.multipleRelation,
        controlKey: 'tenants',
        component: CodeeMultiSelectComponent,
        dataEndpoint: 'user/roles',
        editable: true,
        mapSelectObjectFn: (item: ApiData) => {
          return {
            id: item.id,
            label: item.name,
          };
        }
      },
    }]
  }],
  manage: {
    hasStatus: false,
    columns: [{
      title: $localize`Data`,
      className: 'col-md-4',
      fields: [{
        key: 'id',
        label: $localize`ID`,
        component: RenderTextComponent,
      },{
        key: 'name',
        label: $localize`Name`,
        component: RenderTextComponent,
      },{
        key: 'email',
        label: $localize`Email`,
        component: RenderTextComponent,
      }]
    }, {
      title: $localize`Roles`,
      className: 'col-md-4',
      fields: [{
        label: $localize`Roles`,
        component: RenderRelationshipComponent,
        relation: 'roles',
        relationType: 'multiple',
        key: 'name',
      }, {
        label: $localize`Tenants`,
        component: RenderRelationshipComponent,
        relation: 'tenants',
        relationType: 'multiple',
        key: 'id',
      }]
    }, ]
  }
}

export default users;
