import {FilterFunction, Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";
import {CodeeSelectComponent} from "../../../ui/forms/fields/codee-select/codee-select.component";
import {ApiData} from "../../../types/api.types";

const route: Route = {
  endpoint: "hotel/rooms",
  translatable: true,
  model: "CodeeHut\\HotelManagement\\Models\\Room",
  texts: {
    key: 'rooms',
    label: $localize`Room`,
    title: $localize`Rooms`,
    backLabel: $localize`Back to Rooms`,
    description: $localize`Below is the list of the created rooms`,
  },
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({key: 'media_main', label: $localize`Main image`, apiEndpoint: 'files/media'}),
      FieldGeneratorClass._mediaImage({key: 'media_floorplan', label: $localize`Floor plan`, apiEndpoint: 'files/media'}),
    ]
  }, {
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldGeneratorClass._text({
        key: 'id', label: $localize`ID`, hidden: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        }
      }),
      FieldGeneratorClass._text({
        key: 'name', label: $localize`Name`, translatable: true, filter: {
          control: FetchControl.value,
          component: CodeeInputComponent,
          filterable: FilterFunction.TranslationLike
        }
      }),
      FieldGeneratorClass._text({key: 'code', label: $localize`Code`}),
      FieldGeneratorClass._richText({key: 'excerpt', label: $localize`Room excerpt`, translatable: true,}),
      FieldGeneratorClass._richText({key: 'content', label: $localize`Room Amenities`, translatable: true,}),
    ]
  }, {
    title: $localize`Room Specifications`,
    key: 'room-specifications',
    fields: [
      FieldGeneratorClass._text({key: 'beds', label: $localize`Room Beds (ex. King size bed)`, translatable: true,}),
      FieldGeneratorClass._text({key: 'extent', label: $localize`Room Size (in square meters)`}),
      FieldGeneratorClass._text({key: 'min_person', col: 3, label: $localize`Minimum Persons`, type: 'number'}),
      FieldGeneratorClass._text({key: 'max_person', col: 3, label: $localize`Maximum Persons`, type: 'number'}),
      FieldGeneratorClass._text({key: 'max_adults', col: 3, label: $localize`Maximum Adults`, type: 'number'}),
      FieldGeneratorClass._text({key: 'max_infants', col: 3, label: $localize`Maximum Infants`, type: 'number'}),
      FieldGeneratorClass._checkbox({key: 'children', col: 3, label: $localize`Can accommodate children`}),
    ]
  }, {
    title: 'Relations',
    key: "relations",
    fields: [
      FieldGeneratorClass._choiceFromApi({
        key: 'properties', col: 12, label: $localize`Properies`, apiEndpoint: 'hotel/properties', filter: {
          control: FetchControl.singleRelation,
          component: CodeeSelectComponent,
          dataEndpoint: 'hotel/properties?resultsPerPage=all&page=1',
          mapSelectObjectFn: (item: ApiData) => {
            return {
              id: item.id,
              label: item.name,
            };
          },
          filterable: FilterFunction.HasRelation,
          relation: 'properties',
        }
      }),
    ],
  }, {
    title: $localize`Media`,
    key: 'media',
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_gallery',
        label: $localize`Room Gallery`,
        apiEndpoint: 'files/media',
        multiple: true
      }),
    ]
  }],
  label: "",
  links: {
    create: '/app/hotel/rooms/create',
    update: '/app/hotel/rooms/update/',
    manage: '/app/hotel/rooms/manage',
  },
  manage: {
    hasStatus: true,
    urlPattern: '/room/:slug/:id',
    columns: [{
      title: $localize`Logo`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Excerpt`,
        key: 'excerpt',
        component: RenderTextComponent,
      }, {
        label: $localize`Property`,
        component: RenderRelationshipComponent,
        relation: 'properties',
        relationType: 'multiple',
        key: 'name',
      }],
    }]
  },

  layoutBlock: {
    id: 'room_id',
    relation_key: 'rooms',
  },

};


export default route;
