import {Component, Input, OnInit} from '@angular/core';
import {HttpParams} from "@angular/common/http";
import {CdkDragDrop, moveItemInArray} from "@angular/cdk/drag-drop";
import {ApiService} from "../../../../services/abstract/api.service";
import {SortManagementService} from "../../sort-management.service";
import {RequestService} from "../../../../services/abstract/request.service";

@Component({
  selector: 'module-sort-management',
  templateUrl: './sort-management.component.html',
  styleUrls: ['./sort-management.component.scss']
})
export class SortManagementComponent implements OnInit {

  /** ------------------
   // Sorting temporary section

   todo Create save method
   todo Get items sorted by the current sorting method we manage

   */

  @Input() endpoint: string;

  active: boolean = false;
  sortableItems: Array<any> = [];

  constructor(
      private apiService: ApiService,
      private request: RequestService,
      private sortManagementService: SortManagementService,
  ) { }

  ngOnInit(): void {
    this.sortManagementService.getProps().subscribe( props => {
      console.log(props);
      this.active = props.active;

      // Load the necessary items based on the reference_id (e.x. property_id)
      if (props.reference_id)
        this.loadSortableItems(props.reference_id);
    })
  }

  loadSortableItems(reference_id: number) {
    let params = new HttpParams();
    params = params.append('properties', JSON.stringify({
      value: reference_id,
      filterableFn: 'whereHas',
      relation: 'properties'
    }));
    this.apiService.getEntities(this.endpoint, params, res => {
      console.log(res);
      this.sortableItems = res.data;
    });
  }

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.sortableItems, event.previousIndex, event.currentIndex);
    const entities = this.sortableItems.map( (item, index) => {
      return {
        id: item.id,
        order: index,
      }
    })

    // Send updated order and ids
    this.request.putRequest(this.endpoint + '/re-order', {
      entities: entities,
    }).subscribe(
        res => console.log(res)
    );

  }

  close() {
    this.sortManagementService.disable();
    this.sortableItems = [];
  }

}
