import {FetchControl} from "../../inc/fetch-control.class";
import {CodeeChoicesComponent} from "../../ui/forms/fields/codee-choices/codee-choices.component";
import {Field, FilterFunction} from "../../types/route.type";
import {CodeeFileUploadComponent} from "../../ui/forms/fields/codee-file-upload/codee-file-upload.component";
import {CodeeInputComponent} from "../../ui/forms/fields/codee-input/codee-input.component";
import {CodeeRichTextComponent} from "../../ui/forms/fields/codee-rich-text/codee-rich-text.component";
import {CodeeHiddenComponent} from "../../ui/forms/fields/codee-hidden/codee-hidden.component";
import {CodeeSelectComponent} from "../../ui/forms/fields/codee-select/codee-select.component";
import {ApiData} from "../../types/api.types";

export class FieldsCommonClass {

  static namespace = 'hotel';

  static _propertyRelation(col: number = 12, renderHidden = false): Field {
    return {
      key: 'properties',
      label: $localize`Properties`,
      hidden: false,
      col: col,
      default: '',
      validators: [],
      formProps: {
        type: 'choices',
        control: FetchControl.mapIds,
        component: CodeeChoicesComponent,
        dataEndpoint: 'hotel/properties',
        editable: true,
      },
      filterProps: {
        control: FetchControl.singleRelation,
        component: CodeeSelectComponent,
        dataEndpoint: 'hotel/properties?resultsPerPage=all&page=1',
        mapSelectObjectFn: (item: ApiData) => {
          return {
            id: item.id,
            label: item.name,
          };
        },
        filterable: FilterFunction.HasRelation,
        relation: 'properties',
      },
    }
  }



  static _mediaMenu(col: number = 6, renderHidden = false, namespace: string = 'hotel'): Field {
    return {
      key: 'media_menu',
      label: $localize`Menu / Pricelist`,
      hidden: false,
      col: col,
      default: null,
      validators: [], // [Validators.required],
      formProps: {
        type: 'input',
        control: FetchControl.value,
        component: CodeeFileUploadComponent,
        editable: true,
        apiEndpoint: namespace + "/media",
        multiple: true,
        fileTypes: 'application/pdf',
      },
    }
  }

  static _mediaTreatments(col: number = 6, renderHidden = false, namespace: string = 'hotel'): Field {
    return {
      key: 'media_treatments',
      label: $localize`Treatments`,
      hidden: false,
      col: col,
      default: null,
      validators: [], // [Validators.required],
      formProps: {
        type: 'input',
        control: FetchControl.value,
        component: CodeeFileUploadComponent,
        editable: true,
        apiEndpoint: namespace + "/media",
        multiple: true,
        fileTypes: 'application/pdf',
      },
    }
  }

  static _mediaVideo(col: number = 12, renderHidden = false, namespace: string = 'hotel'): Field {
    return {
      key: 'media_video',
      label: $localize`Video`,
      hidden: false,
      col: col,
      default: null,
      validators: [], // [Validators.required],
      formProps: {
        type: 'input',
        control: FetchControl.value,
        component: CodeeFileUploadComponent,
        editable: true,
        apiEndpoint: namespace + "/media",
        multiple: false,
        fileTypes: 'video/mp4',
      },
    }
  }

  static _name(col: number = 12, renderHidden = false): Field {
    return {
      key: 'name',
      label: $localize`Name`,
      hidden: false,
      col: col,
      default: '',
      validators: [],
      translatable: true,
      formProps: {
        type: 'input',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: true,
      },
      filterProps: {
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: true,
        filterable: FilterFunction.TranslationLike
      },
    }
  }

  static _excerpt(col: number = 12, renderHidden = false): Field {
    return {
      key: 'excerpt',
      label: $localize`Excerpt`,
      hidden: false,
      col: col,
      default: '',
      validators: [],
      translatable: true,
      formProps: {
        type: 'input',
        control: FetchControl.value,
        component: CodeeRichTextComponent,
        editable: true,
      },
    }
  }

  static _content(col: number = 12, renderHidden = false): Field {
    return {
      key: 'content',
      label: $localize`Content`,
      hidden: false,
      col: col,
      default: '',
      validators: [],
      translatable: true,
      formProps: {
        type: 'input',
        control: FetchControl.value,
        component: CodeeRichTextComponent,
        editable: true,
      },
    }
  }

  static _id(col: number = 12, renderHidden = false): Field {
    return {
      key: 'id',
      label: $localize`ID`,
      hidden: true,
      col: col,
      default: '',
      validators: [],
      formProps: {
        type: 'hidden',
        control: FetchControl.value,
        component: CodeeHiddenComponent,
        editable: false,
      },
      filterProps: {
        control: FetchControl.value,
        component: CodeeInputComponent,
        // editable: true,
        filterable: FilterFunction.Equals
      },
    }
  }
}
