import {Injectable} from '@angular/core';

import {DialogLayoutDisplay, ToastNotificationInitializer} from '@costlydeveloper/ngx-awesome-popup';


@Injectable({
  providedIn: 'root'
})
export class AlertService {

  constructor() { }

  danger(message: string = '', title = 'ERROR') {
    this.toastNotification(DialogLayoutDisplay.DANGER, title, message);
  }

  success(message: string = '', title = 'SUCCESS') {
    this.toastNotification(DialogLayoutDisplay.SUCCESS, title, message);
  }

  warning(message: string = '', title = 'WARNING') {
    this.toastNotification(DialogLayoutDisplay.WARNING, title, message);
  }

  info(message: string = '', title = 'INFO') {
    this.toastNotification(DialogLayoutDisplay.INFO, title, message);
  }

  none(title: string, message: string = '') {
    this.toastNotification(DialogLayoutDisplay.NONE, title, message);
  }


  toastNotification(type: DialogLayoutDisplay, title: string, message: string,) {
    const newToastNotification = new ToastNotificationInitializer();
    newToastNotification.setTitle(title);
    newToastNotification.setMessage(message);

    // Choose layout color type
    newToastNotification.setConfig({
      LayoutType: type
    });

    // Simply open the toast
    newToastNotification.openToastNotification$();
  }

}
