import {Component, Input, OnInit} from '@angular/core';
import {FormBuilder, FormControl, FormGroup} from "@angular/forms";
import {Route} from "../../../../types/route.type";
import {ApiService} from "../../../../services/abstract/api.service";
import {AlertService} from "../../../../modules/alert/alert.service";
import {ActivatedRoute, Router} from "@angular/router";
import {ApiError, ApiResponse} from "../../../../types/api.types";

@Component({
    selector: 'entity-form-media',
    templateUrl: './form-media.component.html',
    styleUrls: ['./form-media.component.scss']
})
export class FormMediaComponent implements OnInit {

    // @Input() $route: Route;
    @Input() mode: string;
    @Input() entityToUpdate: any;
    @Input() groupName: 'settingGroups' | 'mediaGroups' = 'mediaGroups';

    public entityForm: FormGroup = new FormBuilder().group({});

    public $route: Route;
    public $errors: Error;

    constructor(
        private fb: FormBuilder,
        private apiService: ApiService,
        private alertService: AlertService,
        private router: Router,
        private route: ActivatedRoute,
    ) {
    }

    ngOnInit(): void {
        this.route.data.subscribe(data => {
            this.$route = data.handler.route;
            // console.log('entityToUpdate', this.entityToUpdate)

            // this.getEntityIfNeeded();
            this.addFormControls();
        });
    }

    addFormControls() {
        /**
         * Add Form controls
         */
        // console.log('settings', this.$route[this.groupName])
        const $routeElement = this.$route[this.groupName];
        if ($routeElement) {
            for (let fieldGroup of $routeElement) {
                for (let field of fieldGroup.fields) {
                    // console.log('field', field)
                    // Check if there is submitted value for the formcontrol
                    const media = this.entityToUpdate[field.key];
                    // console.log('media', media, field.key)
                    // console.log('setting', setting, field);
                    this.entityForm.addControl(field.key, new FormControl(media, field.validators));
                    // this.entityForm.controls[field.key].setValue(setting.value);
                }
            }
        }

        /**
         * Fill fields
         */
        // if (this.entityToUpdate) {
        //   if (this.$route[this.groupName]) {
        //     this.$route[this.groupName].forEach(fieldGroup => {
        //       fieldGroup.fields.forEach((field: Field) => {
        //         // console.log('getEntityIfNeeded', res.data[field.key]);
        //         const [setting] = this.entityToUpdate.settings.filter( (s: ApiData) => s.key === field.key);
        //         console.log('fieldValue', field.key, setting.value);
        //         this.entityForm.controls[field.key].setValue('2022-03-03');
        //       })
        //     })
        //   }
        // }
    }

    onSubmit() {
        this.entityForm.markAsTouched();
        if (this.entityForm.valid) {
            this.submitForm();
        } else {
            this.alertService.warning('Please fix validation errors');
        }
    }

    updateAndStay() {
        this.entityForm.markAsTouched();
        // console.log(this.entityForm.value);
        if (this.entityForm.valid) {
            this.apiService.updateEntity(this.$route.endpoint, this.entityToUpdate.id, {  // todo Check if id exists
                    ...this.entityForm.value,
                    id: this.entityToUpdate.id,
                    type: 'settings',
                },
                (res: ApiResponse) => this.alertService.success(res.message),
                (err: ApiError) => this.afterErrorApiAction(err));
        } else {
            this.alertService.warning('Please fix validation errors');
        }
    }

    submitForm() {
        this.apiService.updateEntity(this.$route.endpoint, this.entityForm.controls['id'].value, {
            ...this.entityForm.value,
            handler: 'media',
          },
            (res: ApiResponse) => this.afterSuccessfulApiAction(res),
            (err: ApiError) => this.afterErrorApiAction(err));

        // console.log('submitForm', this.entityForm.value);
        // if (this.mode === 'update') {
        //   this.apiService.updateEntity(this.$route.endpoint, this.entityForm.controls['id'].value, this.entityForm.value,
        //       (res: ApiResponse) => this.afterSuccessfulApiAction(res),
        //       (err: HttpErrorResponse) => this.afterErrorApiAction(err));
        // } else {
        //   this.apiService.addEntity(this.$route.endpoint, this.entityForm.value,
        //       (res: ApiResponse) => this.afterSuccessfulApiAction(res),
        //       (err: HttpErrorResponse) => this.afterErrorApiAction(err));
        // }
    }

    afterSuccessfulApiAction(res: ApiResponse) {
        this.alertService.success(res.message);
        this.router.navigate([this.$route.links.manage]);
    }

    afterErrorApiAction(err: ApiError) {
        // @ts-ignore
        this.$errors = err.errors;
        this.alertService.danger(err.message);
    }


    getValidationErrorByKey(key: string): string | null {
        if (this.$errors)
            if (this.$errors[key])
                return this.$errors[key].join();
        return null;
    }

}

type Error = {
    [key: string]: string[];
}
