import {Component, OnInit} from '@angular/core';
import {MediaLibraryService} from "../media-library.service";
import {Media} from "../../../types/entities.types";
import {ApiService} from "../../../services/abstract/api.service";
import {HttpParams} from "@angular/common/http";
import {Metadata} from "../../../types/layout.types";
import {Field, FilterFunction} from "../../../types/route.type";
import {FormBuilder, FormGroup} from "@angular/forms";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";

@Component({
  selector: 'module-media-library-box',
  templateUrl: './media-library-box.component.html',
  styleUrls: ['./media-library-box.component.scss']
})
export class MediaLibraryBoxComponent implements OnInit {

  public active: boolean = false;

  mediaFiltersForm: FormGroup;

  public mediaFiles: Media[] = [];
  public selectedFiles: Media[] = [];

  public page: number = 1;
  public resultsPerPage: number = 24;
  public metadata: Metadata;

  public fields: Field[];

  constructor(
    private fb: FormBuilder,
    private mediaLibraryService: MediaLibraryService,
    private apiService: ApiService,
  ) {}

  ngOnInit(): void {

    this.fields = [{
      key: 'name',
      label: 'Name',
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'text',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: false,
      },
    }];
    this.mediaFiltersForm = this.fb.group({
      name: ['', []],
    })
    this.getMedia();
  }


  getMedia(page: number = 1) {

    if (page)
      this.page = page;

    this.mediaLibraryService.isActive().subscribe(
      active => {
        this.active = active;
        // console.log(active);
        if (active) {
          const endpoint = this.mediaLibraryService.props.getValue().endpoint; // console.log(endpoint);
          if (endpoint) {
            let params = new HttpParams();
            if (this.mediaFiltersForm.get('name')) {
              params = params.append('name', JSON.stringify({
                value: this.mediaFiltersForm.get('name')?.value,
                filterableFn: FilterFunction.Like,
              }));
            }
            params = params.append('resultsPerPage', this.resultsPerPage.toString());
            params = params.append('page', this.page.toString());
            // params = params.append('order', this.page.toString());
            params = params.append('mime', JSON.stringify({
              value: this.mediaLibraryService.props.getValue().component.props.fileTypes,
              filterableFn: FilterFunction.WhereIn,
            }));
            this.apiService.getEntities(endpoint, params,
              res => {
                this.mediaFiles = res.data;
                this.metadata = res.meta;
              }
            )
          }
        }
      },
      error => console.log(error)
    )
  }

  closeMyself() {
    this.mediaLibraryService.disable();
    this.page = 1;
    this.mediaFiltersForm.get('name')?.reset();
    this.mediaFiles = [];
  }

  selectMedia(media: Media) {
    const component = this.mediaLibraryService.props.getValue().component;

    if (component.props.multiple) {
      component.uploadedMedia.push(media);
    } else {
      component.uploadedMedia = [media];
    }
    component.onChange(component.uploadedMedia);
    this.mediaLibraryService.disable();

    // console.log(this.mediaLibraryService.props.getValue().component);
    // this.mediaLibraryService.props.getValue().component.push(media);
    // console.log(this.mediaLibraryService.props.getValue());
  }

  pageChanged(page: number = 1) {
    // Scroll into view
    // this.mainCard.nativeElement.scrollIntoView({ top: -100, behavior: "smooth", block: "start" });
    this.getMedia(page);
  }

  onFilterSubmit() {
      this.getMedia(1);
  }
}
