import {Route} from "../../../types/route.type";
import {FieldsCommonClass} from "../../common/fields-common.class";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {RenderRelationshipComponent} from "../../../ui/render/fields/render-relationship/render-relationship.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";

const route: Route = {
  endpoint: "hotel/wellnesses",
  translatable: true,
  model: "CodeeHut\\HotelManagement\\Models\\Wellness",
  texts: {
    key: 'wellnesses',
    label: $localize`Wellness`,
    title: $localize`Wellnesss`,
    backLabel: $localize`Back to Wellnesss`,
    description: $localize`Below is the list of the created wellnesses`,
  },
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({
        key: 'media_main',
        label: 'Main image',
        apiEndpoint: 'files/media'
      }),
    ]
  }, {
    title: $localize`Details`,
    key: 'details',
    fields: [
      FieldsCommonClass._id(6),
      FieldsCommonClass._name(6),
      FieldsCommonClass._excerpt(12),
      FieldsCommonClass._content(12),
    ]
  }, {
    title: 'Relations',
    key: "relations",
    fields: [
      FieldsCommonClass._propertyRelation(),
    ],
  }, {
    title: "Attachments",
    key: 'attachments',
    fields: [
      FieldsCommonClass._mediaMenu(6, true),
      FieldsCommonClass._mediaTreatments(6, true),
    ]
  }, {
    title: "Media",
    key: 'media',
    fields: [
      FieldGeneratorClass._mediaImage({key: 'media_gallery', label: 'Gallery images', apiEndpoint: 'files/media', col: 12, multiple: true}),
    ]
  }],
  label: "",
  links: {
    create: '/app/hotel/wellness/create',
    update: '/app/hotel/wellness/update/',
    manage: '/app/hotel/wellness/manage',
  },
  layoutBlock: {
    id: 'wellness_id',
    relation_key: 'wellnesses',
  },
  manage: {
    hasStatus: true,
    urlPattern: '/wellness/:slug/:id',
    columns: [{
      title: $localize`Logo`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }, {
        label: $localize`Excerpt`,
        key: 'excerpt',
        component: RenderTextComponent,
      }, {
        label: $localize`Property`,
        component: RenderRelationshipComponent,
        relation: 'properties',
        relationType: 'multiple',
        key: 'name',
      }],
    }]
  },

};


export default route;
