import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges
} from '@angular/core';
import {FormBuilder, FormControl, FormGroup} from "@angular/forms";
import {ConditionCompare, Field, Route} from "../../../../types/route.type";
import {ApiService} from "../../../../services/abstract/api.service";
import {AlertService} from "../../../../modules/alert/alert.service";
import {ActivatedRoute, Router} from "@angular/router";
import {ApiError, ApiResponse} from "../../../../types/api.types";
import {LoadableService} from "../../../../services/abstract/loadable.service";
import {Language} from "../../../../types/layout.types";
import {FORM_MODE} from "../../../../enums/entities.enum";
import {LanguageService} from "../../../../services/abstract/language.service";
import {Subscription} from "rxjs";
import {environment} from "../../../../../environments/environment";

@Component({
  selector: 'entity-form-data',
  templateUrl: './form-data.component.html',
  styleUrls: ['./form-data.component.scss']
})
export class FormDataComponent implements OnInit, OnChanges, OnDestroy {

  @Input() mode: FORM_MODE;
  @Input() entityToUpdate: any;

  @Output() returnEntity = new EventEmitter<any>();

  public entityForm: FormGroup = new FormBuilder().group({});

  public selectedLanguage: Language;
  protected langSubscription: Subscription;

  public $route: Route;
  public $errors: Error;

  constructor(
    private fb: FormBuilder,
    private apiService: ApiService,
    private loadableService: LoadableService,
    private alertService: AlertService,
    private router: Router,
    private route: ActivatedRoute,
    private languageService: LanguageService,
  ) {
  }

  ngOnInit(): void {

    this.route.data.subscribe(data => {
      this.$route = data.handler.route;
      this.langSubscription = this.languageService.selectedLanguage$.subscribe( lang => {
        this.selectedLanguage = lang;
        this.onSelectLanguage()
      })
      this.addFormControls();
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    // If changes contain the entityToUpdate then we have to patchValue
    if (changes.entityToUpdate) {
      this.entityForm.patchValue(changes.entityToUpdate.currentValue)
    }
  }

  ngOnDestroy() {
    this.langSubscription.unsubscribe();
  }

  addFormControls() {
    for (let fieldGroup of this.$route.fieldGroups) {
      for (let field of fieldGroup.fields) {
        this.entityForm.addControl(field.key, new FormControl(field.default, field.validators));
      }
    }
    if (this.entityToUpdate) {
      this.$route.fieldGroups.forEach(fieldGroup => {
        fieldGroup.fields.forEach((field: Field) => {
          // console.log('getEntityIfNeeded', res.data[field.key]);
          // this.entityForm.controls[field.key].setValue(field.formProps.control(this.entityToUpdate[field.key], field.key))
          this.setValueToFormByKey(field, false);
        })
      })
    }
  }

  onSubmit() {
    this.entityForm.markAsTouched();
    if (this.entityForm.valid) {
      this.submitForm();
    } else {
      this.alertService.warning('Please fix validation errors');
    }
  }

  updateAndStay() {
    this.entityForm.markAsTouched();
    // console.log(this.entityForm.value);
    this.loadableService.enable();
    if (this.entityForm.valid) {
      this.apiService.updateEntity(this.$route.endpoint, this.entityForm.controls['id'].value, {
          ...this.entityForm.value,
          lang: this.selectedLanguage.code,
        },
        (res: ApiResponse) => this.afterSuccessfulApiAction(res, false),
        (err: ApiError) => this.afterErrorApiAction(err));
    } else {
      this.loadableService.disable();
      this.alertService.warning('Please fix validation errors');
    }
  }

  submitForm() {
    // console.log('submitForm', this.entityForm.value);
    this.loadableService.enable();
    if (this.mode === 'update') {
      this.apiService.updateEntity(this.$route.endpoint, this.entityForm.controls['id'].value, {
          ...this.entityForm.value,
          lang: this.selectedLanguage.code,
          handler: 'data',
        },
        (res: ApiResponse) => this.afterSuccessfulApiAction(res),
        (err: ApiError) => this.afterErrorApiAction(err));
    } else {
      this.apiService.addEntity(this.$route.endpoint, {
          ...this.entityForm.value,
          lang: this.selectedLanguage.code,
        },
        (res: ApiResponse) => this.afterSuccessfulApiAction(res),
        (err: ApiError) => this.afterErrorApiAction(err));
    }
  }

  afterSuccessfulApiAction(res: ApiResponse, redirect: boolean = true) {
    this.loadableService.disable();
    this.alertService.success(res.message);
    this.checkUpdatableFieldsAfterApiResponse(res);
    // this.returnEntity.emit (res.data);
    if (redirect)
      this.router.navigate([this.$route.links.manage]);
  }

  /**
   * Check if we have to update any field value
   * @param res
   */
  checkUpdatableFieldsAfterApiResponse(res: ApiResponse) {
    this.$route.fieldGroups
      .map(fieldGroup => fieldGroup.fields)
      .reduce((appender, state) => appender.concat(state), [])
      .filter((field) => field.formProps.isUpdatable)
      .map(field => field.key)
      .forEach((fieldKey: string) => {
        // @ts-ignore
        const fieldValue = res.data[fieldKey];
        if (fieldValue) {
          this.entityForm.get(fieldKey)?.setValue(fieldValue)
        }
      });
    // console.log('res', res.data, updatableFields);
  }

  afterErrorApiAction(err: ApiError) {
    this.loadableService.disable();
    // @ts-ignore
    this.$errors = err.errors;
    this.alertService.danger(err.message);
  }


  getValidationErrorByKey(key: string): string | null {
    if (this.$errors)
      if (this.$errors[key])
        return this.$errors[key].join();
    return null;
  }

  isFieldVisible(field: Field) {
    if (field.conditions) {
      field.conditions.forEach(f => {
        switch (f.compare) {
          case ConditionCompare.equals:
            return this.entityForm.get(f.field_key)?.value === f.value;
          default:
            return true;
        }
      })
    }
    return true;
  }

  /**
   * What happens when user change language
   */
  onSelectLanguage() { // console.log('onSelectLanguage', this.$route)
    if (this.mode === FORM_MODE.update) {
      const $routeElement = this.$route.fieldGroups;
      if ($routeElement) {
        for (let fieldGroup of $routeElement) {
          for (let field of fieldGroup.fields) {
            this.setValueToFormByKey(field, true);
          }
        }
      }
    }
  }

  /**
   *
   * @param field
   * @param changeOnlyTranslations
   * @param pickFromDefault
   */
  setValueToFormByKey(field: Field, changeOnlyTranslations: boolean = false, pickFromDefault: boolean = false) {

    let translatedValue;
    if (this.entityToUpdate.translations) {
      translatedValue = this.entityToUpdate?.translations
        .find((item: any) => item.locale === (pickFromDefault ? environment.defaultLocale : this.selectedLanguage.code))?.[field.key];
    } else {
      translatedValue = this.entityToUpdate;
    }

    if (field.translatable) {
      if (translatedValue) {
        this.entityForm.get(field.key)?.setValue(translatedValue)
      } else {
        this.entityForm.get(field.key)?.reset();
      }
    } else if (!changeOnlyTranslations) {
      this.entityForm.get(field.key)?.setValue(field.formProps.control(this.entityToUpdate[field.key], field.key))
    }
  }

  copyValuesFromDefault() {
    //
    const $routeElement = this.$route.fieldGroups;
    if ($routeElement) {
      for (let fieldGroup of $routeElement) {
        for (let field of fieldGroup.fields) {
          this.setValueToFormByKey(field, true, true);
          // console.log('field', field)
          // Check if there is submitted value for the formcontrol
          // const [setting] = settings.filter((s: any) => s.key === field.key);
          // // If translatable is true then look first for submitted translation
          // if (field.translatable) {
          //   const value = setting.translations.find((item: any) => item.locale === 'en')
          //   const fieldS = this.entityForm.get(field.key);
          //   if (fieldS !== null) {
          //     fieldS.reset();
          //     fieldS.setValue(value.localized_value);
          //   }
          // }
        }
      }
    }

  }

}

type Error = {
  [key: string]: string[];
}

