import {Component, Input, OnInit} from '@angular/core';
import {ApiData} from "../../../types/api.types";
import {environment} from "../../../../environments/environment";
import {Media} from "../../../types/entities.types";

@Component({
  selector: 'app-media-display',
  templateUrl: './media-display.component.html',
  styleUrls: ['./media-display.component.scss']
})
export class MediaDisplayComponent implements OnInit {

  @Input() media: Media;
  @Input() size: string = 'thumbnail';
  @Input() width: number = 76;
  @Input() height: number = 76;

  constructor() { }

  ngOnInit(): void {
    // console.log('[MediaDisplayComponent]init', this.media);
  }

  getImageSrc() {

    if (this.media && this.media.data) {

      switch (this.media.mime) {
        case 'image/png':
        case 'image/jpeg':
        case 'image/jpg':
        case 'image/webp':
          let thumbnail;
          [thumbnail] = this.media.data.thumbnails.filter(thumb => thumb.key === this.size);

          if (thumbnail)
            return environment.mediaUrl + this.media.data.upload_dir + '/' + thumbnail.filename;
          else
            return null;
          // pdf icon
        case 'image/svg':
        case 'image/svg+xml':
          return '/assets/images/file-types/vector.png';
        case 'video/mp4':
          return '/assets/images/file-types/video.png';
        case 'application/pdf':
          return '/assets/images/file-types/pdf.png';
          // microsoft document icon
        case 'application/msword':
        case 'application/vnd.openxmlformats':
        case 'officedocument.wordprocessingml.document':
          return '/assets/images/file-types/doc.png';
        // Fallback icon
        default:
          return '';
      }
    } else {
      return null;
    }
  }
}
