import {Route} from "../../../types/route.type";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";

const roles: Route = {
  endpoint: 'user/roles',
  withRelations: ['permissions'],
  model: "jeremykenedy\\LaravelRoles\\Models\\Role",
  label: $localize`Roles`,
  texts: {
    key: 'role',
    label: $localize`Role`,
    title: $localize`Roles`,
    backLabel: $localize`Back to Roles`,
    description: $localize`Below is the list of the created roles`,
  },
  links: {
    create: '/app/user/roles/create',
    update: '/app/user/roles/update/',
    manage: '/app/user/roles/manage',
  },
  fieldGroups: [{
    title: $localize`Role Details`,
    key: 'role-details',
    fields: [
      FieldGeneratorClass._text({ key: 'id', label: $localize`ID`, hidden: true}),
      FieldGeneratorClass._text({ key: 'name', label: $localize`Name`, }),
      FieldGeneratorClass._richText({ key: 'description', label: $localize`Description`, }),
    ],
  }, {
    title: $localize`Permissions`,
    key: 'permissions',
    fields: [
      FieldGeneratorClass._choiceFromApi({ key: 'permissions', col: 12, label: $localize`Permissions`, apiEndpoint: 'user/permissions?resultsPerPage=all&page=1'}),
    ]
  }],
  manage: {
    hasStatus: false,
    columns: [{
      title: $localize`Data`,
      className: 'col-md-4',
      fields: [{
        key: 'id',
        label: $localize`ID`,
        component: RenderTextComponent,
      },{
        key: 'name',
        label: $localize`Name`,
        component: RenderTextComponent,
      },{
        key: 'slug',
        label: $localize`Slug`,
        component: RenderTextComponent,
      },{
        key: 'description',
        label: $localize`Description`,
        component: RenderTextComponent,
      },]
    }, ]
  }
}

export default roles;
