import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {ApiError, ApiResponse} from "../../../types/api.types";
import {Field, Route} from "../../../types/route.type";
import {Metadata} from "../../../types/layout.types";
import {HttpParams} from "@angular/common/http";
import {ApiService} from "../../../services/abstract/api.service";
import {RequestService} from "../../../services/abstract/request.service";
import {AlertService} from "../../../modules/alert/alert.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SessionService} from "../../../services/auth/session.service";
import {LoadableService} from "../../../services/abstract/loadable.service";
import {ModalComponent} from "../../../ui/widgets/modal/modal.component";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {ConfirmBoxInitializer, DialogLayoutDisplay} from "@costlydeveloper/ngx-awesome-popup";
import {environment} from "../../../../environments/environment";

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {

  public $route: Route;

  @ViewChild("mainCard") mainCard: ElementRef;
  @ViewChild(ModalComponent) modal: ModalComponent;

  public filtersForm: FormGroup = new FormBuilder().group({});
  public qrCodeForm: FormGroup = new FormBuilder().group({});

  public items: QrCode[] = [];
  public $filterableFields: Field[];
  public page: number = 1;
  public metadata: Metadata;
  public resultsPerPage: Number = 25; // environment.resultsPerPage;
  public params: HttpParams = new HttpParams();

  public isQrCodeFormActive: boolean = false

  public apiUrl: string; // = 'http://client1.codeehut.local:8000/'; // environment.apiUrl;

  constructor(
    private fb: FormBuilder,
    private apiService: ApiService,
    private request: RequestService,
    private alertService: AlertService,
    private router: Router,
    private route: ActivatedRoute,
    private sessionService: SessionService,
    private loadableService: LoadableService,
  ) { }

  ngOnInit(): void {

    this.sessionService.selectedTenant().subscribe(
      tenant => {
        const [domain] = tenant.domains;
        this.apiUrl = environment.protocol + '://' + domain.domain;

        if (environment.port) {
          this.apiUrl = ':' + environment.port;
        }
      }
    )

    this.qrCodeForm = this.fb.group({
      id: [null],
      name: ['', Validators.required],
      url: ['', Validators.required],
    })

    this.getEntities();
  }



  pageChanged(page: number = 1) {
    // Scroll into view
    if (this.mainCard) {
      this.mainCard.nativeElement.scrollIntoView({top: -100, behavior: "smooth", block: "start"});
    }
    this.getEntities(page);
  }

  getEntities(page: number = 1) {

    // console.log('getEntities: page', page);
    this.loadableService.enable();

    if (page)
      this.page = page;

    this.params = new HttpParams();
    this.params = this.params.append('resultsPerPage', this.resultsPerPage.toString());
    this.params = this.params.append('page', this.page.toString());
    this.params = this.params.append('lang', environment.defaultLocale);

    // if (this.$route.withRelations) {
    //   this.params = this.params.append('with', this.$route.withRelations.join(','))
    // }
    //
    // // Check if extra attributes have been defined in the query
    // if (this.$route.withAttributes) {
    //   this.params = this.params.append('attributes', this.$route.withAttributes.join(','))
    // }

    // Add Filter params
    this.setFilterParams();

    // this.apiService.getEntities(this.$route.endpoint, this.params, res => {
    this.apiService.getEntities('settings/qr-codes', this.params, res => {
        this.items = res.data;
        this.metadata = res.meta;
        this.loadableService.disable();
      },
      (err: ApiError) => {
        this.alertService.danger(err.message);
        this.loadableService.disable();
      });
  }

  setFilterParams(): void {
    // console.log('this.filtersForm.value', this.filtersForm.value);
    if (Object.keys(this.filtersForm.controls).length > 0) {
      for (const key of Object.keys(this.filtersForm.controls)) {
        let value = this.filtersForm.controls[key].value;
        if (value) {
          // if (isMoment(value)) {
          //   value = value.format('YYYY-MM-DD');
          // }
          const [props] = this.$filterableFields.filter(filter => filter.key === key)
          // console.log('filterProps', props);
          let data = {
            value: value,
            filterableFn: props?.filterProps?.filterable,
            relation: '',
          };

          if (props.filterProps && props.filterProps.relation) {
            data.relation = props.filterProps.relation;
          }
          this.params = this.params.append(key, JSON.stringify(data));
        }
      }
    }
  }

  editQrCode(item: QrCode) {
    this.isQrCodeFormActive = true;
    this.qrCodeForm.patchValue({
      id: item.id,
      name: item.name,
      url: item.url,
    })
  }

  deleteQrCode(item: QrCode) {
    const confirmBox = new ConfirmBoxInitializer();
    confirmBox.setTitle('Confirm Delete');
    confirmBox.setMessage('Are you sure you want to delete ' + item.name);
    confirmBox.setButtonLabels('YES', 'NO');

    // Choose layout color type
    confirmBox.setConfig({
      LayoutType: DialogLayoutDisplay.DANGER // SUCCESS | INFO | NONE | DANGER | WARNING
    });

    // Simply open the popup and listen which button is clicked
    confirmBox.openConfirmBox$().subscribe(resp => {
      // IConfirmBoxPublicResponse
      // console.log('Clicked button response: ',  resp.Success);
      if (resp.Success) {
        this.apiService.deleteEntity('settings/qr-codes', item.id, res => {
          this.getEntities();
        })
      }
    });
  }

  updateStatus(id: number | string, status: number | string) {
    // console.log(status);
    this.apiService.updateEntity('settings/qr-codes', typeof id === "string" ? parseInt(id) : id, {
        id: id,
        status: status === 0 ? 1 : 0,
      },
      (res: ApiResponse) => this.afterSuccessfulApiAction(res),
      (err: ApiError) => this.afterErrorApiAction(err));
  }

  onCreateNewQrCode() {
    if (this.qrCodeForm.get('id')?.value) {
      this.apiService.updateEntity('settings/qr-codes', this.qrCodeForm.get('id')?.value , this.qrCodeForm.value,
        res => {
          this.getEntities();
          this.isQrCodeFormActive = false;
          this.qrCodeForm.reset();
        });
    } else {
      this.apiService.addEntity('settings/qr-codes', this.qrCodeForm.value,
        res => {
          this.getEntities();
          this.isQrCodeFormActive = false;
          this.qrCodeForm.reset();
        });
    }
  }

  afterSuccessfulApiAction(res: ApiResponse) {
    this.alertService.success(res.message);
    this.getEntities();
  }

  afterErrorApiAction(err: ApiError) {
    // @ts-ignore
    this.$errors = err.errors;
    this.alertService.danger(err.message);
  }

  openQrCodeForm() {
    this.isQrCodeFormActive = true;
  }

  doModalClosed($event: any) {
    this.qrCodeForm.reset();
    this.isQrCodeFormActive = false;
  }

  testUrl(item: QrCode) {
    window.open(item.url, '_blank');
  }
}


type QrCode = {
  id: number;
  name: string;
  url: string;
  hash: string;
  valid_until: string;
  status: number;
  used_count: number;
  created_at: number;
  updated_at: number;
}
