import {Component, Input, OnInit} from '@angular/core';
import {CodeeRenderControlInterface} from "../codee-render-control.interface";
import {FormProps, RenderProps} from "../../../../types/route.type";

@Component({
  selector: 'app-render-text',
  templateUrl: './render-text.component.html',
  styleUrls: ['./render-text.component.scss']
})
export class RenderTextComponent implements OnInit, CodeeRenderControlInterface {

  @Input() props: RenderProps;
  @Input() label: string;
  @Input() item: any;
  @Input() key: string;

  public _value!: string;

  constructor() {
  }

  ngOnInit(): void {
    this.renderValue(this.props.maxLength ?? 56);
  }

  renderValue(limit: number) {

    let keys = this.key.split('.');

    this.jumpToNestedKey(this.item, keys);

    if (this._value) {

      this.stripHtmlTags();

      this.addEllipsisToValue(limit);
    }
  }

  jumpToNestedKey(item: any, keys: string[]) {

    const currentKey = keys.shift();

    if (currentKey) {

      if (keys.length === 0) {
        this._value = item[currentKey];
      } else {
        this.jumpToNestedKey(item[currentKey], keys);
      }
    } else {
      this._value = '-';
    }

  }

  stripHtmlTags() {
    // Strip tags only for strings
    if (typeof this._value === 'string') {
      this._value = this._value.replace(/(<([^>]+)>)/gi, "");
    }
  }


  addEllipsisToValue(limit: number) {
    if (this._value.length > limit) {
      this._value = this._value.substring(0, limit) + '...';
    }
  }
}
