import {Component, OnDestroy, OnInit} from '@angular/core';
import {SessionService} from "../../../services/auth/session.service";
import {Router} from "@angular/router";
import {Role, Tenant, User} from "../../../types/entities.types";
import {Subscription} from "rxjs";
import {environment} from "../../../../environments/environment";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnDestroy {

  public $user: User = <User>{};

  private tenantSbscr: Subscription;
  private tenantSelectedSbscr: Subscription;

  public settingsPanelActive: boolean = false;
  public selectedTenant !: string;

  // @ts-ignore
  private activeTimeout;
  public availableTenants: Tenant[];

  public appName = environment.appName;
  isSuperAdmin: Role;

  constructor(
    private sessionService: SessionService,
    private router: Router,
    // private tenantService: TenantsService,
  ) { }

  ngOnInit(): void {
    const session = this.sessionService.getSession();
    if (session.user) {
      this.$user = session.user;
    }

    this.isSuperAdmin = this.sessionService.isSuperAdmin();

    // Subscribe to tenants service
    this.tenantSbscr = this.sessionService.tenants().subscribe(
        (tenants: Tenant[]) => this.availableTenants = tenants,
        (error: Error) => console.log(error)
    );

    // Subscribe to selected tenant subscription
    this.tenantSelectedSbscr = this.sessionService.selectedTenant().subscribe(
        (tenant: Tenant) => {
          // console.log('HeaderComponent', tenant);
          if (tenant) {
            this.selectedTenant = tenant.id
          }
        },
        (error: Error) => console.log(error)
    )
  }

  doLogout() {
    this.sessionService.removeSession();
    this.router.navigate(['/connect']);
  }

  ngOnDestroy(): void {
    this.tenantSbscr.unsubscribe();
  }

  activateSettingsPanel() {
    clearTimeout(this.activeTimeout);
    this.settingsPanelActive = true;
  }
  deactivateSettingsPanel() {
    // @todo Search if can be better
    this.activeTimeout = setTimeout( () => this.settingsPanelActive = false, 500);
  }

  onChangeTenantSelect() {
    // console.log(this.selectedTenant);
    this.sessionService.doSelectTenant(this.selectedTenant)
    // @todo Set different tenant for the application
  }
}
