import {FilterFunction, Route} from "../../../types/route.type";
import {FetchControl} from "../../../inc/fetch-control.class";
import {CodeeInputComponent} from "../../../ui/forms/fields/codee-input/codee-input.component";
import {RenderTextComponent} from "../../../ui/render/fields/render-text/render-text.component";
import {CodeeHiddenComponent} from "../../../ui/forms/fields/codee-hidden/codee-hidden.component";
import {RenderImageComponent} from "../../../ui/render/fields/render-image/render-image.component";
import {FieldGeneratorClass} from "../../common/field-generator.class";

const route: Route = {
  endpoint: "knowledge-base/categories",
  translatable: true,
  model: "CodeeHut\\KnowledgeBase\\Models\\Category",
  texts: {
    key: 'categories',
    label: $localize`Category`,
    title: $localize`Categories`,
    backLabel: $localize`Back to Categories`,
    description: $localize`Below is the list of the created categories`,
  },
  fieldGroups: [{
    title: $localize`Images`,
    key: 'images',
    fields: [
      FieldGeneratorClass._mediaImage({key: 'media_main', label: $localize`Main image`, apiEndpoint: 'files/media'}),
    ]
  }, {
    title: $localize`Details`,
    key: 'details',
    fields: [{
      key: 'id',
      label: 'ID',
      hidden: true,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'hidden',
        control: FetchControl.value,
        component: CodeeHiddenComponent,
        editable: false,
      },
      renderProps: {
        // control: RenderControl.string, // Used in: manage app-codee-render-outlet
        component: RenderTextComponent,
      },
    }, {
      key: 'name',
      label: $localize`Category Name`,
      hidden: false,
      col: 6,
      default: '',
      validators: [],
      formProps: {
        type: 'text',
        control: FetchControl.value,
        component: CodeeInputComponent,
        editable: true,
      },
      filterProps: {
        type: 'text',
        control: FetchControl.value,
        component: CodeeInputComponent,
        // editable: true,
        filterable: FilterFunction.TranslationLike
      },
      renderProps: {
        // control: RenderControl.string, // Used in: manage app-codee-render-outlet
        component: RenderTextComponent,
      },
    },
      FieldGeneratorClass._selectFromApi({
        key: 'parent_id',
        col: 6,
        label: $localize`Parent Category`,
        apiEndpoint: 'knowledge-base/categories?flat-tree=1&resultsPerPage=all&page=1',
        formPropsControlFn: FetchControl.value,
      }),
    ]
  }, {
    title: $localize`Content`,
    key: 'content',
    fields: [
      FieldGeneratorClass._richText({key: 'excerpt', translatable: true, label: $localize`Excerpt`}),
      FieldGeneratorClass._richText({key: 'content', translatable: true, label: $localize`Extended description`}),
    ]
  }],
  label: "",
  links: {
    create: '/app/knowledge-base/categories/create',
    update: '/app/knowledge-base/categories/update/',
    manage: '/app/knowledge-base/categories/manage',
  },
  manage: {
    hasStatus: true,
    columns: [{
      title: $localize`Logo`,
      className: 'col-auto',
      fields: [{
        label: $localize`Main image`,
        key: 'media_main',
        component: RenderImageComponent,
      }],
    }, {
      title: $localize`Data`,
      className: 'col',
      fields: [{
        label: $localize`ID`,
        key: 'id',
        component: RenderTextComponent,
      }, {
        label: $localize`Name`,
        key: 'name',
        component: RenderTextComponent,
      }],
    }]
  },

};


export default route;
