import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormComponent} from "./form/form.component";
import {ManageComponent} from "./manage/manage.component";
import {ViewComponent} from "./view/view.component";

import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {RouterModule} from "@angular/router";
import {MatSelectModule} from "@angular/material/select";
import {MatSlideToggleModule} from "@angular/material/slide-toggle";

// Import from library
import {
    NgxAwesomePopupModule,
    ConfirmBoxConfigModule
} from '@costlydeveloper/ngx-awesome-popup';

import {FormLayoutBlocksComponent} from './form/form-layout-blocks/form-layout-blocks.component';
import {FormDataComponent} from './form/form-data/form-data.component';
import {FormSettingsComponent} from './form/form-settings/form-settings.component';
import {DragDropModule} from "@angular/cdk/drag-drop";
import {FormMediaComponent} from "./form/form-media/form-media.component";
import { TranslateComponent } from './translate/translate.component';
import { TranslateDataComponent } from './translate/translate-data/translate-data.component';
import { TranslateLayoutBlocksComponent } from './translate/translate-layout-blocks/translate-layout-blocks.component';
import { TranslateItemComponent } from './translate/views/translate-item/translate-item.component';
import { FormMenuComponent } from './form/form-menu/form-menu.component';

import { UploadMediaComponent } from './upload-media/upload-media.component';
import {UiModule} from "../../ui/ui.module";
import {InputControlDirective} from "../../directives/input-control.directive";
import {SortManagementModule} from "../../modules/sort-management/sort-management.module";
import { FormEmailComponent } from './form/form-email/form-email.component';
import { PreviewLinkComponent } from './manage/preview-link/preview-link.component';

@NgModule({
    declarations: [
        FormComponent,
        ManageComponent,
        ViewComponent,

        InputControlDirective,
        FormLayoutBlocksComponent,
        FormDataComponent,
        FormSettingsComponent,
        FormMediaComponent,
        TranslateComponent,
        TranslateDataComponent,
        TranslateLayoutBlocksComponent,
        TranslateItemComponent,
        FormMenuComponent,
        UploadMediaComponent,
        FormEmailComponent,
        PreviewLinkComponent,
    ],
    imports: [
        CommonModule,
        UiModule,
        FormsModule,
        ReactiveFormsModule,
        RouterModule,
        MatSelectModule,
        MatSlideToggleModule,
        DragDropModule,
        SortManagementModule,
        NgxAwesomePopupModule.forRoot(), // Essential, mandatory main module.
        ConfirmBoxConfigModule.forRoot(),
        // Essential, mandatory confirm box module.
    ],
    exports: [
        FormComponent,
        ManageComponent,
        ViewComponent,
    ]
})
export class EntityModule {
}
