import {Component, Input, OnInit} from '@angular/core';

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss']
})
export class CardComponent implements OnInit {

  @Input() title: string = '';
  @Input() isLoading: boolean = false;
  @Input() capacious: "large" | "medium" | "small" = "large"

  constructor() { }

  get capaciousClass() {
    switch (this.capacious) {
      case "large":
        return "py-3 py-md-4 px-3 px-md-4 mb-3 mb-md-4";
      case "medium":
        return "py-2 py-md-3 px-2 px-md-3 mb-2 mb-md-3";
      case "small":
        return "py-1 py-md-2 px-1 px-md-2 mb-1 mb-md-2";
      default:
        return "py-3 py-md-4 px-3 px-md-4 mb-3 mb-md-4"
    }
  }

  ngOnInit(): void {
  }

}
